import React from "react";
import { Editor } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import styled from "styled-components";
import {
  AppEventType,
  ChangeUiSettingsSidebar,
} from "../controllers/AppEventType";
import { sendEvent } from "../controllers/storeController";
import { CloseButton, PanelContentStyles } from "./PanelContentStyles";
import { useTranslation } from "react-i18next";

export const LeftSidebar: React.FC<{}> = () => {
  const leftSidebar = useSelector((state: Editor) => {
    return state.ui.leftSidebar;
  });
  return (
    <LeftSidebarStyles>
      <div className={"left-sidebar-wrapper"}>
        {leftSidebar.isOpen === true ? (
          <>{leftSidebar.panel === "comments" ? <CommentsPanel /> : null}</>
        ) : null}
      </div>
    </LeftSidebarStyles>
  );
};

export const LeftSidebarStyles = styled.div`
  .left-sidebar-wrapper {
    position: absolute;
    top: 0;
    left: calc(var(--leftSidebarWidth) * -1);
    z-index: var(--zIndexLeftSidebar);
    width: var(--leftSidebarWidth);
    display: flex;
    transition: all 0.4s ease-in-out;
    background: #fff;
    height: 100%;
    & > div {
      width: 100%;
      height: 100%;
    }
  }
`;

const CommentsPanel: React.FC<{}> = () => {
  const onClickCloseComments = () => {
    const event: ChangeUiSettingsSidebar = {
      name: AppEventType.ChangeUiSettingsSidebar,
      payload: {
        open: false,
        panel: "comments",
      },
    };
    sendEvent(event);
  };
  const [t] = useTranslation(["board", "translation"]);
  return (
    <PanelContentStyles>
      <div className={"content-wrapper"}>
        <div className={"title"}>{t("board:tab.comments_title")}</div>
        <div>{t("translation:list_is_empty")}</div>
        <CloseButton onClick={onClickCloseComments} />
      </div>
    </PanelContentStyles>
  );
};
