import React from "react";
import { Editor } from "../editorObjects/Editor";
import { EditorModes } from "./Toolbar/EditorModes";
import { useSelector } from "react-redux";
import styled from "styled-components";

export const ActionsWrapper: React.FC<{}> = () => {
  const editorMode = useSelector((state: Editor) => {
    return state.ui.mode;
  });

  return (
    <ActionsWrapperStyles>
      <div className="actions-wrapper">
        <div className={"actions-content"}>
          <EditorModes editorMode={editorMode} />
        </div>
      </div>
    </ActionsWrapperStyles>
  );
};

export const ActionsWrapperStyles = styled.div`
  .actions-wrapper {
    position: absolute;
    z-index: var(--zIndexActions);
    left: var(--standardPadding);
    top: 50%;
    transform: translate(0, -50%);
    width: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    transition: all 0.4s ease-in-out;
  }
  .actions-content {
    padding: 0;
    background-color: rgba(255, 255, 255, 1);
    padding: 12px 0;
    transition: height 0.2s linear, top 0.2s linear;
    border-radius: 4px;
    box-shadow: 0 8px 16px 0 rgb(0 0 0 / 12%);
    width: 100%;
  }
`;
