import React from "react";
import { Bead, Editor,  ObjectTypes } from "../../editorObjects/Editor";
import { useSelector } from "react-redux";
import { Actions, ActionsTypes, MapActionObjectType } from "./ActionsTypes";
import { ActionsIconsWrapper } from "./ActionsIconsWrapper";
import { PopupWrapper } from "./PopupWrapper";
// import { getStore } from "../../controllers/store";

export const TopBarContent: React.FC<{ x: number; y: number }> = (props) => {
  const selectedIds = useSelector((state: Editor) => {
    return state.selectedIds;
  });
  const actionsGrouped = useSelector((state: Editor) => {
    const selectedActions: ActionsTypes[] = [];
    state.selectedIds.forEach((id) => {
      if (state.objects[id]) {
        const objectType = state.objects[id].objectType;
        const actionsForType = MapActionObjectType[objectType];
        if (selectedActions.length === 0) {
          actionsForType.forEach((action) => {
            selectedActions.push(action);
          });
        } else {
          for (let i = selectedActions.length - 1; i >= 0; i--) {
            const action = selectedActions[i];
            if (actionsForType.includes(action) === false) {
              selectedActions.splice(selectedActions.indexOf(action), 1);
            }
          }
        }
      }
    });
    const result: ActionsTypes[][] = [];
    let groupedResults: { [key: string]: ActionsTypes[] } = {};
    selectedActions.forEach((action) => {
      if (Actions[action]) {
        if (action === ActionsTypes.Alignment) {
          if (state.selectedIds.length === 1) {
            return;
          }
        }
        if (action === ActionsTypes.Lock || action === ActionsTypes.Unlock) {
          if (state.selectedIds.length === 1) {
            const id = state.selectedIds[0];
            const obj = state.objects[id];
            if (obj.isLocked === true) {
              groupedResults = {};
              groupedResults[Actions[ActionsTypes.Unlock].groupId] = [
                ActionsTypes.Unlock,
              ];
              return false;
            }
            if (obj.isLocked === false && action === ActionsTypes.Unlock) {
              return;
            }
          } else {
            let containsLockedGroup: any = false;
            state.selectedIds.forEach((objectId) => {
              if (state.objects[objectId] === undefined) {
                return;
              }

              const isGroup =
                state.objects[objectId].objectType === ObjectTypes.Group;
              const groupIdKey: keyof Bead = "groupId";
              const isInGroup =
                state.objects[objectId][groupIdKey] !== undefined &&
                state.objects[objectId][groupIdKey]!.length > 0;
              const groupId = state.objects[objectId][groupIdKey]!;
              if (isGroup) {
                const obj = state.objects[objectId];
                if (obj.isLocked === true) {
                  groupedResults = {};
                  groupedResults[Actions[ActionsTypes.Unlock].groupId] = [
                    ActionsTypes.Unlock,
                  ];
                  containsLockedGroup = true;
                  return false;
                }
                if (obj.isLocked === false && action === ActionsTypes.Unlock) {
                  containsLockedGroup = true;
                  return;
                }
              } else if (isInGroup) {
                const obj = state.objects[groupId];
                if (obj.isLocked === true) {
                  groupedResults = {};
                  groupedResults[Actions[ActionsTypes.Unlock].groupId] = [
                    ActionsTypes.Unlock,
                  ];
                  containsLockedGroup = true;
                  return false;
                }
                if (obj.isLocked === false && action === ActionsTypes.Unlock) {
                  containsLockedGroup = true;
                  return;
                }
              }
            });
            if (containsLockedGroup === true) {
              return;
            } else {
              if (action === ActionsTypes.Unlock) {
                return;
              }
            }
          }
        }
        if (action === ActionsTypes.Group || action === ActionsTypes.Ungroup) {
          if (state.selectedIds.length === 1) {
            return;
          } else {
            //@TODO: zmienic typ z any na boolean
            let containsRegion: any = false;
            let anySelectedBeadInRegion: any = false;

            // let containsGroup: any = false;
            // let allCount: any = state.selectedIds.length;
            let groupCount: any = 0;
            let inGroupCount: any = 0;
            let noGroupCount: any = 0;

            state.selectedIds.forEach((objectId) => {
              if (containsRegion === true) {
                return;
              }
              if (anySelectedBeadInRegion === true) {
                return;
              }
              if (state.objects[objectId] === undefined) {
                return;
              }
              if (state.objects[objectId].objectType === ObjectTypes.Region) {
                containsRegion = true;
                return;
              }

              if (state.objects[objectId].objectType === ObjectTypes.Bead) {
                if ((state.objects[objectId] as Bead).regionId !== undefined) {
                  anySelectedBeadInRegion = true;
                  return;
                }
              }

              const isGroup =
                state.objects[objectId].objectType === ObjectTypes.Group;

              const groupIdKey: keyof Bead = "groupId";
              const isInGroup =
                state.objects[objectId][groupIdKey] !== undefined &&
                state.objects[objectId][groupIdKey]!.length > 0;
              if (isGroup) {
                groupCount = groupCount + 1;
              } else if (isInGroup) {
                inGroupCount = inGroupCount + 1;
              } else {
                noGroupCount = noGroupCount + 1;
              }
            });

            if (containsRegion === true) {
              return;
            }

            if (anySelectedBeadInRegion === true) {
              return;
            }

            // console.log(
            //   "------------- jak jest?",
            //   allCount,
            //   groupCount,
            //   inGroupCount,
            //   noGroupCount
            // );
            if (noGroupCount === 0 && groupCount > 0) {
              if (action === ActionsTypes.Group) {
                return;
              }
            } else if (groupCount > 0 || inGroupCount > 0) {
              // do nothing
            } else {
              if (action === ActionsTypes.Ungroup) {
                return;
              }
            }
          }
        }
        if (!groupedResults[Actions[action].groupId]) {
          groupedResults[Actions[action].groupId] = [action];
        } else {
          groupedResults[Actions[action].groupId].push(action);
        }
      }
    });
    Object.keys(groupedResults)
      .sort((groupIdA, groupIdB) => {
        const firstActionA = groupedResults[groupIdA][0];
        const firstActionB = groupedResults[groupIdB][0];
        const groupSortOrderA = Actions[firstActionA].groupSortOrder;
        const groupSortOrderB = Actions[firstActionB].groupSortOrder;
        return groupSortOrderA === groupSortOrderB
          ? 0
          : groupSortOrderA > groupSortOrderB
          ? 1
          : -1;
      })
      .forEach((groupId) => {
        const actions = groupedResults[groupId].sort((actionA, actionB) => {
          return Actions[actionA].sortOrder === Actions[actionB].sortOrder
            ? 0
            : Actions[actionA].sortOrder > Actions[actionB].sortOrder
            ? 1
            : -1;
        });
        result.push(actions);
      });
    return result;
  });
  const [popupInfo, setPopupInfo] = React.useState<{
    isOpen: boolean;
    type: ActionsTypes | undefined;
    offsetLeft: number;
  }>({
    isOpen: false,
    type: undefined,
    offsetLeft: 0,
  });
  const onClickAnyIcon = (type: ActionsTypes, offsetLeft: number) => {
    const newValue = type === popupInfo.type ? !popupInfo.isOpen : true;
    setPopupInfo({
      isOpen: newValue,
      type,
      offsetLeft,
    });
  };

  React.useEffect(() => {
    setPopupInfo({
      isOpen: false,
      type: undefined,
      offsetLeft: 0,
    });
  }, [selectedIds]);

  return (
    <div
      className={`selected-objects-top-bar`}
      style={{
        top: props.y,
        left: props.x,
      }}
    >
      <div className={"content-wrapper"}>
        <div className={"action-icons-wrapper"}>
          {actionsGrouped.map((actions, index) => {
            return (
              <ActionsIconsWrapper
                key={index}
                actions={actions}
                onClick={onClickAnyIcon}
                selectedAction={
                  popupInfo.isOpen === true && popupInfo.type !== undefined
                    ? popupInfo.type
                    : undefined
                }
              />
            );
          })}
        </div>

        {popupInfo.isOpen === true && popupInfo.type !== undefined ? (
          <PopupWrapper
            offsetLeft={popupInfo.offsetLeft}
            type={popupInfo.type}
          />
        ) : null}
      </div>
    </div>
  );
};
