import produce from "immer";
import { cloneDeep } from "lodash";
import { ChangeUiSettings } from "../AppEventType";
import { refreshReactState } from "../refreshReactState";
import { setStore } from "../store";
import { BaseEventHandler } from "../BaseHandlers/BaseEventHandler";

export class ChangeUiSettingsHandler extends BaseEventHandler<ChangeUiSettings> {
  run() {
    const { uiSettings } = this.event.payload;
    let newStore = produce(this.store, (draft) => {
      Object.keys(uiSettings).forEach((keyName) => {
        draft.ui[keyName] = cloneDeep(uiSettings[keyName]);
      });
    });
    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);
  }
}
