import React from "react";
import { Editor } from "../../editorObjects/Editor";
import { useSelector } from "react-redux";
import styled from "styled-components";
import { TopBarContent } from "./TopBarContent";


export const SelectedObjectsTopBar: React.FC<{}> = (props) => {
  const selectedTopBarPosition = useSelector((state: Editor) => {
    return state.ui.selectedTopBarPosition;
  });

  if (selectedTopBarPosition === undefined) {
    return null;
  }

  return (
    <SelectedObjectsTopBarStyles
      style={{
        zIndex:
          selectedTopBarPosition === undefined
            ? "-5"
            : ("var(--zIndexSelectedObjectsTopBar)" as any),
      }}
    >
      {selectedTopBarPosition !== undefined ? (
        <TopBarContent
          x={selectedTopBarPosition.x}
          y={selectedTopBarPosition.y}
        />
      ) : null}
    </SelectedObjectsTopBarStyles>
  );
};

export const SelectedObjectsTopBarStyles = styled.div`
  .selected-objects-top-bar {
    position: fixed;
    top: 100px;
    z-index: var(--zIndexSelectedObjectsTopBar);
  }

  .content-wrapper {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -65px);
    height: 55px;
    padding: 0 12px;
    background: #fff;
    border-radius: 4;
    box-shadow: 0 8px 16px 0 rgb(0 0 0 / 12%);
  }
  .action-icons-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    column-gap: calc(var(--standardPadding) * 2);
    width: 100%;
    height: 100%;
  }

  .action-icons-wrapper > div:nth-last-child(1) .actions-group:before {
    background: transparent;
  }

  .action-icons-wrapper > div:first-child .actions-group:before {
    background: transparent;
  }
`;
