import { AppData } from "./AppData";
import React from "react";
import { createContext } from "use-context-selector";

const initialState: State = {
  isAuthorized: false,
  data: undefined,
};

export const AuthContext = createContext<{ state: State; dispatch: Dispatch }>({
  state: initialState,
  dispatch: () => null,
});

type State =
  | {
      isAuthorized: true;
      data: AppData;
    }
  | {
      isAuthorized: false;
      data: undefined;
    };

type Action = { type: "logout" };
type Dispatch = (action: Action) => void;

const reducer = (state: State, action: Action): State => {
  switch (action.type) {
    case "logout":
      return {
        isAuthorized: false,
        data: undefined,
      };
    default:
      return state;
  }
};

export const useValue = (startState: State) => {
  return React.useReducer(reducer, startState);
};
