export const clearThreeScene = () => {
  appThree.scene.clear();
};

export const addLightFromUI = (
  config: ThreeLightConfig,
  scene: AppThreeScene
) => {
  if (config.type === "THREE.DirectionalLight") {
    const light = new THREE.DirectionalLight(config.color, config.intensity);
    light.position.set(
      config.position?.x ?? 0,
      config.position?.y ?? 0,
      config.position?.z ?? 10
    );
    light.name = config.name;
    light.userData = merge({}, light, {
      objectType: ObjectTypes.SceneLight,
      name: `${ObjectTypes.SceneLight}${NAME_SEPARATOR}${light.id}`,
    });
    scene.add(light);
  }
};

export const addGridFromUi = (config: ThreeGridHelperConfig) => {
  const size = 100000;
  const grid = new THREE.GridHelper(1, size / 10, "#e9e9e9", "#eee");
  grid.position.x = 0;
  grid.position.y = 0;
  grid.position.z = -500;
  grid.scale.x = size;
  grid.scale.y = size;
  grid.scale.z = size;

  grid.rotation.x = THREE.MathUtils.DEG2RAD * 90;
  grid.rotation.y = THREE.MathUtils.DEG2RAD * 0;
  grid.rotation.z = THREE.MathUtils.DEG2RAD * 0;
  grid.userData = merge({}, config, {
    objectType: ObjectTypes.SceneBackgroundGrid,
    name: `${ObjectTypes.SceneBackgroundGrid}${NAME_SEPARATOR}${config.id}`,
  });
  appThree.scene.add(grid);
};

export const addObjectByObjectType = (object: AppObject) => {
  const { objectType } = object;
  if (objectType === ObjectTypes.Bead) {
    triggerAddBeadToScene(object as any, appThree.scene, false);
  }
  if (objectType === ObjectTypes.Region) {
    triggerRegionToScene(object as any);
  }
  if (objectType === ObjectTypes.Group) {
    triggerGroupToScene(object as any);
  }
  if (objectType === ObjectTypes.PreviewProject3D) {
    triggerPreviewProject3DToScene(object as any);
  }
};

