import * as THREE from "three";
import {
  AddObjects,
  AppEventType,
  ChangeUiSettings,
} from "../../../controllers/AppEventType";
import { sendEvent } from "../../../controllers/storeController";
import { EditorMode, ObjectTypes } from "../../../editorObjects/Editor";
import { appThree } from "../../renderThreejs";
import { MouseDragHelper } from "./../MouseDragHelper";
import { MouseSelectionHelper } from "./../MouseSelectionHelper";
import { screenToWorld } from "./../screenToWorld";
import { MouseEventsBase } from "./MouseEventsBase";

export class MouseEventsEditorModeAddRegion extends MouseEventsBase {
  isCameraGrabed: boolean;
  startDragMousePosition: THREE.Vector2;
  startDragCamera: THREE.Vector2;
  boundingClientRect: DOMRect | undefined;
  mouseDragHelper: MouseDragHelper;
  mouse: THREE.Vector2;
  raycaster: THREE.Raycaster;
  mouseSelectionHelper: MouseSelectionHelper;

  constructor() {
    super();
    this.boundingClientRect = undefined;
    this.isCameraGrabed = false;
    this.startDragMousePosition = new THREE.Vector2();
    this.startDragCamera = new THREE.Vector2();
    this.mouseDragHelper = new MouseDragHelper();
    this.mouse = new THREE.Vector2();
    this.raycaster = new THREE.Raycaster();

    this.mouseSelectionHelper = new MouseSelectionHelper();
    this.mouseSelectionHelper.setRenderer(appThree.renderer);
  }

  onWheel(event: PointerEvent) {}

  onResize(event: PointerEvent) {}

  onDown(event: PointerEvent) {
    if (appThree.el !== null) {
      this.boundingClientRect = appThree.el.getBoundingClientRect();
    }
    if (this.boundingClientRect === undefined) {
      return;
    }
    const { top, left } = this.boundingClientRect;
    const screenX = event.clientX - left;
    const screenY = event.clientY - top;
    this.mouseSelectionHelper.onDown(screenX, screenY);
    const { x, y } = screenToWorld(event.clientX, event.clientY);
    this.mouseDragHelper.onDown(x, y);
  }

  onMove(event: PointerEvent) {
    if (this.boundingClientRect === undefined) {
      return;
    }
    const { top, left } = this.boundingClientRect;
    const screenX = event.clientX - left;
    const screenY = event.clientY - top;
    this.mouseSelectionHelper.onMove(screenX, screenY);
    const { x, y } = screenToWorld(event.clientX, event.clientY);
    this.mouseDragHelper.onMove(x, y);
  }

  onUp(event: PointerEvent) {
    // const { getState, dispatch } = appStore;
    // const { dispatch } = appStore;
    // const state = getState();
    const minX = Math.min(
      this.mouseDragHelper.start.x,
      this.mouseDragHelper.end.x
    );
    const minY = Math.min(
      this.mouseDragHelper.start.y,
      this.mouseDragHelper.end.y
    );
    const width = Math.abs(
      this.mouseDragHelper.end.x - this.mouseDragHelper.start.x
    );
    const height = Math.abs(
      this.mouseDragHelper.end.y - this.mouseDragHelper.start.y
    );
    // let defaultRegion: Partial<Region> = {
    //  ...this.draftState.ui.regionTemplate,
    // };
    // defaultRegion.id = undefined;
    // defaultRegion.position = buildPoint(minX + width / 2, minY + height / 2);
    // defaultRegion.height = height;
    // defaultRegion.width = width;

    const action1: AddObjects = {
      name: AppEventType.AddObjects,
      payload: {
        objects: [
          {
            objectType: ObjectTypes.Region,
            height,
            width,
            position: {
              x: minX + width / 2,
              y: minY + height / 2,
              z: 1,
            },
          },
        ],
      },
    };
    sendEvent(action1);
    const action2: ChangeUiSettings = {
      name: AppEventType.ChangeUiSettings,
      payload: {
        uiSettings: {
          mode: EditorMode.SelectOrMove,
        },
      },
    };
    sendEvent(action2);
    // dispatch(
    //   Actions.Region.action_addRegion(
    //     this.mouseDragHelper.start as Point,
    //     this.mouseDragHelper.end as Point
    //   )
    // );
    // dispatch(
    //   Actions.ChangeEditorMode.action_onChangeEditorMode(
    //     EditorMode.SelectOrMove
    //   )
    // );
    this.mouseDragHelper.onUp();
    this.mouseSelectionHelper.onUp();
  }
}
