import produce from "immer";
import { AppEventType, DuplicateObjects } from "../AppEventType";
import { setStore } from "../store";
import { DuplicateObjectsHistory } from "../AppHistory";
import { addObjectByObjectType } from "../../threejs/eventEmitterHandlers";
import {
  AppObject,
  Group,
  groupIdKey,
  ObjectTypes,
} from "../../editorObjects/Editor";
import { refreshReactState } from "../refreshReactState";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import { cloneDeep } from "lodash";
import { notifyBackend, SaveEventType } from "../../ApiService";
import { formatISO } from "date-fns";

export class DuplicateObjectsHandler extends StoreObjectHandler<DuplicateObjects> {
  run() {
    const mapIds = {};
    const objects: AppObject[] = [];
    this.event.payload.objects.forEach((obj) => {
      if (!obj.id) {
        return;
      }
      const oldId = obj.id;
      const clone = cloneDeep(obj);
      delete (clone as any).id;
      let newObject = this.getAppObjectFromCloneAppObject(clone);
      if (newObject === undefined) {
        return;
      }
      mapIds[oldId] = newObject.id;
      objects.push(newObject);
    });
    // const offsetY = Math.random() * 3 * (Math.random() < 0.5 ? -1 : 1);
    // const offsetX = Math.random() * 3 * (Math.random() < 0.5 ? -1 : 1);
    const { diffX, diffY } = this.getPositionDiffFromMousePosition(objects);
    this.moveObjectsByDiff(objects, diffX, diffY);
    // this.moveObjectsByDiff(objects, diffX + offsetX, diffY + offsetY);
    objects.forEach((object) => {
      if (object.objectType === ObjectTypes.Group) {
        const newChildrenIds: string[] = [];
        (object as Group).childrenIds.forEach((id) => {
          newChildrenIds.push(mapIds[id]);
        });
        (object as Group).childrenIds = newChildrenIds;
      }
      if (object[groupIdKey] !== undefined) {
        object[groupIdKey] = mapIds[object[groupIdKey]];
      }
    });
    this.store.selectedIds.forEach((id) => {
      this.selectObjectOnThreeScene(this.store.objects[id], false);
    });
    let newStore = produce(this.store, (draft) => {
      draft.selectedIds = [];
      objects.forEach((object) => {
        draft.objects[object.id] = object;
        draft.selectedIds.push(object.id);
      });
    });
    objects.forEach((object) => {
      addObjectByObjectType(object);
      this.selectObjectOnThreeScene(object, true);
    });
    if (this.saveToHistory) {
      const historyEvent: DuplicateObjectsHistory = {
        do: this.event,
        redo: this.event,
        undo: {
          name: AppEventType.RemoveObjects,
          payload: {
            ids: objects.map((object) => {
              return object.id;
            }),
          },
        },
        createdAt: formatISO(new Date()),
      };
      this.history.push(historyEvent);
    }
    newStore = this.getStoreWithHistory(newStore);
    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);

    notifyBackend(
      objects.map((object) => {
        return {
          action: SaveEventType.CREATE,
          createdAt: new Date().toISOString(),
          object,
        };
      })
    );
  }
}
