import React from "react";
import { Editor } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import { SelectedObjectsTopBar } from "./SelectedObjectsTopBar/SelectedObjectsTopBar";
import ReactTooltip from "react-tooltip";
import { ModalEditBoard } from "./ModalEditBoard";
import { ProjectInfoWrapper } from "./ProjectInfoWrapper";
import { MinimapWrapper } from "./MinimapWrapper";
import { BeadsSummary } from "./BeadsSummary";
import { ActionsWrapper } from "./ActionsWrapper";
import { RightSidebar } from "./RightSidebar";
import { LeftSidebar } from "./LeftSidebar";
import { BottomBar } from "./BottomBar";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSyncAlt } from "@fortawesome/free-solid-svg-icons";
import { ModalSearchProducts } from "./ModalSearchProducts";

export const EditorScene = () => {
  const leftSidebarOpen = useSelector((state: Editor) => {
    return state.ui.leftSidebar.isOpen;
  });
  const rightSidebarOpen = useSelector((state: Editor) => {
    return state.ui.rightSidebar.isOpen;
  });

  return (
    <div>
      <ReactTooltip />
      <ModalEditBoard />
      <ModalSearchProducts />
      <EditorMainStyles>
        <div
          className={`scene 
          ${leftSidebarOpen ? "left-sidebar-open" : ""} 
          ${rightSidebarOpen ? "right-sidebar-open" : ""}`}
        >
          <LoadingInfo />
          <ProjectInfoWrapper />
          <MinimapWrapper />
          <BeadsSummary />
          <ActionsWrapper />
          <BottomBar />
          <LeftSidebar />
          <RightSidebar />

          <SelectedObjectsTopBar />
        </div>
      </EditorMainStyles>
    </div>
  );
};

const EditorMainStyles = styled.div`
  .scene {
    border: 1px solid #e0e0e0;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    box-sizing: border-box;
    height: 100vh;

    .block-loading {
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(255, 255, 255, 0.9);
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      z-index: var(--zIndexBlockLoading);
      svg {
        animation: rotating 2s linear infinite;
      }
    }

    &.right-sidebar-open {
      .right-sidebar-wrapper {
        right: 0;
      }

      .minimap-wrapper {
        right: calc(var(--standardPadding) + var(--leftSidebarWidth));
      }
      .beads-summary {
        right: calc(
          var(--standardPadding) + var(--leftSidebarWidth) +
            var(--standardPadding) + var(--minimapWidth)
        );
      }
    }

    &.left-sidebar-open {
      .left-sidebar-wrapper {
        left: 0;
      }
      .project-info-wrapper {
        left: calc(var(--standardPadding) + var(--rightSidebarWidth));
      }
      .actions-wrapper {
        left: calc(var(--standardPadding) + var(--rightSidebarWidth));
      }
      .bottom-bar-wrapper {
        left: calc(var(--standardPadding) + var(--rightSidebarWidth));
      }
    }
  }

  @keyframes rotating {
    from {
      -ms-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    to {
      -ms-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -webkit-transform: rotate(360deg);
      -o-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
`;

const LoadingInfo: React.FC<{}> = () => {
  const loading = useSelector((state: Editor) => {
    return state.ui.blockThreejs.loading;
  });

  if (loading === false) {
    return <div data-test={"blockLoadingLoaded"}></div>;
  }

  return (
    <div className={"block-loading"} data-test={"blockLoading"}>
      <FontAwesomeIcon icon={faSyncAlt} />
    </div>
  );
};

