import React from "react";
import { EditorScene } from "./components/Editor";
import { Provider } from "react-redux";
import { appStore } from "./immerReducers/appStore";
import { useParams } from "react-router-dom";
import { renderThreejs, unmountThreejs } from "./threejs/renderThreejs";
import { clearBoard, prepareBoard } from "./prepareBoard";
import { AppData } from "./AppData";
import { startBatchQueue, stopBatchQueue } from "./ApiService";

export const onStart = (
  uuid: string,
  data: AppData,
  canvas: HTMLCanvasElement | OffscreenCanvas | undefined,
  context: WebGLRenderingContext | undefined
) => {
  renderThreejs(
    canvas === undefined ? document.getElementById("threejs") : (canvas as any),
    canvas,
    context
  ).then(() => {
    prepareBoard(uuid);

    if (uuid !== "demo" && data.save_events === true) {
      startBatchQueue(uuid);
    }
  });
};

export const onEnd = (
  uuid: string,
  data: AppData,
  canvas: HTMLCanvasElement | OffscreenCanvas | undefined,
  context: WebGLRenderingContext | undefined
) => {
  stopBatchQueue(uuid);
  clearBoard(uuid);
  unmountThreejs(
    canvas === undefined ? document.getElementById("threejs") : (canvas as any)
  );
};

export const PageBoard: React.FC<{
  data: AppData;
  canvas: HTMLCanvasElement | OffscreenCanvas | undefined;
  context: WebGLRenderingContext | undefined;
}> = (props) => {
  let { uuid } = useParams<{ uuid: string }>();

  React.useEffect(() => {
    onStart(uuid, props.data, props.canvas, props.context);
    return () => {
      onEnd(uuid, props.data, props.canvas, props.context);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="App">
      <Provider store={appStore}>
        <EditorScene />
      </Provider>
    </div>
  );
};
