import * as THREE from "three";
import {
  addLightFromUI,
  getThreeBeadModel
} from "./eventEmitterHandlers";
import { AppObject, Bead, ObjectTypes } from "../editorObjects/Editor";
import { AppThreeScene } from "./renderThreejs";
import { getStore } from "../controllers/store";

export let thumbnailsGenerator: ThumbnailsGenerator;

export const initThumbnailsGenerator = () => {
  try {
    const rootEl = document.createElement("div");
    rootEl.setAttribute("id", "thumbnailsGeneratorRoot");
    thumbnailsGenerator = new ThumbnailsGenerator(rootEl);
    rootEl.appendChild(thumbnailsGenerator.renderer.domElement);
    rootEl.style.display = "none";
    document.body.appendChild(rootEl);
    // loadAppModels(thumbnailsGenerator);
  } catch (err) {
    console.error("initThumbnailsGenerator err:", err.toString());
  }
};

class ThumbnailsGenerator {
  el: HTMLElement;
  // camera: THREE.OrthographicCamera;
  scene: AppThreeScene;
  renderer: THREE.WebGLRenderer;
  cache: { [key: string]: string };
  constructor(el: HTMLElement) {
    this.el = el;
    this.cache = {};
    // const { width, height } = el.getBoundingClientRect();

    // this.camera = new THREE.OrthographicCamera(
    //   width / -2,
    //   width / 2,
    //   height / 2,
    //   height / -2,
    //   0,
    //   1000
    // );

    // this.camera.position.set(0, 0, 500);
    // this.camera.lookAt(0, 0, 0);

    this.scene = new THREE.Scene() as any;
    this.scene.background = new THREE.Color("#fff");

    this.renderer = new THREE.WebGLRenderer({
      antialias: true,
      preserveDrawingBuffer: true,
    });
    // this.renderer.setSize(width, height);
  }

  getObjectThumbnail(
    object: AppObject,
    imgWidth: number,
    imgHeight: number,
    forceCache: boolean = false
  ) {
    const cacheData: any = {
      imgWidth,
      imgHeight,
      width: object.width,
      height: object.height,
      depth: object.depth,
      rotateX: object.rotateX,
      rotateY: object.rotateY,
      rotateZ: object.rotateZ,
    };
    if (object.objectType === ObjectTypes.Bead) {
      cacheData.type = (object as Bead).type;
      cacheData.color = (object as Bead).color;
    }

    const cacheId = JSON.stringify(cacheData);
    if (forceCache === true || this.cache[cacheId] === undefined) {
      const content = this.createObjectThumbnail(object, imgWidth, imgHeight);
      if (content === undefined) {
        return undefined;
      } else {
        this.cache[cacheId] = content;
      }
    }
    return this.cache[cacheId];
  }

  createObjectThumbnail(
    object: AppObject,
    imgWidth: number,
    imgHeight: number
  ) {
    try {
      this.renderer.setSize(imgWidth, imgHeight);

      this.scene.clear();

      if (object.objectType !== ObjectTypes.Bead) {
        console.warn("object.objectType not supported yet");
        return;
      }

      const object3d = getThreeBeadModel(object as Bead);

      if (object3d === undefined) {
        console.warn("triggerAddBeadToScene object not created in scene");
        return;
      }


      object3d.position.x = 0;
      object3d.position.y = 0;

      this.scene.add(object3d);

      const bbox = new THREE.Box3();
      bbox.expandByObject(object3d);

      let width = 1;
      let height = 1;
      // let depth = 1;

      if (bbox) {
        width = Math.abs(bbox.max.x - bbox.min.x);
        height = Math.abs(bbox.max.y - bbox.min.y);
        // depth = Math.abs(bbox.max.z - bbox.min.z);
      }

      const objectRatio = width / height;
      const ratioImage = imgWidth / imgHeight;
      let baseWidth = 1;
      let baseHeight = 1;
      if (ratioImage < objectRatio) {
        baseWidth = width;
        baseHeight = baseWidth * ratioImage;
      } else {
        baseHeight = height;
        baseWidth = baseHeight / ratioImage;
      }
      const halfWidth = (baseWidth + baseWidth * 0.1) / 2;
      const halfHeight = (baseHeight + baseHeight * 0.1) / 2;

      const camera = new THREE.OrthographicCamera(
        halfWidth * -1,
        halfWidth,
        halfHeight,
        halfHeight * -1,
        0,
        1000
      );

      camera.position.set(0, 0, 500);
      camera.lookAt(0, 0, 0);

      const store = getStore();
      store.ui.lights.forEach((lightConfig) => {
        addLightFromUI(lightConfig, this.scene);
      });

      this.renderer.render(this.scene, camera);

      const MIME_TYPE = "image/png";
      const content = this.renderer.domElement.toDataURL(MIME_TYPE);
      //   .replace("image/png", "image/octet-stream");
      // const link = document.createElement("a");
      // document.body.appendChild(link);
      // link.download = "coralinio-preview.png";
      // link.href = content;
      // link.click();
      // document.body.removeChild(link);

      this.scene.clear();

      return content;

      // var img = new Image();
      // img.src = content;
      // img.id = "1111111111111111"
      // document.body.appendChild(img);
    } catch (e) {
      console.log("getObjectThumbnail error:", e);
      return;
    }
  }
}
