import produce from "immer";
import { cloneDeep } from "lodash";
import { updateObjectPosition } from "../../threejs/eventEmitterHandlers";
import {
  ChangeObjectsProperty,
  AppEventType,
  ChangeObject,
  ChangeSelectedObjectsAlignment,
  SnapToLineType,
} from "../AppEventType";
import { ChangeSelectedObjectsAlignmentHistory } from "../AppHistory";
import { refreshReactState } from "../refreshReactState";
import { setStore } from "../store";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import { notifyBackend, SaveEventType } from "../../ApiService";
import { getSelectBBox } from "../../threejs/SelectBBox";
import { formatISO } from "date-fns";

export class ChangeObjectsAlignmentHandler extends StoreObjectHandler<ChangeSelectedObjectsAlignment> {
  run() {
    const { snapType, selectedIds } = this.event.payload;
    const updates: ChangeObjectsProperty["payload"]["changes"] = [];
    const historyChanges: ChangeObjectsProperty["payload"]["changes"] = [];

    let newStore = produce(this.store, (draft) => {
      let maxX: number | undefined = undefined;
      let minX: number | undefined = undefined;
      let maxY: number | undefined = undefined;
      let minY: number | undefined = undefined;

      selectedIds.forEach((_id) => {
        if (draft.objects[_id] === undefined) {
          console.warn("no object found in store", _id);
          return;
        }
        const pos = draft.objects[_id].position;
        // const w = draft.objects[_id].width;
        // const h = draft.objects[_id].height;

        if (minX === undefined) {
          minX = pos.x;
        } else if (pos.x < minX) {
          minX = pos.x;
        }

        if (maxX === undefined) {
          maxX = pos.x;
        } else if (pos.x > maxX) {
          maxX = pos.x;
        }

        if (minY === undefined) {
          minY = pos.y;
        } else if (pos.y < minY) {
          minY = pos.y;
        }

        if (maxY === undefined) {
          maxY = pos.y;
        } else if (pos.y > maxY) {
          maxY = pos.y;
        }
      });

      if (snapType === SnapToLineType.X_MIN) {
        if (minX !== undefined) {
          const newPos = minX;
          selectedIds.forEach((id) => {
            const singleHistoryChange: ChangeObject = {
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            };
            historyChanges.push(singleHistoryChange);
            draft.objects[id].position.x = newPos;
            updates.push({
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            });
          });
        }
      } else if (snapType === SnapToLineType.X_MIDDLE) {
        if (minX !== undefined && maxX !== undefined) {
          const newPos = minX + (maxX - minX) / 2;
          selectedIds.forEach((id) => {
            const singleHistoryChange: ChangeObject = {
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            };
            historyChanges.push(singleHistoryChange);
            draft.objects[id].position.x = newPos;
            updates.push({
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            });
          });
        }
      } else if (snapType === SnapToLineType.X_MAX) {
        if (maxX !== undefined) {
          const newPos = maxX;
          selectedIds.forEach((id) => {
            const singleHistoryChange: ChangeObject = {
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            };
            historyChanges.push(singleHistoryChange);
            draft.objects[id].position.x = newPos;
            updates.push({
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            });
          });
        }
      } else if (snapType === SnapToLineType.Y_MIN) {
        if (minY !== undefined) {
          const newPos = minY;
          selectedIds.forEach((id) => {
            const singleHistoryChange: ChangeObject = {
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            };
            historyChanges.push(singleHistoryChange);
            draft.objects[id].position.y = newPos;
            updates.push({
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            });
          });
        }
      } else if (snapType === SnapToLineType.Y_MIDDLE) {
        if (minY !== undefined && maxY !== undefined) {
          const newPos = minY + (maxY - minY) / 2;
          selectedIds.forEach((id) => {
            const singleHistoryChange: ChangeObject = {
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            };
            historyChanges.push(singleHistoryChange);
            draft.objects[id].position.y = newPos;
            updates.push({
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            });
          });
        }
      } else if (snapType === SnapToLineType.Y_MAX) {
        if (maxY !== undefined) {
          const newPos = maxY;
          selectedIds.forEach((id) => {
            const singleHistoryChange: ChangeObject = {
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            };
            historyChanges.push(singleHistoryChange);
            draft.objects[id].position.y = newPos;
            updates.push({
              id: id,
              properties: {
                position: cloneDeep(draft.objects[id].position),
              },
            });
          });
        }
      } else {
        console.error("unknow SnapToLineType", snapType);
      }
    });

    if (this.saveToHistory) {
      const historyEvent: ChangeSelectedObjectsAlignmentHistory = {
        do: this.event,
        redo: this.event,
        undo: {
          name: AppEventType.ChangeObjectsProperty,
          payload: { changes: historyChanges },
        },
        createdAt: formatISO(new Date()),
      };
      this.history.push(historyEvent);
    }

    newStore = this.getStoreWithHistory(newStore);

    // @INFO: CHANGES IN THREEJS
    updates.forEach((_singleChange) => {
      if (newStore.objects[_singleChange.id] === undefined) {
        console.warn("no object found in store", _singleChange.id);
        return;
      }
      const object = newStore.objects[_singleChange.id];
      updateObjectPosition(
        _singleChange.id,
        object.objectType,
        object.position.x,
        object.position.y
      );
    });

    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);

    const selectBBox = getSelectBBox();
    selectBBox.syncRectsWithStore();

    notifyBackend(
      updates.map((_singleChange) => {
        return {
          action: SaveEventType.UPDATE,
          createdAt: new Date().toISOString(),
          object: newStore.objects[_singleChange.id],
        };
      })
    );
  }
}
