// @TODO: wybrac logger dla frontend
let enabled = true;
export const logger = {
  log: (...arg) => {
    if (enabled) {
      console.log(...arg);
    }
  },
  error: (...arg) => {
    if (enabled) {
      console.error(...arg);
    }
  },
  warn: (...arg) => {
    if (enabled) {
      console.warn(...arg);
    }
  },
  trace: (...arg) => {
    if (enabled) {
      console.log(...arg);
    }
  },
  disableAll: () => {
    enabled = false;
  },
  enableAll: () => {
    enabled = true;
  },
};
