import * as THREE from "three";
import { AddObjects, AppEventType } from "../../../controllers/AppEventType";
import { sendEvent } from "../../../controllers/storeController";
import { ObjectTypes } from "../../../editorObjects/Editor";
import { MouseDragHelper } from "../MouseDragHelper";
import { screenToWorld } from "../screenToWorld";
import { MouseEventsBase } from "./MouseEventsBase";

export class MouseEventsEditorModeAddBead extends MouseEventsBase {
  mouseDragHelper: MouseDragHelper;
  lastAddPosition: THREE.Vector2;

  constructor() {
    super();
    this.mouseDragHelper = new MouseDragHelper();
    this.lastAddPosition = new THREE.Vector2();
  }

  onWheel(event: PointerEvent) {}

  onResize(event: PointerEvent) {}

  onDown(event: PointerEvent) {
    const { x, y } = screenToWorld(event.clientX, event.clientY);
    this.mouseDragHelper.onDown(x, y);
    this.addBead(x, y);
    this.lastAddPosition.set(x, y);
  }

  onMove(event: PointerEvent) {
    if (this.mouseDragHelper.isDragStarted === true) {
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onMove(x, y);
      const currPosition = new THREE.Vector2(x, y);
      const distance = this.lastAddPosition.distanceTo(currPosition);
      if (distance > 10) {
        this.lastAddPosition = currPosition.clone();
        this.addBead(x, y);
      }
    }
  }

  onUp(event: PointerEvent) {
    this.mouseDragHelper.onUp();
    this.lastAddPosition = new THREE.Vector2();
  }

  addBead(worldX: number, wordlY: number) {
    const action1: AddObjects = {
      name: AppEventType.AddObjects,
      payload: {
        objects: [
          {
            objectType: ObjectTypes.Bead,
            position: { x: worldX, y: wordlY, z: 1 },
          },
        ],
      },
    };
    sendEvent(action1);
  }
}
