export const getProjectData = (
  id: string
): Promise<{ board: { uuid: string; name: string }; objects: AppObject[] }> => {
  if (id === "") {
    return new Promise((resolve, reject) => {
      resolve({
        board: { name: "---", uuid: "" },
        objects: [],
      });
    });
  }

  if (id === "demo") {
    return new Promise((resolve, reject) => {
      getDemoProjectData().then((response) => {
        resolve(response);
      });
    });
  }

  return new Promise((resolve, reject) => {
    fetchBoard(id)
      .then((response) => {
        resolve(response);
      })
      .catch((err) => reject(err));
  });
};

