import { EditBBox, NAME_SEPARATOR, ObjectTypes } from "./Editor";
import { DeepPartial } from "ts-essentials";
import { buildPoint } from "./Point";
import { merge } from "lodash";
import * as THREE from "three";

export enum EditBBoxChildSuffix {
  MAIN = "MAIN",
  BG = "BG",
  EDIT_CONTAINER = "EDIT_CONTAINER",
  LINES = "LINES",
  WHITE_DOT_BG = "WHITE_DOT_BG",
  GRAY_DOT_BG = "GRAY_DOT_BG",
  RESIZE_CONTAINER = "RESIZE_CONTAINER",
  DOT_RESIZE_TOP_LEFT = "DOT_RESIZE_TOP_LEFT",
  DOT_RESIZE_TOP_RIGHT = "DOT_RESIZE_TOP_RIGHT",
  DOT_RESIZE_BOTTOM_LEFT = "DOT_RESIZE_BOTTOM_LEFT",
  DOT_RESIZE_BOTTOM_RIGHT = "DOT_RESIZE_BOTTOM_RIGHT",
}

export const getThreeNameFromEditBBoxId = (
  id: string,
  childSuffix: EditBBoxChildSuffix = EditBBoxChildSuffix.MAIN
) => {
  return `${ObjectTypes.SceneEditBBox}${NAME_SEPARATOR}${id}${NAME_SEPARATOR}${childSuffix}`;
};

export const getEditBBoxIdFromThreeName = (name?: string) => {
  if (!name) {
    return undefined;
  }
  if (name.includes(ObjectTypes.SceneEditBBox) === false) {
    return undefined;
  }
  return name.split(NAME_SEPARATOR)[1];
};

export const getThreeUserDataFromEditBBox = (
  id: string,
  childSuffix?: EditBBoxChildSuffix
) => {
  return {
    id,
    name: getThreeNameFromEditBBoxId(id, childSuffix),
  };
};

export const buildEditBBox = (props: DeepPartial<EditBBox>): EditBBox => {
  const defaults: EditBBox = {
    id: THREE.MathUtils.generateUUID(),
    position: buildPoint(0, 0),
    rotateX: 0,
    rotateY: 0,
    rotateZ: 0,
    height: 0,
    width: 0,
    depth: 10,
    editable: true,
    // beadIds: [],
    // regionsIds: [],
    selectedIds: [],
    canLockOrUnlock: true,
    isLocked: false,
    objectType: ObjectTypes.SceneEditBBox,
  };

  return merge(defaults, props);
};
