const StateButton: React.FC<{
  label: string;
  isSelected: boolean;
  onClick: any;
  icon: React.ReactChild;
  testId?: string;
}> = (props) => {
  return (
    <ButtonStyles>
      <div
        className={`action-button ${props.isSelected ? "is-active" : ""}`}
        onClick={props.onClick}
        data-tip={props.label}
        data-place={"right"}
        data-type={"dark"}
        data-effect={"solid"}
        data-test={props.testId}
      >
        <span className="icon">{props.icon}</span>
      </div>
    </ButtonStyles>
  );
};

