const FastAddBeadsButton: React.FC<{ label: string; icon: any }> = (props) => {
  const defaultDimesions = [20, 20];
  const defaultSelected = [1, 1];
  const [dimensions, setDimensions] = React.useState<number[]>(
    defaultDimesions
  );
  const [selected, setSelected] = React.useState<number[]>(defaultSelected);
  const [selectionVisible, setVisible] = React.useState<boolean>(false);

  const toggleSelection = () => {
    setVisible(!selectionVisible);
  };
  const onSelect = (rowIndex: number, colIndex: number) => {
    const action: AddMultipleBeads = {
      name: AppEventType.AddMultipleBeads,
      payload: {
        rows: rowIndex,
        columns: colIndex,
        figure: "rectangle",
      },
    };
    sendEvent(action);

    toggleSelection();
    setDimensions(defaultDimesions);
    setSelected(defaultSelected);
  };
  const onCancel = () => {
    setDimensions(defaultDimesions);
    setVisible(false);
  };
  return (
    <div style={{ position: "relative" }}>
      <ButtonStyles>
        <div
          className={`action-button ${selectionVisible ? "is-active" : ""}`}
          onClick={toggleSelection}
          data-tip={selectionVisible === true ? undefined : props.label}
          data-place={"right"}
          data-type={"dark"}
          data-effect={"solid"}
          data-tip-disable={selectionVisible === true}
        >
          <span className={`icon`}>{props.icon}</span>
        </div>
      </ButtonStyles>

      {selectionVisible === true ? (
        <SelectionStyles>
          <SelectionFrame
            selected={selected}
            dimensions={dimensions}
            setSelected={setSelected}
            setDimensions={setDimensions}
            onSelect={onSelect}
            onCancel={onCancel}
          />
        </SelectionStyles>
      ) : null}
    </div>
  );
};

