import { AppEvents } from "../AppEventType";
import { Store } from "../store";
import { AppHistory } from "../AppHistory";
import produce from "immer";
import { getTopBarPosition } from "../../threejs/eventEmitterHandlers";
import { getMousePositionAtWindow } from "../../threejs/mouseListeners/mouseScreenPosition";
import { appThree } from "../../threejs/renderThreejs";

export class BaseEventHandler<T extends AppEvents> {
  store: Store;
  event: T;
  history: AppHistory;
  saveToHistory: boolean;
  constructor(
    event: T,
    store: Store,
    history: AppHistory,
    saveToHistory: boolean
  ) {
    this.store = store;
    this.history = history;
    this.event = event;
    this.saveToHistory = saveToHistory;
  }

  getNewStoreWithTopBarPosition(store: Store) {
    return produce(store, (draft) => {
      const position = getTopBarPosition(
        store.selectedIds.map((id) => {
          return { id, objectType: store.objects[id].objectType };
        })
      );
      draft.ui.selectedTopBarPosition = position;
    });
  }

  getCenterPosition() {
    const { width, height, top, left } = appThree.el.getBoundingClientRect();
    const posX = left + width / 2;
    const posY = top + height / 2;
    return { posX, posY };
  }

  getMousePosition() {
    let posX = 0;
    let posY = 0;
    const pos = getMousePositionAtWindow();
    if (pos === undefined) {
      const center = this.getCenterPosition();
      posX = center.posX;
      posY = center.posY;
    } else {
      posX = pos.x;
      posY = pos.y;
    }
    return { posX, posY };
  }

  getStoreWithHistory(_store?: Store) {
    const store = _store === undefined ? this.store : _store;
    return produce(store, (draft) => {
      draft.history.canRedo = this.history.canRedo();
      draft.history.canUndo = this.history.canUndo();
      draft.history.events = this.history.getSimpleHistoryEvents();
    });
  }
}
