import React from "react";
import {
  AppEventType,
  ChangeUiSettingsSidebar,
} from "../controllers/AppEventType";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleDoubleLeft,
  faAngleDoubleRight,
  faComment,
  faHistory,
  faListAlt,
} from "@fortawesome/free-solid-svg-icons";
import { sendEvent } from "../controllers/storeController";
import styled from "styled-components";
import { useSelector } from "react-redux";
import { Editor } from "../editorObjects/Editor";
import { useTranslation } from "react-i18next";

export const BottomBar: React.FC<{}> = (props) => {
  const [isOpen, setIsOpen] = React.useState(true);
  const leftSidebar = useSelector((state: Editor) => {
    return state.ui.leftSidebar;
  });
  const rightSidebar = useSelector((state: Editor) => {
    return state.ui.rightSidebar;
  });
  const toggleBar = () => {
    setIsOpen(!isOpen);
  };
  const onClickOpenComments = () => {
    const event: ChangeUiSettingsSidebar = {
      name: AppEventType.ChangeUiSettingsSidebar,
      payload: {
        open: true,
        panel: "comments",
        toggle: true,
      },
    };
    sendEvent(event);
  };
  const onClickOpenHistory = () => {
    const event: ChangeUiSettingsSidebar = {
      name: AppEventType.ChangeUiSettingsSidebar,
      payload: {
        open: true,
        panel: "history",
        toggle: true,
      },
    };
    sendEvent(event);
  };
  const onClickOpenObjects = () => {
    const event: ChangeUiSettingsSidebar = {
      name: AppEventType.ChangeUiSettingsSidebar,
      payload: {
        open: true,
        panel: "objects",
        toggle: true,
      },
    };
    sendEvent(event);
  };
  const [t] = useTranslation(["board"]);
  return (
    <BottomBarStyles>
      <div className={`bottom-bar-wrapper ${isOpen ? "open" : "closed"}`}>
        <div className={"buttons"}>
          <div
            className={`item ${
              leftSidebar.panel === "comments" ? "active" : "inactive"
            }`}
            onClick={onClickOpenComments}
            data-tip={t("board:tab.comments")}
            data-place={"top"}
            data-type={"dark"}
            data-effect={"solid"}
          >
            <div className={"value"}>
              <FontAwesomeIcon icon={faComment} />
            </div>
          </div>
          <div
            className={`item ${
              rightSidebar.panel === "history" ? "active" : "inactive"
            }`}
            onClick={onClickOpenHistory}
          >
            <div className={"value"}>
              <FontAwesomeIcon
                icon={faHistory}
                data-tip={t("board:tab.history_events")}
                data-place={"top"}
                data-type={"dark"}
                data-effect={"solid"}
              />
            </div>
          </div>
          <div
            className={`item ${
              rightSidebar.panel === "objects" ? "active" : "inactive"
            }`}
            onClick={onClickOpenObjects}
          >
            <div className={"value"}>
              <FontAwesomeIcon
                icon={faListAlt}
                data-tip={t("board:tab.objects")}
                data-place={"top"}
                data-type={"dark"}
                data-effect={"solid"}
              />
            </div>
          </div>
        </div>
        <div className={"toggle-item"} onClick={toggleBar}>
          <div className={"value"}>
            <FontAwesomeIcon
              icon={isOpen ? faAngleDoubleLeft : faAngleDoubleRight}
            />
          </div>
        </div>
      </div>
    </BottomBarStyles>
  );
};

export const BottomBarStyles = styled.div`
  .bottom-bar-wrapper {
    position: absolute;
    bottom: var(--standardPadding);
    left: var(--standardPadding);
    z-index: var(--zIndexBottomBar);
    width: auto;
    display: flex;
    /* transition: all 0.4s ease-in-out; */
    background: #fff;
    height: 55px;
    transition: width 0.2s cubic-bezier(0.25, 0.1, 0.25, 0.1),
      left 0.4s ease-in-out;
    &.open {
    }
    &.closed {
      .buttons {
        width: 0;
      }
    }
  }
  .buttons {
    overflow: hidden;
    transition: width 0.2s cubic-bezier(0.25, 0.1, 0.25, 0.1);
    width: 100%;
    white-space: nowrap;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .item {
    width: 45px;
    height: 55px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    svg path {
      transition: all 0.4s ease-in-out;
      fill: var(--uiButton);
    }
    &.active,
    &:hover {
      color: var(--uiButtonActive);
      svg path {
        fill: var(--uiButtonActive);
      }
    }
  }
  .toggle-item {
    width: 45px;
    height: 55px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    svg path {
      fill: #e9e9e9;
    }
  }
`;
