import { Editor } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";
import { Store } from "../controllers/store";

export class MainImmerReducer extends ImmerReducer<Editor> {
  // /** @deprecated */
  // action_test() {}
  // /** @deprecated */
  // action_restoreState() {}
  updateEditorState(newState: Store) {
    this.draftState.objects = newState.objects;
    this.draftState.selectedIds = newState.selectedIds;
    this.draftState.ui = newState.ui;
    this.draftState.board = newState.board;
    this.draftState.history = newState.history;
  }
}
