import { ClipboardCopy } from "../AppEventType";
import { BaseEventHandler } from "../BaseHandlers/BaseEventHandler";
import { setClipboardBoardContent } from "../../threejs/clipboard";
import { cloneDeep } from "lodash";

export class ClipboardCopyHandler extends BaseEventHandler<ClipboardCopy> {
  run() {

    const objects = this.store.selectedIds.map((id) => {
      return cloneDeep(this.store.objects[id]);
    });
    setClipboardBoardContent({
      objects,
    });
  }
}


