import React from "react";
import { Bead, Editor, ObjectTypes } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import styled from "styled-components";
import { sendEvent } from "../controllers/storeController";
import {
  AppEventType,
  ChangeUiSettingsSidebar,
} from "../controllers/AppEventType";
import { thumbnailsGenerator } from "../threejs/thumbnailsGenerator";
import { cloneDeep } from "lodash";
import { useTranslation } from "react-i18next";

export const BeadsSummary: React.FC<{}> = () => {
  const [t] = useTranslation(["board"]);
  const { groupedCount } = useSelector((state: Editor) => {
    const groups: {
      [key: string]: {
        count: number;
        example: Bead;
        textName: string;
        textTooltip: string;
      };
    } = {};
    Object.keys(state.objects).forEach((id) => {
      if (state.objects[id].objectType === ObjectTypes.Bead) {
        const bead = state.objects[id] as Bead;
        const groupData: any = {
          width: bead.width,
          height: bead.height,
          depth: bead.depth,
          // rotateX: bead.rotateX,
          // rotateY: bead.rotateY,
          // rotateZ: bead.rotateZ,
          type: bead.type,
          color: bead.color,
        };
        const groupId = JSON.stringify(groupData);

        if (groups[groupId] === undefined) {
          const item = cloneDeep(bead) as Bead;
          groups[groupId] = {
            count: 1,
            example: item,
            textName: `${item.color}`,
            textTooltip: t(`board:bead.summary_colors_with_count`, {
              color: item.color,
              count: 1,
            }),
          };
          // `${item.color} - 1x`
        } else {
          const newCount = groups[groupId].count + 1;
          groups[groupId].count = newCount;
          groups[groupId].textTooltip = t(
            `board:bead.summary_colors_with_count`,
            {
              color: bead.color,
              count: newCount,
            }
          );
          // `${bead.color} - ${newCount}x`
          // {{color}}-{{count}}
        }
      }
    });
    const groupedCount: {
      count: number;
      example: Bead;
      textName: string;
      textTooltip: string;
      textCount: string;
    }[] = [];
    const maxGroups = 10;
    Object.keys(groups).forEach((id, index) => {
      if (index > maxGroups) {
        return;
      }
      const group = groups[id];
      const textCount = `${group.count}x`;
      groupedCount.push({
        count: group.count,
        example: group.example,
        textName: group.textName,
        textTooltip: group.textName,
        textCount,
      });
    });
    return {
      groupedCount,
    };
  });
  const onClickOpenObjectsPanel = () => {
    const event: ChangeUiSettingsSidebar = {
      name: AppEventType.ChangeUiSettingsSidebar,
      payload: {
        panel: "objects",
        open: true,
        toggle: true,
      },
    };
    sendEvent(event);
  };

  return (
    <BeadsSummaryStyles>
      <div className={"beads-summary"}>
        <div
          className={"beads-summary-content"}
          onClick={onClickOpenObjectsPanel}
        >
          {groupedCount.map((group, index) => {
            if (!group) {
              return null;
            }
            if (!group.example) {
              return null;
            }
            if (!thumbnailsGenerator) {
              return null;
            }
            const imgSrc = thumbnailsGenerator.getObjectThumbnail(
              group.example,
              30,
              30
            );
            return (
              <div
                className={"bead-count"}
                key={index}
                data-tip={group.textTooltip}
                data-place={"top"}
                data-type={"dark"}
                data-effect={"solid"}
              >
                <span className={"preview"}>
                  <img src={imgSrc} alt={""} />
                </span>
                <span className={"value"}>{group.textCount}</span>
              </div>
            );
          })}
        </div>
      </div>
    </BeadsSummaryStyles>
  );
};

export const BeadsSummaryStyles = styled.div`
  .beads-summary {
    position: absolute;
    bottom: var(--standardPadding);
    right: calc(var(--standardPadding) + 170px + var(--standardPadding));
    z-index: var(--zIndexBeadsSummary);
    transition: all 0.4s ease-in-out;
  }
  .beads-summary-content {
    background: #fff;
    display: flex;
    column-gap: var(--standardPadding);
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: calc(var(--standardPadding) * 2);
    padding-right: calc(var(--standardPadding) * 2);
  }
  .bead-count {
    display: flex;
    flex-direction: column;
    cursor: pointer;

    .value {
      font-size: 15px;
      font-weight: 600;
    }

    .preview {
      width: 30px;
      height: 30px;
      /* border: 1px solid #eee; */
      /* border-radius: 30px; */
      /* font-size: 8px; */
      /* line-height: 27px; */
      display: flex;
      justify-content: center;
      align-items: center;
      white-space: nowrap;
      overflow: hidden;
    }
  }
`;
