import { appThree } from "../renderThreejs";
import { screenToWorld } from "./screenToWorld";

export const getRect10x10FromScreenToWorld = () => {
  const viewportOffset = appThree.el.getBoundingClientRect();
  const w = 10;
  const h = 10;
  const x1 = viewportOffset.left;
  const x2 = viewportOffset.left + w;
  const y1 = viewportOffset.top;
  const y2 = viewportOffset.top + h;
  const pos1 = screenToWorld(x1, y1);
  const pos2 = screenToWorld(x2, y2);
  const maxX = Math.max(pos1.x, pos2.x);
  const minX = Math.min(pos1.x, pos2.x);
  const minY = Math.min(pos1.y, pos2.y);
  const maxY = Math.max(pos1.y, pos2.y);
  const rect10x10FromScreenToWorld = {
    min: {
      x: minX,
      y: minY,
    },
    max: {
      x: maxX,
      y: maxY,
    },
    w: maxX - minX,
    h: maxY - minY,
  };
  return rect10x10FromScreenToWorld;
};
