export const prepareBoard = (id: string) => {
  return getProjectData(id).then((data) => {
    return new Promise((resolve, reject) => {
      const refreshThreejs = shouldRefreshThree();
      const refreshReact = shouldRefreshReact();

      const store = getStore();

      const newStore = produce(store, (draft) => {
        draft.board = cloneDeep(emptyStore.board);
        draft.history = cloneDeep(emptyStore.history);
        draft.objects = cloneDeep(emptyStore.objects);
        draft.selectedIds = cloneDeep(emptyStore.selectedIds);
        draft.ui = cloneDeep(emptyStore.ui);

        if (data.objects !== undefined && data.objects.length > 0) {
          data.objects.forEach((object) => {
            draft.objects[object.id] = object;
          });
        }

        draft.board.name = data.board?.name ?? "";
        draft.board.uuid = data.board?.uuid ?? "";
        draft.board.canEdit = true;
        draft.ui.blockThreejs.loading = false;
      });

      setStore(newStore);

      if (refreshThreejs) {
        store.ui.lights.forEach((light) => {
          addLightFromUI(light, appThree.scene);
        });
      }

      if (refreshThreejs) {
        if (store.ui.grid) {
          addGridFromUi(store.ui.grid);
        }
      }

      if (refreshThreejs) {
        Object.keys(newStore.objects).forEach((id) => {
          addObjectByObjectType(newStore.objects[id]);
        });
      }

      if (refreshReact) {
        refreshReactState(newStore);
      }

      // testPaintBeads();
      // testRegionsWithMoveAndNoSnap()
      // testRegionsWithBricks();
      testRegions();
      // sendEvent({
      //   name: AppEventType.ChangeUiSettingsZoom,
      //   payload: {
      //     useAnimation: false,
      //     zoomType: "zoomByScroll",
      //     scrollDelta: 20
      //   },
      // });
      resolve(undefined);
    });
  });
};

export const clearBoard = (id: string) => {
  return new Promise<void>((resolve) => {
    const store = getStore();
    const newStore = produce(store, (draft) => {
      draft = cloneDeep(emptyStore);
    });

    refreshReactState(newStore);
    resolve();
  });
};

// eslint-disable-next-line @typescript-eslint/no-unused-vars
const testPaintBeads = () => {
  const size = 20;
  for (let i = 0; i < 10; i++) {
    const beadr = buildBead({
      color: "red",
      position: {
        x: size * 1 + 10,
        y: size * i + 10,
      },
      width: size,
      height: size,
    });
    const beadg = buildBead({
      color: "green",
      position: {
        x: size * 2 + 10,
        y: size * i + 10,
      },
      width: size,
      height: size,
    });
    const beadb = buildBead({
      color: "blue",
      position: {
        x: size * 3 + 10,
        y: size * i + 10,
      },
      width: size,
      height: size,
    });
    sendEvent({
      name: AppEventType.AddObjects,
      payload: {
        objects: [beadr, beadg, beadb],
      },
    });
  }
};

// eslint-disable-next-line @typescript-eslint/no-unused-vars
const testRegionsWithBricks = () => {

  sendEvent({
    name: AppEventType.AddObjects,
    payload: {
      objects: [
        {
          id: "regionA",
          objectType: ObjectTypes.Region,
          width: 100,
          height: 100,
          position: {
            x: 0,
            y: 0,
            z: 0,
          },
          columns: 7,
          rows: 7,
          gridType: RegionGridType.Bricks,
          shape: RegionShape.Triangle,
        },
      ],
    },
  });
  sendEvent({
    name: AppEventType.ChangeObjectsProperty,
    payload: {
      changes: [
        {
          id: "regionA",
          properties: {
            autoFillBeads: true,
            snapBeadsToCell: true,
          },
        },
      ],
    },
  });
  sendEvent({
    name: AppEventType.ChangeObjectsProperty,
    payload: {
      changes: [
        {
          id: "regionA",
          properties: {
            position: {
              x: -50,
              y: 0,
              z: 0,
            },
          },
        },
      ],
    },
  });
};

