import { Group, NAME_SEPARATOR, ObjectTypes } from "./Editor";
import { DeepPartial } from "ts-essentials";
import { buildPoint } from "./Point";
import { merge } from "lodash";
import * as THREE from "three";

export enum GroupChildSuffix {
  MAIN = "MAIN",
  BG = "bg",
  ANY = "GroupChildOf",
}

export const getThreeNameFromGroupId = (
  id: string,
  childSuffix: GroupChildSuffix = GroupChildSuffix.MAIN
) => {
  return `${ObjectTypes.Group}${NAME_SEPARATOR}${id}${NAME_SEPARATOR}${childSuffix}`;
};

export const getGroupIdFromThreeName = (name?: string) => {
  if (!name) {
    return undefined;
  }
  if (name.includes(ObjectTypes.Group) === false) {
    return undefined;
  }
  return name.split(NAME_SEPARATOR)[1];
};

export const getThreeUserDataFromGroup = (
  group: Group,
  childSuffix?: GroupChildSuffix
) => {
  return merge({}, group, {
    name: getThreeNameFromGroupId(group.id, childSuffix),
  });
};

export const buildGroup = (props: DeepPartial<Group>): Group => {
  const defaults: Group = {
    id: THREE.MathUtils.generateUUID(),
    position: buildPoint(0, 0),
    rotateX: 90,
    rotateY: 0,
    rotateZ: 0,
    height: 10,
    width: 10,
    depth: 10,
    canLockOrUnlock: true,
    isLocked: false,
    objectType: ObjectTypes.Group,
    childrenIds: [],
  };

  return merge(defaults, props);
};
