import { debounce } from "lodash";
import { AppData } from "./AppData";
import { buildBead } from "./editorObjects/Bead";
import { AppObject, Bead, BeadTypes } from "./editorObjects/Editor";

let apiHost: string;
let headers: { [key: string]: any } = {
  "Content-Type": "application/json",
};

export const setApiFromAppData = (data: AppData) => {
  apiHost = data.apiHost;
  headers["Authorization"] = "Bearer " + data.token;
};

const handleError = (err: any, reject: any) => {
  console.error("request error", err);
  reject("err");
};

export const prepareBoardsList = (): Promise<{
  boards: {
    uuid: string;
    name: string;
  }[];
}> => {
  return new Promise((resolve, reject) => {
    return fetch(apiHost + "api/boards/list", {
      method: "GET",
      headers,
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        resolve({
          boards: response.boards,
        });
      })
      .catch((err) => handleError(err, reject));
  });
};

export const fetchBoard = (
  id: string
): Promise<{ board: { uuid: string; name: string }; objects: AppObject[] }> => {
  return new Promise((resolve, reject) => {
    return fetch(apiHost + "api/boards/one/" + id, {
      method: "GET",
      headers,
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        // console.log("co tam jest??", response.objects);
        resolve({
          board: response.board,
          objects: Object.keys(response.objects).map(
            (key) => response.objects[key]
          ),
        });
      })
      .catch((err) => handleError(err, reject));
  });
};

export interface BeadResponse {
  display_name: string;
  bead_product_uuid: string;
  data: {
    id: string;
    internal_id: string;
    bead_producer: {
      uuid: string;
      internal_id: string;
      name: string;
    };
    bead_material: {
      uuid: string;
      internal_id: string;
      display_name: string;
    };
    bead_geometry: {
      uuid: string;
      internal_id: string;
      width: string;
      height: string;
      depth: string;
      whole: string;
      file_name: string;
    };
    bead_color: {
      uuid: string;
      internal_id: string;
      display_name: string;
      hex: string;
    };
    bead_color_category: {
      uuid: string;
      internal_id: string;
      display_name: string;
      hex: string;
    };
  };
}

export const fetchBeadProducts = (
  q?: string,
  producer?: string,
  color?: string
): Promise<{
  response: { bead: Bead; item: BeadResponse }[];
}> => {
  return new Promise((resolve, reject) => {
    const queryString = new URLSearchParams();
    if (q !== undefined && q.length > 0) {
      queryString.append("q", q);
    }
    if (producer !== undefined && producer.length > 0) {
      queryString.append("producer", producer);
    }
    if (color !== undefined && color.length > 0) {
      queryString.append("color_category", color);
    }
    return fetch(apiHost + "api/bead_products/list?" + queryString.toString(), {
      method: "GET",
      headers,
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        let result: { bead: Bead; item: BeadResponse }[] = [];
        if (
          response &&
          response.bead_products &&
          response.bead_products.length > 0
        ) {
          result = (response?.bead_products as BeadResponse[]).map((item) => {
            let type: BeadTypes | undefined = undefined;
            if (
              item &&
              item.data &&
              item.data.bead_geometry &&
              item.data.bead_geometry.file_name
            ) {
              if (
                item.data.bead_geometry.file_name === "model_toho_round.obj"
              ) {
                type = BeadTypes.MODEL_TOHO_ROUND;
              }
              if (item.data.bead_geometry.file_name === "model_cylinder.obj") {
                type = BeadTypes.MODEL_CYLINDER;
              }
              if (
                item.data.bead_geometry.file_name === "model_fire_polish.obj"
              ) {
                type = BeadTypes.MODEL_FIRE_POLISH;
              }
            }
            const { height, width, depth } = item.data.bead_geometry;
            const getNum = (value) => {
              if (value === undefined || value === null || value === "") {
                return 1;
              }
              return value.includes(",")
                ? Number(value.replaceAll(",", "."))
                : Number(value);
            };
            return {
              bead: buildBead({
                id: item.data.id,
                color: item.data.bead_color.hex,
                type,
                height: getNum(height),
                width: getNum(width),
                depth: getNum(depth),
              }),
              item,
            };
          });
        }
        resolve({
          response: result,
        });
      })
      .catch((err) => handleError(err, reject));
  });
};

export const createBoard = (
  name?: string
): Promise<{ board: { uuid: string; name: string } }> => {
  return new Promise((resolve, reject) => {
    return fetch(apiHost + "api/boards/create", {
      method: "POST",
      headers,
      body: JSON.stringify({ name: name ?? "" }),
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        resolve({
          board: response.board,
        });
      })
      .catch((err) => handleError(err, reject));
  });
};
export const sendBoardBatchEvents = (
  id: string,
  body: string
): Promise<{ status: boolean }> => {
  return new Promise((resolve, reject) => {
    return fetch(apiHost + "api/boards/events/" + id, {
      method: "POST",
      headers,
      body,
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        resolve({
          status: response.statusCode,
        });
      })
      .catch((err) => handleError(err, reject));
  });
};

let tempBoardId: string | undefined;
let sendingEnabled: boolean = false;
let sendingIntervalMs: number = 500;
let sendingInterval: any = undefined;
let lastSendTimestamp: any = new Date().getTime();
let toSendEventsQueue: SaveEvent[] = [];

export enum SaveEventType {
  CREATE = "CREATE",
  UPDATE = "UPDATE",
  DELETE = "DELETE",
}
export interface SaveEvent {
  action: SaveEventType;
  object: AppObject;
  createdAt: string;
}

export const notifyBackend = (events: SaveEvent[]) => {
  if (sendingEnabled === true) {
    if (events.length > 0) {
      events.forEach((event) => {
        toSendEventsQueue.push(event);
      });
      debouncedSend();
    }
  }
};
const debouncedSend = debounce(
  () => triggerSendBatchEvents(),
  sendingIntervalMs,
  { trailing: true }
);

export const startBatchQueue = (id: string) => {
  sendingEnabled = true;
  tempBoardId = id;
  lastSendTimestamp = new Date().getTime();
  // sendingInterval = setInterval(debouncedSend, 250);
};

const triggerSendBatchEvents = () => {
  const now = new Date().getTime();
  console.log(
    "triggerSendBatchEvents: diff: ",
    now - lastSendTimestamp,
    toSendEventsQueue.length > 0
  );
  if (toSendEventsQueue.length > 0 && tempBoardId !== undefined) {
    const body = JSON.stringify({ changes: toSendEventsQueue });
    toSendEventsQueue = [];
    sendBoardBatchEvents(tempBoardId, body);
  }
  lastSendTimestamp = now;
};

export const stopBatchQueue = (id: string) => {
  sendingEnabled = false;
  if (tempBoardId !== undefined) {
    tempBoardId = undefined;
  }
  if (sendingInterval !== undefined) {
    clearInterval(sendingInterval);
  }
  if (toSendEventsQueue.length > 0) {
    toSendEventsQueue = [];
  }
};