export const getModelFromBeadType = (type: BeadTypes) => {
  let beadModel: THREE.Object3D | undefined;

  if (type === BeadTypes.MODEL_CYLINDER) {
    if (base3dModels.model_cylinder.model === null) {
      console.error("triggerAddBeadToScene: 3dModel is not loaded baseBead");
      return beadModel;
    }
    beadModel = base3dModels.model_cylinder.model.clone();
  }

  if (type === BeadTypes.MODEL_FIRE_POLISH) {
    if (base3dModels.model_fire_polish.model === null) {
      console.error("triggerAddBeadToScene: 3dModel is not loaded baseBead");
      return beadModel;
    }
    beadModel = base3dModels.model_fire_polish.model.clone();
  }

  if (type === BeadTypes.MODEL_TOHO_ROUND) {
    if (base3dModels.model_toho_round.model === null) {
      console.error("triggerAddBeadToScene: 3dModel is not loaded baseBead");
      return beadModel;
    }
    beadModel = base3dModels.model_toho_round.model.clone();
  }

  if (type === BeadTypes.MODEL_SPHERE) {
    if (base3dModels.model_sphere.model === null) {
      console.error("triggerAddBeadToScene: 3dModel is not loaded baseBead");
      return beadModel;
    }
    beadModel = base3dModels.model_sphere.model.clone();
  }

  return beadModel;
};

export const getModelBBoxFromBeadType = (type: BeadTypes) => {
  if (type === BeadTypes.MODEL_CYLINDER) {
    return base3dModels.model_cylinder.bbox;
  }

  if (type === BeadTypes.MODEL_SPHERE) {
    return base3dModels.model_sphere.bbox;
  }

  if (type === BeadTypes.MODEL_FIRE_POLISH) {
    return base3dModels.model_fire_polish.bbox;
  }

  if (type === BeadTypes.MODEL_TOHO_ROUND) {
    return base3dModels.model_toho_round.bbox;
  }

  console.warn("missing model for bead type : ", type);

  return null;
};

export const getModelTexture = () => {
  return base3dModels.model_cylinder.texture;
};
