import { NAME_SEPARATOR, ObjectTypes, PreviewProject3D } from "./Editor";
import { DeepPartial } from "ts-essentials";
import { buildPoint } from "./Point";
import { merge } from "lodash";
import * as THREE from "three";

export enum PreviewProject3DChildSuffix {
  MAIN = "MAIN",
  BG = "BG",
  ANY = "PreviewProject3DChildOf",
}

export const getThreeNameFromPreviewProject3DId = (
  id: string,
  childSuffix: PreviewProject3DChildSuffix = PreviewProject3DChildSuffix.MAIN
) => {
  return `${ObjectTypes.PreviewProject3D}${NAME_SEPARATOR}${id}${NAME_SEPARATOR}${childSuffix}`;
};

export const getPreviewProject3DIdFromThreeName = (name?: string) => {
  if (!name) {
    return undefined;
  }
  if (name.includes(ObjectTypes.PreviewProject3D) === false) {
    return undefined;
  }
  return name.split(NAME_SEPARATOR)[1];
};

export const getThreeUserDataFromPreviewProject3D = (
  obj: PreviewProject3D,
  childSuffix?: PreviewProject3DChildSuffix
) => {
  return merge({}, obj, {
    name: getThreeNameFromPreviewProject3DId(obj.id, childSuffix),
  });
};

export const buildPreviewProject3D = (
  props: DeepPartial<PreviewProject3D>
): PreviewProject3D => {
  const defaults: PreviewProject3D = {
    id: THREE.MathUtils.generateUUID(),
    position: buildPoint(0, 0),
    rotateX: 90,
    rotateY: 0,
    rotateZ: 0,
    height: 10,
    width: 10,
    depth: 10,
    canLockOrUnlock: true,
    isLocked: false,
    objectType: ObjectTypes.PreviewProject3D,
    color: "red",
  };

  return merge(defaults, props);
};
