import produce from "immer";
import { AppEventType, GroupObjects } from "../AppEventType";
import { setStore } from "../store";
import { GroupObjectsHistory } from "../AppHistory";
import {
  addObjectByObjectType,
  triggerDeleteObjectIds,
} from "../../threejs/eventEmitterHandlers";
import { Group, groupIdKey, ObjectTypes } from "../../editorObjects/Editor";
import { buildGroup } from "../../editorObjects/Group";
import { refreshReactState } from "../refreshReactState";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import * as THREE from "three";
import { appThree } from "../../threejs/renderThreejs";
import { cloneDeep } from "lodash";
import { notifyBackend, SaveEventType } from "../../ApiService";
import { formatISO } from "date-fns";

export class GroupObjectsHandler extends StoreObjectHandler<GroupObjects> {
  run() {
    const idsGroups: string[] = [];
    const idsObjectsWithoutGroups: string[] = [];
    const idsInGroup: string[] = [];
    const removed: GroupObjects["payload"]["removed"] = [];
    const added: Group[] = [];

    let newStore = produce(this.store, (draft) => {
      let groupCount: any = 0;
      let inGroupCount: any = 0;
      let noGroupCount: any = 0;
      this.event.payload.objects.forEach((objId) => {
        if (draft.objects[objId.id] === undefined) {
          console.warn("object not found in store", objId.id);
          return;
        }
        const object = draft.objects[objId.id];
        const isGroup = object.objectType === ObjectTypes.Group;
        const isInGroup =
          object[groupIdKey] !== undefined && object[groupIdKey].length > 0;
        if (isGroup) {
          if (idsGroups.includes(object.id) === false) {
            groupCount = groupCount + 1;
            idsGroups.push(object.id);
          }
        } else if (isInGroup) {
          inGroupCount = inGroupCount + 1;
          idsObjectsWithoutGroups.push(object.id);
          idsInGroup.push(object.id);
          if (
            object[groupIdKey] &&
            draft.objects[object[groupIdKey]] !== undefined
          ) {
            if (idsGroups.includes(object[groupIdKey]) === false) {
              groupCount = groupCount + 1;
              idsGroups.push(object[groupIdKey]);
            }
          }
        } else {
          noGroupCount = noGroupCount + 1;
          idsObjectsWithoutGroups.push(object.id);
        }
      });

      if (
        this.event.payload.removed !== undefined &&
        this.event.payload.removed.length > 0
      ) {
        draft.selectedIds = [];
        this.event.payload.removed.forEach((group) => {
          if (group === undefined) {
            return;
          }
          if (group.id === undefined) {
            return;
          }

          const bbox = new THREE.Box3();

          group.childrenIds.forEach((id) => {
            const object = appThree.objects.get(id);
            if (object !== undefined) {
              bbox.expandByObject(object);
            }
          });

          const w = bbox.max.x - bbox.min.x;
          const h = bbox.max.y - bbox.min.y;
          const newGroup = buildGroup({
            id: group.id,
            childrenIds: group.childrenIds,
            position: {
              x: bbox.min.x + w / 2,
              y: bbox.min.y + h / 2,
            },
            width: w,
            height: h,
          });

          draft.objects[group.id] = newGroup;

          added.push(newGroup);
          draft.selectedIds.push(newGroup.id);

          if (group.childrenIds !== undefined && group.childrenIds.length > 0) {
            group.childrenIds.forEach((id) => {
              if (draft.objects[id] === undefined) {
                console.warn("object not found after group.childrenIds...");
                return;
              }
              draft.objects[id][groupIdKey] = newGroup.id;
              draft.selectedIds.push(id);
            });
          }
        });
      } else {
        idsGroups.forEach((id) => {
          if (draft.objects[id] === undefined) {
            return;
          }
          this.selectObjectOnThreeScene(draft.objects[id], false);
          removed.push(cloneDeep(draft.objects[id] as Group));
          delete draft.objects[id];
        });

        const bbox = new THREE.Box3();

        idsObjectsWithoutGroups.forEach((id) => {
          const object = appThree.objects.get(id);
          if (object !== undefined) {
            bbox.expandByObject(object);
          }
        });

        let newGroupId: string | undefined = undefined;

        if (this.event.payload.added !== undefined) {
          newGroupId = this.event.payload.added.id;
        }

        const w = bbox.max.x - bbox.min.x;
        const h = bbox.max.y - bbox.min.y;
        const newGroup = buildGroup({
          id: newGroupId,
          position: {
            x: bbox.min.x + w / 2,
            y: bbox.min.y + h / 2,
          },
          width: w,
          height: h,
          childrenIds: idsObjectsWithoutGroups,
        });
        draft.objects[newGroup.id] = newGroup;
        added.push(newGroup);

        idsObjectsWithoutGroups.forEach((id) => {
          draft.objects[id][groupIdKey] = newGroup.id;
        });

        draft.selectedIds = [newGroup.id, ...idsObjectsWithoutGroups];
      }
    });

    if (this.saveToHistory) {
      const historyEvent: GroupObjectsHistory = {
        do: this.event,
        redo: {
          name: AppEventType.GroupObjects,
          payload: {
            objects: this.event.payload.objects.map(({ id }) => {
              return cloneDeep({ id });
            }),
            removed: this.event.payload.removed?.map(({ id, childrenIds }) => {
              return cloneDeep({ id, childrenIds });
            }),
            added: added[0]
              ? {
                  id: added[0].id,
                  childrenIds: added[0].childrenIds,
                }
              : undefined,
          },
        },
        undo: {
          name: AppEventType.UngroupObjects,
          payload: {
            objects: newStore.selectedIds.map((id) => {
              return cloneDeep({ id });
            }),
            removed: removed.map(({ id, childrenIds }) => {
              return cloneDeep({ id, childrenIds });
            }),
          },
        },
        createdAt: formatISO(new Date()),
      };
      this.history.push(historyEvent);
    }
    newStore = this.getStoreWithHistory(newStore);

    this.event.payload.objects.forEach((object) => {
      this.selectObjectOnThreeScene(object as any, false);
    });

    triggerDeleteObjectIds(
      this.event.payload.objects.map(({ id }) => {
        if (this.store.objects[id] === undefined) {
          return { id, objectType: "" } as any;
        }
        return { id, objectType: this.store.objects[id].objectType };
      })
    );

    triggerDeleteObjectIds(
      removed.map(({ id }) => {
        if (this.store.objects[id] === undefined) {
          return { id, objectType: "" } as any;
        }
        return { id, objectType: this.store.objects[id].objectType };
      })
    );

    idsObjectsWithoutGroups.forEach((id) => {
      addObjectByObjectType(newStore.objects[id]);
    });

    added.forEach((obj) => {
      addObjectByObjectType(newStore.objects[obj.id]);
    });

    newStore.selectedIds.forEach((id) => {
      this.selectObjectOnThreeScene(newStore.objects[id], true);
    });

    notifyBackend(
      added.map(({ id }) => {
        return {
          action: SaveEventType.CREATE,
          createdAt: new Date().toISOString(),
          object: cloneDeep(newStore.objects[id]),
        };
      })
    );

    notifyBackend(
      idsObjectsWithoutGroups.map((id) => {
        return {
          action: SaveEventType.UPDATE,
          createdAt: new Date().toISOString(),
          object: cloneDeep(newStore.objects[id]),
        };
      })
    );

    notifyBackend(
      removed.map(({ id }) => {
        return {
          action: SaveEventType.DELETE,
          createdAt: new Date().toISOString(),
          object: cloneDeep(this.store.objects[id]),
        };
      })
    );

    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);
  }
}
