import produce from "immer";
import { ChangeSelectObjects } from "../AppEventType";
import { setStore } from "../store";
import { refreshReactState } from "../refreshReactState";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";

export class ChangeSelectObjectsHandler extends StoreObjectHandler<ChangeSelectObjects> {
  run() {
    const { change } = this.event.payload;

    const historyChange: { id: string; isSelected: boolean }[] = [];
    let newStore = produce(this.store, (draft) => {
      change.forEach((obj) => {
        if (draft.objects[obj.id] === undefined) {
          console.warn("no object found in store", obj.id);
          return;
        }
        if (draft.selectedIds.includes(obj.id) === true) {
          if (obj.isSelected === true) {
            // do nothing
          } else {
            draft.selectedIds.splice(draft.selectedIds.indexOf(obj.id), 1);
          }
        } else {
          if (obj.isSelected === true) {
            draft.selectedIds.push(obj.id);
          } else {
            // nothing
          }
        }
        historyChange.push({
          id: obj.id,
          isSelected: obj.isSelected,
        });
      });
    });
    // setStore(newStore);

    // @INFO: Remove later
    // if (this.saveToHistory) {
    //   const historyEvent: ChangeSelectObjectsHistory = {
    //     do: this.event,
    //     redo: this.event,
    //     undo: {
    //       name: AppEventType.ChangeSelectObjects,
    //       payload: { change: historyChange },
    //     },
    //   };
    //   this.history.push(historyEvent);
    // }
    // appThree.additionalData.selectedBBox = undefined;

    historyChange.forEach((change) => {
      const object = newStore.objects[change.id];
      if (object === undefined) {
        return;
      }
      this.selectObjectOnThreeScene(object, change.isSelected);
    });
    // refreshReactState(newStore);
    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);
  }
}
