import React from "react";
import { AppObject, Editor, ObjectTypes } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import {
  AppEventType,
  ChangeUiSettingsSidebar,
} from "../controllers/AppEventType";
import { sendEvent } from "../controllers/storeController";
import styled from "styled-components";
import { CloseButton, PanelContentStyles } from "./PanelContentStyles";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHistory } from "@fortawesome/free-solid-svg-icons";
import { jumpToIndexHistory } from "../controllers/AppHistory";
import { thumbnailsGenerator } from "../threejs/thumbnailsGenerator";
import { useTranslation } from "react-i18next";
import { toDate } from "date-fns";

export const RightSidebar: React.FC<{}> = () => {
  const rightSidebar = useSelector((state: Editor) => {
    return state.ui.rightSidebar;
  });

  return (
    <RightSidebarStyles>
      <div className={"right-sidebar-wrapper"}>
        {rightSidebar.isOpen === true ? (
          <>
            {rightSidebar.panel === "history" ? <HistoryPanel /> : null}

            {rightSidebar.panel === "objects" ? <ObjectsPanel /> : null}
          </>
        ) : null}
      </div>
    </RightSidebarStyles>
  );
};

export const RightSidebarStyles = styled.div`
  .right-sidebar-wrapper {
    position: absolute;
    top: 0;
    right: calc(var(--leftSidebarWidth) * -1);
    width: var(--leftSidebarWidth);
    z-index: var(--zIndexRightSidebar);
    display: flex;
    transition: all 0.4s ease-in-out;
    background: #fff;
    height: 100%;
    & > div {
      width: 100%;
      height: 100%;
    }
  }
`;

const HistoryPanel: React.FC<{}> = () => {
  const [t] = useTranslation(["board"]);
  const onClickCloseHistory = () => {
    const event: ChangeUiSettingsSidebar = {
      name: AppEventType.ChangeUiSettingsSidebar,
      payload: {
        open: false,
        panel: "history",
      },
    };
    sendEvent(event);
  };
  const events = useSelector((state: Editor) => {
    return state.history.events;
  });
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const texts = [
    t("board:history_events.AddObjects"),
    t("board:history_events.DuplicateObjects"),
    t("board:history_events.RemoveObjects"),
    t("board:history_events.ChangeSelectObjects"),
    t("board:history_events.ChangeObjectsProperty"),
    t("board:history_events.ChangeObjectsAlignment"),
    t("board:history_events.ClipboardCut"),
    t("board:history_events.ClipboardCopy"),
    t("board:history_events.ClipboardPaste"),
    t("board:history_events.AddMultipleBeads"),
    t("board:history_events.ChangeUiSettings"),
    t("board:history_events.ChangeUiSettingsZoom"),
    t("board:history_events.ChangeUiSettingsSidebar"),
    t("board:history_events.GroupObjects"),
    t("board:history_events.UngroupObjects"),
  ];
  return (
    <PanelContentStyles>
      <div className={"content-wrapper"}>
        <div className={"title"}>{t("board:tab.history_events_title")}</div>
        <CloseButton onClick={onClickCloseHistory} />
        <HistoryPanelStyles>
          <div className={"events"}>
            {events.map((_, i, events) => {
              const index = events.length - 1 - i;
              const event = events[index];
              if (!event) {
                return <React.Fragment />;
              }
              const onClickHistoryItem = () => {
                jumpToIndexHistory(index);
              };
              
              return (
                <div
                  key={i}
                  className={`event ${event.isActive ? "active" : "inactive"}`}
                  onClick={onClickHistoryItem}
                >
                  <div className={"icon"}>
                    <FontAwesomeIcon icon={faHistory} />
                  </div>
                  <div className={"name"}>
                    {t(`board:history_events.${event.name}`)}
                  </div>
                  <div
                    className={"time"}
                    data-tip={`${t(
                      "board:tab.history_events_event_created_at"
                    )} ${t("dates:long", {
                      date: toDate(Date.parse(event.createdAt)),
                    })}`}
                    data-place={"bottom"}
                    data-type={"dark"}
                    data-effect={"solid"}
                  >
                    {t("dates:hh_mm_ss", {
                      date: toDate(Date.parse(event.createdAt)),
                    })}
                  </div>
                </div>
              );
            })}
          </div>
        </HistoryPanelStyles>
      </div>
    </PanelContentStyles>
  );
};

