const ObjectsPanel: React.FC<{}> = () => {
  const [t] = useTranslation(["board", "dates"]);
  const onClickCloseObjects = () => {
    const event: ChangeUiSettingsSidebar = {
      name: AppEventType.ChangeUiSettingsSidebar,
      payload: {
        open: false,
        panel: "objects",
      },
    };
    sendEvent(event);
  };

  const objects = useSelector((state: Editor) => {
    return state.objects;
  });
  type Filters = "all" | "beads" | "groups" | "other";
  const [filterSelected, setFilterSelected] = React.useState<Filters>("all");
  const [selectedObjects, setSelectedObjects] = React.useState<AppObject[]>([]);
  const onClickFilter = (filter: Filters) => {
    setFilterSelected(filter);
    if (filter === "all") {
      setSelectedObjects(
        Object.keys(objects).map((id) => {
          return objects[id];
        })
      );
    } else if (filter === "beads") {
      setSelectedObjects(
        Object.keys(objects)
          .map((id) => {
            return objects[id];
          })
          .filter((e) => e.objectType === ObjectTypes.Bead)
      );
    } else if (filter === "groups") {
      setSelectedObjects(
        Object.keys(objects)
          .map((id) => {
            return objects[id];
          })
          .filter((e) => e.objectType === ObjectTypes.Group)
      );
    } else if (filter === "other") {
      setSelectedObjects(
        Object.keys(objects)
          .map((id) => {
            return objects[id];
          })
          .filter(
            (e) =>
              !(
                e.objectType === ObjectTypes.Bead ||
                e.objectType === ObjectTypes.Group
              )
          )
      );
    }
  };
  const onClickFilterAll = () => {
    onClickFilter("all");
  };
  const onClickFilterBeads = () => {
    onClickFilter("beads");
  };
  const onClickFilterGroups = () => {
    onClickFilter("groups");
  };
  const onClickFilterOther = () => {
    onClickFilter("other");
  };

  React.useEffect(() => {
    onClickFilter(filterSelected);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [objects]);
  return (
    <PanelContentStyles>
      <div className={"content-wrapper"}>
        <div className={"title"}>{t("board:tab.objects_title")}</div>
        <CloseButton onClick={onClickCloseObjects} />
        <ObjectsPanelStyles>
          <div className={"filters"}>
            <div
              className={`filter ${filterSelected === "all" ? "active" : ""}`}
              onClick={onClickFilterAll}
            >
              {t("board:tab.objects_filter_all")}
            </div>
            <div
              className={`filter ${filterSelected === "beads" ? "active" : ""}`}
              onClick={onClickFilterBeads}
            >
              {t("board:tab.objects_filter_only_beads")}
            </div>
            <div
              className={`filter ${
                filterSelected === "groups" ? "active" : ""
              }`}
              onClick={onClickFilterGroups}
            >
              {t("board:tab.objects_filter_only_groups")}
            </div>
            <div
              className={`filter ${filterSelected === "other" ? "active" : ""}`}
              onClick={onClickFilterOther}
            >
              {t("board:tab.objects_filter_only_others")}
            </div>
          </div>
          <div className={"objects"}>
            {selectedObjects.map((object, index) => {
              if (!object) {
                return <React.Fragment />;
              }

              const isBead = object.objectType === ObjectTypes.Bead;
              const onClickObject = () => {
                if (isBead) {
                  thumbnailsGenerator.getObjectThumbnail(object, 500, 500);
                }
              };
              return (
                <div key={index} className={"object"} onClick={onClickObject}>
                  <div className={"icon"}>
                    {isBead ? (
                      <img
                        src={thumbnailsGenerator.getObjectThumbnail(
                          object,
                          30,
                          30
                        )}
                        alt={""}
                      />
                    ) : (
                      <FontAwesomeIcon icon={faHistory} />
                    )}
                  </div>
                  <div className={"name"}>{object.objectType}</div>
                  <div className={"id"}>ID:{object.id}</div>
                </div>
              );
            })}
          </div>
        </ObjectsPanelStyles>
      </div>
    </PanelContentStyles>
  );
};

