import React from "react";
import { Editor } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMinus, faPlus } from "@fortawesome/free-solid-svg-icons";
import { sendEvent } from "../controllers/storeController";
import {
  AppEventType,
  ChangeUiSettingsZoom,
} from "../controllers/AppEventType";
import styled from "styled-components";
import { useTranslation } from "react-i18next";

export const MinimapWrapper: React.FC<{}> = (props) => {
  const { zoomScale } = useSelector((state: Editor) => {
    const { zoomScale } = state.ui;
    return {
      zoomScale,
    };
  });
  const onClickPlus = () => {
    const updateUiEvent: ChangeUiSettingsZoom = {
      name: AppEventType.ChangeUiSettingsZoom,
      payload: {
        zoomType: "zoomInScreenCenter",
        useAnimation: true,
      },
    };
    sendEvent(updateUiEvent);
  };
  const onClickMinus = () => {
    const updateUiEvent: ChangeUiSettingsZoom = {
      name: AppEventType.ChangeUiSettingsZoom,
      payload: {
        zoomType: "zoomOutScreenCenter",
        useAnimation: true,
      },
    };
    sendEvent(updateUiEvent);
  };
  const [t] = useTranslation(["board", "translation"]);
  return (
    <MinimapWrapperStyles>
      <div className={"minimap-wrapper"}>
        <div className={"minimap-wrapper-content"}>
          <div
            className={"item"}
            onClick={onClickMinus}
            data-tip={t("board:ui.zoom_out")}
            data-place={"top"}
            data-type={"dark"}
            data-effect={"solid"}
          >
            <div className={"value"}>
              <FontAwesomeIcon icon={faMinus} />
            </div>
          </div>
          <div
            className={"item"}
            onClick={onClickPlus}
            data-tip={t("board:ui.zoom_in")}
            data-place={"top"}
            data-type={"dark"}
            data-effect={"solid"}
          >
            <div className={"value"}>
              <FontAwesomeIcon icon={faPlus} />
            </div>
          </div>
          <div
            className={"item-text"}
            data-tip={t("board:ui.zoom_percentage")}
            data-place={"top"}
            data-type={"dark"}
            data-effect={"solid"}
          >
            <div className={"value"}>{Math.round((1 / zoomScale) * 100)}%</div>
          </div>
        </div>
      </div>
    </MinimapWrapperStyles>
  );
};

export const MinimapWrapperStyles = styled.div`
  .minimap-wrapper {
    position: absolute;
    z-index: var(--zIndexMinimap);
    bottom: var(--standardPadding);
    right: var(--standardPadding);
    transition: all 0.4s ease-in-out;
    width: var(--minimapWidth);
  }
  .minimap-wrapper-content {
    background: #fff;
    display: flex;
    column-gap: var(--standardPadding) / 2;
  }
  .item {
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45px;
    transition: all 0.4s ease-in-out;
    color: var(--uiButton);
    cursor: pointer;
    svg path {
      transition: all 0.4s ease-in-out;
      fill: var(--uiButton);
    }
    &:hover {
      color: var(--uiButtonActive);
      svg path {
        fill: var(--uiButtonActive);
      }
    }
  }
  .item-text {
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    transition: all 0.4s ease-in-out;
    color: var(--uiButton);
    font-size: 18px;
    font-weight: 600;
  }
`;
