import { xor } from "lodash";

export const keyCodeToKeyName = {
  8: "backspace",
  9: "tab",
  13: "enter",
  16: "shift",
  17: "ctrl",
  18: "alt",
  20: "capslock",
  27: "escape",
  32: "space",
  33: "pageup",
  34: "pagedown",
  35: "end",
  36: "home",
  37: "left",
  38: "up",
  39: "right",
  40: "down",
  45: "ins",
  46: "del",
  91: "meta",
  93: "meta",
  224: "meta",

  106: "*",
  107: "+",
  109: "-",
  110: ".",
  111: "/",
  186: ";",
  187: "=",
  188: ",",
  189: "-",
  190: ".",
  191: "/",
  192: "`",
  219: "[",
  220: "\\",
  221: "]",
  222: "'",

  ArrowUp: "Up",
  ArrowDown: "Down",
  ArrowLeft: "Left",
  ArrowRight: "Right",
};

export const modifiers = {
  ShiftLeft: "shift",
  ShiftRight: "shift",
  AltLeft: "alt",
  AltRight: "alt",
  MetaLeft: "meta",
  MetaRight: "meta",
  ControlLeft: "control",
  ControlRight: "control",
};

export const isEventModifier = (event) => {
  // new browsers
  if ("code" in event) {
    return !!modifiers[event.code];
  }

  // old browsers
  return (
    event.which === 16 ||
    event.which === 17 ||
    event.which === 18 ||
    event.which === 91
  );
};

export const getKeyEventName = (event) => {
  let keyName = "";
  const keys: string[] = [];

  if ("code" in event) {
    keyName = keyCodeToKeyName[event.code]
      ? keyCodeToKeyName[event.code]
      : event.code;
  } else {
    const keyCode = event.which;
    keyName = String.fromCharCode(keyCode).toLowerCase();
    if (keyCodeToKeyName[keyCode]) {
      keyName = keyCodeToKeyName[keyCode];
    }
  }

  //   if (event.altKey) {
  //     keys.push("Alt");
  //   }

  //   if (event.shiftKey) {
  //     keys.push("Shift");
  //   }

  //   if (event.ctrlKey) {
  //     keys.push("Ctrl");
  //   }

  //   if (event.metaKey) {
  //     keys.push("Meta");
  //   }

  keys.push(keyName);

  return keys.join("+");
};

export const isInputElement = (element) => {
  if (!element || !element.tagName) {
    return false;
  }

  return (
    element.tagName === "BUTTON" ||
    element.tagName === "INPUT" ||
    element.tagName === "SELECT" ||
    element.tagName === "TEXTAREA"
  );
};

export const defaultMonitor = (eventName) => {
  // console.log(":keyboard event:", eventName);
};

export const isInputEvent = (event) => {
  return isInputElement(event.target || event.nativeEvent.target);
};

let __preventDefaultEvents = false;
// let __ignoreInputEvents = false;
let __pressed = {};
let __element: Element | Document | undefined = undefined;
let __subscriptions = {};
let __monitor: (
  eventName: string,
  matched: boolean,
  event: any
) => void = defaultMonitor;

export function initKeybinding(element: Element | Document | null = null) {
  if (typeof window === "undefined") {
    // not a browser environment?
    return false;
  }

  // default element is the window document
  if (element === null) {
    __element = window.document;
  }

  // ignore input events, except when the element is an input.
  // __ignoreInputEvents = !isInputElement(__element);

  let hidden, visibilityChange;
  if (typeof document.hidden !== "undefined") {
    // Opera 12.10 and Firefox 18 and later support
    hidden = "hidden";
    visibilityChange = "visibilitychange";
  } else if (typeof (document as any).msHidden !== "undefined") {
    hidden = "msHidden";
    visibilityChange = "msvisibilitychange";
  } else if (typeof (document as any).webkitHidden !== "undefined") {
    hidden = "webkitHidden";
    visibilityChange = "webkitvisibilitychange";
  }
  function handleVisibilityChange() {
    // console.log("handleVisibilityChange.... ", document[hidden]);
    if (document[hidden]) {
      __pressed = {};
    }
  }
  if (
    typeof document.addEventListener === "undefined" ||
    hidden === undefined
  ) {
    console.warn(
      "This demo requires a browser, such as Google Chrome or Firefox, that supports the Page Visibility API."
    );
  } else {
    document.addEventListener(visibilityChange, handleVisibilityChange, false);
    // console.log("podpinamm... visibilityChange");
  }
}

// creates a subscription and returns the unsubscribe function;
export const subscribeKeys = (
  name: string,
  callback: (event: KeyboardEvent, pressed: string[]) => void
) => {
  // Subscripton names are lowercased so both 'Shift+Space' and 'shift+space' works.
  name = name.toLowerCase();
  // remove spaces so both 'Shift + Space' and 'shift+space' works.
  name.replace(/\s/, "");

  if (typeof __subscriptions[name] === "undefined") {
    __subscriptions[name] = [];
  }

  __subscriptions[name].push(callback);

  return {
    unsubscribe: () => {
      const index = __subscriptions[name].indexOf(callback);
      __subscriptions[name].splice(index, 1);
    },
  };
};

// alows to set a monitor function that will run on every event
export function setMonitor(
  enabled: boolean,
  monitorFn?: (eventName: string, matched: boolean, event: any) => void
) {
  if (enabled === false) {
    __monitor = () => {};
  } else {
    if (monitorFn !== undefined) {
      __monitor = monitorFn;
    } else {
      __monitor = defaultMonitor;
    }
  }
}

export function startListeningKeys() {
  if (__element !== undefined) {
    (__element as any).addEventListener("keydown", handleKeyEvent);
    __element.addEventListener("keyup", unhandleKeyEvent);
  }
}

export function stopListeningKeys() {
  if (__element !== undefined) {
    (__element as any).removeEventListener("keydown", handleKeyEvent);
    __element.removeEventListener("keyup", unhandleKeyEvent);
  }
}

export function setPreventDefault(newValue: boolean) {
  __preventDefaultEvents = newValue;
}

export function isPressedKey(eventName: string) {
  return __pressed[eventName] === true;
}
export function isPressedKeys(eventNames: string[]) {
  return xor(Object.keys(__pressed), eventNames).length === 0;
}

export function getKeysPressed() {
  return Object.keys(__pressed);
}

function unhandleKeyEvent(event: Event) {
  __pressed = {};
}

// listens to the event and fires the appropiate event subscription
function handleKeyEvent(event: KeyboardEvent) {
  if (__preventDefaultEvents === true) {
    event.preventDefault();
  }

  //   console.log("handleKeyEventhandleKeyEvent", 1);
  //   if (isEventModifier(event)) {
  //     return;
  //   }
  //   console.log("handleKeyEventhandleKeyEvent", 2);

  //   if (__ignoreInputEvents && isInputEvent(event)) {
  //     return;
  //   }

  const eventName = getKeyEventName(event);
  const listeners = __subscriptions[eventName.toLowerCase()] || [];

  if (typeof __monitor === "function") {
    const matched = listeners.length > 0;
    __monitor(eventName, matched, event);
  }
  __pressed[eventName.toLowerCase()] = true;

  if (listeners.length > 0) {
    event.preventDefault();
  }

  let propagate = true;

  for (let i = listeners.length - 1; i >= 0; i--) {
    if (listeners[i]) {
      propagate = listeners[i](event, Object.keys(__pressed));
    }
    if (propagate === false) {
      break;
    }
  }
}
