import produce from "immer";
import { ChangeUiSettingsZoom } from "../AppEventType";
import { refreshReactState } from "../refreshReactState";
import { setStore } from "../store";
import { BaseEventHandler } from "../BaseHandlers/BaseEventHandler";
import { getRect10x10FromScreenToWorld } from "../../threejs/mouseListeners/getRect10x10FromScreenToWorld";
import { appThree } from "../../threejs/renderThreejs";

export class ChangeUiSettingsZoomHandler extends BaseEventHandler<ChangeUiSettingsZoom> {
  run() {
    const { zoomType } = this.event.payload;
    const screenCenter = this.getCenterPosition();

    if (zoomType === "zoomByScroll") {
      let { scrollDelta, clientX, clientY } = this.event.payload;
      if (!clientX) {
        clientX = screenCenter.posX;
      }
      if (!clientY) {
        clientY = screenCenter.posY;
      }
      if (!scrollDelta) {
        scrollDelta = 1;
      }
      const { top, left } = appThree.el.getBoundingClientRect();
      const screenX = clientX - left;
      const screenY = clientY - top;
      const scrollSign = Math.sign(scrollDelta);
      this.updateByScroll(screenX, screenY, scrollSign);
    } else if (zoomType === "zoomInScreenCenter") {
      this.updateByScroll(screenCenter.posX, screenCenter.posY, -1);
    } else if (zoomType === "zoomOutScreenCenter") {
      this.updateByScroll(screenCenter.posX, screenCenter.posY, 1);
    }
  }

  updateByScroll(screenX: number, screenY: number, scrollSign: number) {
    const { width, height } = appThree.el.getBoundingClientRect();
    const cameraX = appThree.camera.position.x;
    const cameraY = appThree.camera.position.y;

    const mouseXInWorldBefore = (screenX - width / 2) * this.store.ui.zoomScale;
    const mouseYInWorldBefore =
      -1 * (screenY - height / 2) * this.store.ui.zoomScale;

    const zoomChange = this.store.ui.zoomScale >= 0.35 ? 0.02 * 5 : 0.01;
    const zoomMax = 100;
    const zoomMin = 0.0125;
    let sceneScale = 1;
    sceneScale = this.store.ui.zoomScale + scrollSign * zoomChange;
    sceneScale = Math.min(Math.max(zoomMin, sceneScale), zoomMax);

    const mouseXInWorldAfter = (screenX - width / 2) * sceneScale;
    const mouseYInWorldAfter = -1 * (screenY - height / 2) * sceneScale;

    appThree.camera.left = (width * sceneScale) / -2;
    appThree.camera.right = (width * sceneScale) / 2;
    appThree.camera.top = (height * sceneScale) / 2;
    appThree.camera.bottom = (height * sceneScale) / -2;
    appThree.camera.updateProjectionMatrix();

    const diffX = mouseXInWorldBefore - mouseXInWorldAfter;
    const diffY = mouseYInWorldBefore - mouseYInWorldAfter;

    appThree.camera.position.x = cameraX + diffX;
    appThree.camera.position.y = cameraY + diffY;
    appThree.camera.lookAt(
      appThree.camera.position.x,
      appThree.camera.position.y,
      0
    );

    const rect10x10FromScreenToWorld = getRect10x10FromScreenToWorld();
    appThree.camera.userData = { sceneScale, rect10x10FromScreenToWorld };

    let newStore = produce(this.store, (draft) => {
      draft.ui.zoomScale = sceneScale;
    });
    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);

    appThree.scene.traverse((child) => {
      if (child.layers.mask === 4) {
        child.scale.x = sceneScale;
        child.scale.y = sceneScale;
        child.scale.z = sceneScale;
      }
    });
  }
}
