import { times } from "lodash";
import {
  AddObjects,
  RemoveObjects,
  ChangeSelectObjects,
  ChangeObjectsProperty,
  ClipboardCut,
  ClipboardPaste,
  AddMultipleBeads,
  ChangeSelectedObjectsAlignment,
  DuplicateObjects,
  GroupObjects,
  UngroupObjects,
} from "./AppEventType";
import { SimpleHistoryEvent } from "./store";
import { sendEvent } from "./storeController";

export interface HistoryEvent<D, R, U> {
  do: D;
  redo: R;
  undo: U;
  createdAt: string;
}
export interface AddObjectsHistory
  extends HistoryEvent<AddObjects, AddObjects, RemoveObjects> {}

export interface DuplicateObjectsHistory
  extends HistoryEvent<DuplicateObjects, DuplicateObjects, RemoveObjects> {}

export interface RemoveObjectsHistory
  extends HistoryEvent<RemoveObjects, RemoveObjects, AddObjects> {}
export interface ChangeSelectObjectsHistory
  extends HistoryEvent<
    ChangeSelectObjects,
    ChangeSelectObjects,
    ChangeSelectObjects
  > {}
export interface ChangeObjectsPropertyHistory
  extends HistoryEvent<
    ChangeObjectsProperty,
    ChangeObjectsProperty,
    ChangeObjectsProperty
  > {}
export interface ClipboardCutHistory
  extends HistoryEvent<ClipboardCut, ClipboardCut, ClipboardPaste> {}
export interface ClipboardPasteHistory
  extends HistoryEvent<ClipboardPaste, ClipboardPaste, RemoveObjects> {}
export interface AddMultipleBeadsHistory
  extends HistoryEvent<AddMultipleBeads, AddObjects, RemoveObjects> {}
export interface ChangeSelectedObjectsAlignmentHistory
  extends HistoryEvent<
    ChangeSelectedObjectsAlignment,
    ChangeSelectedObjectsAlignment,
    ChangeObjectsProperty
  > {}
export interface GroupObjectsHistory
  extends HistoryEvent<GroupObjects, GroupObjects, UngroupObjects> {}
export interface UngroupObjectsHistory
  extends HistoryEvent<UngroupObjects, UngroupObjects, GroupObjects> {}

export type AppHistoryEvents =
  | AddObjectsHistory
  | RemoveObjectsHistory
  | ChangeSelectObjectsHistory
  | ChangeObjectsPropertyHistory
  | ClipboardCutHistory
  | ClipboardPasteHistory
  | AddMultipleBeadsHistory
  | ChangeSelectedObjectsAlignmentHistory
  | DuplicateObjectsHistory
  | GroupObjectsHistory
  | UngroupObjectsHistory;

export class AppHistory {
  events: AppHistoryEvents[] = [];
  index: number = 0;
  maxHistoryRecords: number = 100;
  push(arg: AppHistoryEvents): void {
    const len = this.events.length;
    if (len !== this.index) {
      this.events.splice(this.index, len - this.index);
    }
    this.events.push(arg);
    this.index += 1;
    if (this.events.length > this.maxHistoryRecords) {
      const diff = this.events.length - this.maxHistoryRecords;
      this.events.splice(0, diff);
      this.index = this.index - diff;
    }
  }
  front(): AppHistoryEvents | undefined {
    const event = this.events[this.index];
    this.index += 1;
    if (this.index > this.events.length) {
      this.index = this.events.length;
    }
    return event;
  }
  back(): AppHistoryEvents | undefined {
    const event = this.events[this.index - 1];
    this.index -= 1;
    if (this.index <= 0) {
      this.index = 0;
    }
    return event;
  }
  getSimpleHistoryEvents() {
    return this.events.map((e, i) => {
      return {
        name: e.do.name,
        isActive: i < this.index,
        createdAt: e.createdAt,
      } as SimpleHistoryEvent;
    });
  }
  canRedo() {
    return this.index !== this.events.length;
  }
  canUndo() {
    return this.index > 0;
  }
}

export const undoHistory = () => {
  const history = getHistory();
  const lastEvent = history.back();
  if (lastEvent !== undefined) {
    sendEvent(lastEvent.undo, false);
  }
};

export const redoHistory = () => {
  const history = getHistory();
  const lastEvent = history.front();
  if (lastEvent !== undefined) {
    sendEvent(lastEvent.redo, false);
  }
};

export const jumpToIndexHistory = (index: number) => {
  const history = getHistory();
  const lastIndex = history.index - 1;

  if (index === lastIndex) {
    // do nothing
  } else if (index >= 0 && index < lastIndex) {
    times(lastIndex - index).forEach(() => {
      undoHistory();
    });
  } else if (index < history.events.length && index > lastIndex) {
    times(index - lastIndex).forEach(() => {
      redoHistory();
    });
  }
};

let history: AppHistory;

export const initHistory = () => {
  history = new AppHistory();
};

export const getHistory = () => {
  return history;
};
