import produce from "immer";
import { cloneDeep } from "lodash";
import {
  addObjectByObjectType,
  triggerDeleteObjectIds,
  updateObjectPosition,
} from "../../threejs/eventEmitterHandlers";
import {
  ChangeObjectsProperty,
  AppEventType,
  ChangeObject,
} from "../AppEventType";
import { ChangeObjectsPropertyHistory } from "../AppHistory";
import { refreshReactState } from "../refreshReactState";
import {
  setStore,
  shouldRefreshReact,
  shouldRefreshThree,
  Store,
} from "../store";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import {
  AppObject,
  Bead,
  Group,
  groupIdKey,
  ObjectTypes,
  Point,
  Region,
  RegionGridType,
  RegionShape,
} from "../../editorObjects/Editor";
import { notifyBackend, SaveEventType } from "../../ApiService";
import { WritableDraft } from "immer/dist/internal";
import { buildBead } from "../../editorObjects/Bead";
import { contain } from "../../editorObjects/Point";
import { isActiveRegionCell } from "../../editorObjects/Region";
import { logger } from "../../logger";
import { getSelectBBox } from "../../threejs/SelectBBox";
import { formatISO } from "date-fns";

type ObjectsMap = { [key: string]: AppObject };

export class ChangeObjectsPropertyHandler extends StoreObjectHandler<ChangeObjectsProperty> {
  run() {
    const added: AppObject[] = [];
    const updated: ObjectsMap = {};
    const removed: AppObject[] = [];
    const { changes } = this.event.payload;
    const historyChanges: ChangeObjectsProperty["payload"]["changes"] = [];
    let newStore = produce(this.store, (draft) => {
      changes.forEach((_singleChange) => {
        if (draft.objects[_singleChange.id] === undefined) {
          logger.warn("no object found in store", _singleChange.id);
          return;
        }
        const singleHistoryChange: ChangeObject = {
          id: _singleChange.id,
          properties: {},
          relatedChanges: [],
          shouldBeCreated: [],
          shouldBeDeleted: [],
        };
        const keyNames = Object.keys(_singleChange.properties);
        if (singleHistoryChange !== undefined) {
          keyNames.forEach((key) => {
            singleHistoryChange.properties[key] = cloneDeep(
              draft.objects[_singleChange.id][key]
            );
          });
        }
        keyNames.forEach((key) => {
          draft.objects[_singleChange.id][key] = cloneDeep(
            _singleChange.properties[key]
          );
        });

        if (_singleChange.relatedChanges) {
          _singleChange.relatedChanges.forEach((_singeRelatedChange) => {
            if (draft.objects[_singeRelatedChange.id] === undefined) {
              logger.warn("no object found in store", _singeRelatedChange.id);
              return;
            }
            const keyNames = Object.keys(_singeRelatedChange.properties);
            keyNames.forEach((key) => {
              draft.objects[_singeRelatedChange.id][key] = cloneDeep(
                _singeRelatedChange.properties[key]
              );
            });
          });
        }

        historyChanges.push(singleHistoryChange);
      });

      changes.forEach((_singleChange, index) => {
        if (draft.objects[_singleChange.id] === undefined) {
          logger.warn("no object found in store", _singleChange.id);
          return;
        }
        const objectType = draft.objects[_singleChange.id].objectType;
        const singleHistoryChange = historyChanges[index];

        if (objectType === ObjectTypes.Bead) {
          this.whenBead(
            _singleChange,
            draft,
            singleHistoryChange,
            added,
            updated,
            removed
          );
        }

        if (objectType === ObjectTypes.Region) {
          this.whenRegion(
            _singleChange,
            draft,
            singleHistoryChange,
            added,
            updated,
            removed
          );
        }
      });
    });

    if (this.saveToHistory) {
      const historyEvent: ChangeObjectsPropertyHistory = {
        do: this.event,
        redo: {
          name: AppEventType.ChangeObjectsProperty,
          payload: {
            changes: this.event.payload.changes.map(
              ({ id, properties, relatedChanges }) => {
                const index = historyChanges.findIndex((e) => e.id === id);
                const exists = index !== -1;
                const shouldBeCreated = exists
                  ? historyChanges[index].shouldBeDeleted
                  : [];
                const shouldBeDeleted = exists
                  ? historyChanges[index].shouldBeCreated
                  : [];

                return {
                  id,
                  properties,
                  relatedChanges,
                  shouldBeCreated,
                  shouldBeDeleted,
                };
              }
            ),
          },
        },
        undo: {
          name: AppEventType.ChangeObjectsProperty,
          payload: { changes: historyChanges },
        },
        createdAt: formatISO(new Date()),
      };
      this.history.push(historyEvent);
    }

    newStore = this.getStoreWithHistory(newStore);

    if (shouldRefreshThree()) {
      // @INFO: CHANGES IN THREEJS
      changes.forEach((_singleChange) => {
        this.applyChangesToThree(_singleChange, newStore);
        notifyBackend([
          {
            action: SaveEventType.UPDATE,
            createdAt: new Date().toISOString(),
            object: newStore.objects[_singleChange.id],
          },
        ]);

        if (_singleChange.relatedChanges) {
          _singleChange.relatedChanges.forEach((_singeRelatedChange) => {
            this.applyChangesToThree(_singeRelatedChange, newStore);
            notifyBackend([
              {
                action: SaveEventType.UPDATE,
                createdAt: new Date().toISOString(),
                object: newStore.objects[_singeRelatedChange.id],
              },
            ]);
          });
        }
      });
    }

    if (shouldRefreshThree()) {
      if (added.length > 0) {
        added.forEach((obj) => {
          addObjectByObjectType(obj);
        });

        notifyBackend(
          added.map((object) => {
            return {
              action: SaveEventType.CREATE,
              createdAt: new Date().toISOString(),
              object,
            };
          })
        );
      }

      const updatedKeys = Object.keys(updated);
      if (updatedKeys.length > 0) {
        triggerDeleteObjectIds(
          updatedKeys.map((id) => {
            return {
              id,
              objectType: newStore.objects[id].objectType,
            };
          })
        );

        updatedKeys.forEach((id) => {
          addObjectByObjectType(newStore.objects[id]);
        });

        notifyBackend(
          updatedKeys.map((id) => {
            return {
              action: SaveEventType.UPDATE,
              createdAt: new Date().toISOString(),
              object: newStore.objects[id],
            };
          })
        );
      }

      if (removed.length > 0) {
        triggerDeleteObjectIds(
          removed.map((obj) => {
            return { id: obj.id, objectType: obj.objectType };
          })
        );
        notifyBackend(
          removed.map((object) => {
            return {
              action: SaveEventType.DELETE,
              createdAt: new Date().toISOString(),
              object,
            };
          })
        );
      }
    }

    if (shouldRefreshThree()) {
      newStore = this.getNewStoreWithTopBarPosition(newStore);
    }

    setStore(newStore);

    if (shouldRefreshReact()) {
      refreshReactState(newStore);
    }

    if (shouldRefreshThree()) {
      const selectBBox = getSelectBBox();
      selectBBox.syncRectsWithStore();
    }
  }

  applyChangesToThree(_singleChange: ChangeObject, _store: Store) {
    if (_store.objects[_singleChange.id] === undefined) {
      logger.warn("no object found in store", _singleChange.id);
      return;
    }
    const object = _store.objects[_singleChange.id];
    const inGroup =
      object[groupIdKey] !== undefined && object[groupIdKey].length > 0;

    const positionKey: keyof AppObject = "position";
    // const objectType = _store.objects[_singleChange.id].objectType;
    const keyNames = Object.keys(_singleChange.properties);
    const isPositionOnly =
      keyNames.length === 1 && keyNames.includes(positionKey) === true;
    if (isPositionOnly && inGroup === false) {
      updateObjectPosition(
        _singleChange.id,
        object.objectType,
        object.position.x,
        object.position.y
      );
    }
    // else if (objectType === ObjectTypes.Region) {
    // const isShapeOnly =
    // keyNames.length === 1 && keyNames.includes("shape") === true;
    // }
    else {
      triggerDeleteObjectIds([
        {
          id: _singleChange.id,
          objectType: object.objectType,
        },
      ]);

      addObjectByObjectType(object);

      if (inGroup) {
        if (
          object[groupIdKey] !== undefined &&
          _store.objects[object[groupIdKey]]
        ) {
          (_store.objects[object[groupIdKey]] as Group).childrenIds.forEach(
            (id) => {
              const obj = _store.objects[id];
              triggerDeleteObjectIds([
                {
                  id: id,
                  objectType: obj.objectType,
                },
              ]);
              addObjectByObjectType(obj);
            }
          );
        }
      }
    }
  }

  whenBead(
    _singleChange: ChangeObject,
    draft: WritableDraft<Store>,
    singleHistoryChange: ChangeObject,
    added: AppObject[],
    updated: ObjectsMap,
    removed: AppObject[]
  ) {
    const positionKey: keyof AppObject = "position";
    const regionIdKey: keyof Bead = "regionId";
    const childrenGridIdsKey: keyof Region = "childrenGridIds";
    const keyNames = Object.keys(_singleChange.properties);

    if (keyNames.includes(positionKey)) {
      let isBeadFoundRegion = false;
      const bead = draft.objects[_singleChange.id] as Bead;
      const regionId = (draft.objects[_singleChange.id] as Bead).regionId;

      if (regionId !== undefined) {
        const region = draft.objects[regionId] as Region;
        if (this.isRegionContainBead(region, bead) === true) {
          const { column, row, index: newIndex } = this.getRegionCellIndex(
            bead.position.x,
            bead.position.y,
            region
          );
          // if (column < 0) {
          //   console.warn("whenBead :: column < 0", column, row, bead.id);
          // }
          const isActiveCell = isActiveRegionCell(region, newIndex);
          if (isActiveCell === true) {
            const oldIndex = region.childrenGridIds.indexOf(bead.id);
            if (oldIndex !== -1 && oldIndex !== newIndex) {
              draft.objects[regionId][childrenGridIdsKey][oldIndex] = undefined;
            }
            if (
              draft.objects[regionId][childrenGridIdsKey][newIndex] !==
              undefined
            ) {
              draft.objects[regionId][childrenGridIdsKey][newIndex] = bead.id;
              isBeadFoundRegion = true;
              if (region.snapBeadsToCell === true) {
                const position = this.getRegionCellCenter(region, row, column);
                draft.objects[bead.id].position.x = position.x;
                draft.objects[bead.id].position.y = position.y;
              }
            }
          }
        }
      }

      if (isBeadFoundRegion === false) {
        Object.keys(draft.objects).forEach((regionId) => {
          if (draft.objects[regionId].objectType !== ObjectTypes.Region) {
            return;
          }
          const region = draft.objects[regionId] as Region;

          if (this.isRegionContainBead(region, bead) === false) {
            return;
          }
          const { column, row, index: newIndex } = this.getRegionCellIndex(
            bead.position.x,
            bead.position.y,
            region
          );

          // if (column < 0) {
          //   console.warn("whenBead :: column < 0", column, row, bead.id);
          // }
          const isActiveCell = isActiveRegionCell(region, newIndex);
          if (isActiveCell === true) {
            if (region.childrenGridIds[newIndex] === undefined) {
              const currentIndex = region.childrenGridIds.indexOf(bead.id);
              if (currentIndex !== -1 && currentIndex !== newIndex) {
                draft.objects[regionId][childrenGridIdsKey][
                  currentIndex
                ] = undefined;
              }
              draft.objects[regionId][childrenGridIdsKey][newIndex] = bead.id;

              updated[regionId] = cloneDeep(draft.objects[regionId]);

              if (draft.objects[bead.id][regionIdKey] !== undefined) {
                const oldRegionId = draft.objects[bead.id][regionIdKey];
                const oldRegion = draft.objects[oldRegionId] as Region;
                const currentIndex = oldRegion.childrenGridIds.indexOf(bead.id);
                if (currentIndex !== -1) {
                  draft.objects[oldRegionId][childrenGridIdsKey][
                    currentIndex
                  ] = undefined;
                  updated[oldRegionId] = cloneDeep(draft.objects[oldRegionId]);
                }
              }

              draft.objects[bead.id][regionIdKey] = region.id;
              isBeadFoundRegion = true;

              if (region.snapBeadsToCell === true) {
                const position = this.getRegionCellCenter(region, row, column);
                draft.objects[bead.id].position.x = position.x;
                draft.objects[bead.id].position.y = position.y;
              }
            } else if (region.childrenGridIds[newIndex] === bead.id) {
              if (region.snapBeadsToCell === true) {
                const position = this.getRegionCellCenter(region, row, column);
                draft.objects[bead.id].position.x = position.x;
                draft.objects[bead.id].position.y = position.y;
                isBeadFoundRegion = true;
              }
            }
          }
        });
      }

      if (isBeadFoundRegion === false) {
        if (regionId !== undefined) {
          const region = draft.objects[regionId] as Region;
          const currentIndex = region.childrenGridIds.indexOf(bead.id);
          if (currentIndex !== -1) {
            draft.objects[regionId][childrenGridIdsKey][
              currentIndex
            ] = undefined;

            updated[regionId] = cloneDeep(draft.objects[regionId]);
          }

          (draft.objects[_singleChange.id] as Bead).regionId = undefined;
        }
      }
    }
  }

  whenRegion(
    _singleChange: ChangeObject,
    draft: WritableDraft<Store>,
    singleHistoryChange: ChangeObject,
    added: AppObject[],
    updated: ObjectsMap,
    removed: AppObject[]
  ) {
    const keyNames = Object.keys(_singleChange.properties);
    const shapeKey: keyof Region = "shape";
    const gridTypeKey: keyof Region = "gridType";
    const autoFillBeadsKey: keyof Region = "autoFillBeads";
    const snapBeadsToCellKey: keyof Region = "snapBeadsToCell";
    const rowsKey: keyof Region = "rows";
    const columnsKey: keyof Region = "columns";
    const positionKey: keyof Region = "position";

    const { shouldBeDeleted } = _singleChange;

    if (shouldBeDeleted !== undefined && shouldBeDeleted.length > 0) {
      const region = draft.objects[_singleChange.id] as Region;
      const tempIds: string[] = [];

      shouldBeDeleted.forEach((bead) => {
        if (draft.objects[bead.id] === undefined) {
          logger.warn("whenRegion:: bead not found", bead.id);
          return;
        }

        tempIds.push(bead.id);
        removed.push(cloneDeep(bead));
        delete draft.objects[bead.id];
      });

      region.childrenGridIds.forEach((id, index) => {
        if (id !== undefined && tempIds.includes(id)) {
          (draft.objects[region.id] as Region).childrenGridIds[
            index
          ] = undefined;
        }
      });
    }

    if (
      keyNames.includes(rowsKey) ||
      keyNames.includes(columnsKey) ||
      keyNames.includes(shapeKey) ||
      keyNames.includes(gridTypeKey)
    ) {
      const region = draft.objects[_singleChange.id] as Region;
      const oldRows = singleHistoryChange.properties[rowsKey];
      const oldColumns = singleHistoryChange.properties[columnsKey];
      const oldShape = singleHistoryChange.properties[shapeKey];
      const oldGridType = singleHistoryChange.properties[gridTypeKey];
      const autoFillBeadsEnabled = region[autoFillBeadsKey] === true;
      if (oldRows === undefined || oldColumns === undefined) {
        logger.warn("missing keys: rowsKey && columnsKey");
        (draft.objects[region.id] as Region).childrenGridIds.forEach(
          (beadId) => {
            if (beadId === undefined) {
              return;
            }
            const bead = draft.objects[beadId] as Bead;
            if (bead === undefined) {
              logger.warn("toDeleteBeadIds:: bead not found", beadId);
              return;
            }

            singleHistoryChange.shouldBeCreated!.push(cloneDeep(bead));
            removed.push(cloneDeep(bead));
            delete draft.objects[beadId];
          }
        );
        const newSize = region.rows * region.columns;
        (draft.objects[region.id] as Region).childrenGridIds = Array(
          newSize
        ).fill(undefined);
      } else if (
        oldShape !== region.shape ||
        oldRows !== region.rows ||
        oldColumns !== region.columns ||
        oldGridType !== region.gridType
      ) {
        const {
          toDeleteBeadIds,
          newChildrenGridIds,
        } = this.getRegionChildrenGridIdsAfterResize(
          region,
          autoFillBeadsEnabled,
          oldRows,
          oldColumns
        );

        toDeleteBeadIds.forEach((beadId) => {
          const bead = draft.objects[beadId] as Bead;
          if (bead === undefined) {
            logger.warn("toDeleteBeadIds:: bead not found", beadId);
            return;
          }

          singleHistoryChange.shouldBeCreated!.push(cloneDeep(bead));
          removed.push(cloneDeep(bead));
          delete draft.objects[beadId];
        });

        (draft.objects[region.id] as Region).childrenGridIds = cloneDeep(
          newChildrenGridIds
        );
      }
    }

    if (
      keyNames.includes(positionKey) ||
      keyNames.includes(snapBeadsToCellKey)
    ) {
      const region = draft.objects[_singleChange.id] as Region;
      const snapBeadsToCellEnabled = region[snapBeadsToCellKey] === true;

      if (snapBeadsToCellEnabled) {
        region.childrenGridIds.forEach((id, index) => {
          if (id !== undefined) {
            const bead = draft.objects[id] as Bead;
            if (bead !== undefined) {
              const oldPosition = cloneDeep(bead.position);
              const { row, column } = this.getRegionCellIndex(
                bead.position.x,
                bead.position.y,
                region
              );
              const { x, y } = this.getRegionCellCenter(region, row, column);
              draft.objects[id].position.x = x;
              draft.objects[id].position.y = y;
              updated[id] = cloneDeep(draft.objects[id]);
              singleHistoryChange.relatedChanges?.push({
                id,
                properties: {
                  position: {
                    x: oldPosition.x,
                    y: oldPosition.y,
                    z: oldPosition.z,
                  },
                },
              });
            }
          } else {
            const region = draft.objects[_singleChange.id] as Region;
            let foundObject = false;
            Object.keys(draft.objects).forEach((id) => {
              if (foundObject === true) {
                return;
              }
              if (draft.objects[id].objectType !== ObjectTypes.Bead) {
                return;
              }

              const bead = draft.objects[id] as Bead;

              if (bead.groupId !== undefined) {
                return;
              }

              if (bead.regionId !== undefined) {
                return;
              }

              const isActiveCell = isActiveRegionCell(region, index);
              if (isActiveCell === false) {
                return;
              }

              if (this.isRegionCellContainBead(region, bead, index) === false) {
                return;
              }

              const oldPosition = cloneDeep(bead.position);
              const { row, column } = this.getRegionCellIndex(
                bead.position.x,
                bead.position.y,
                region
              );
              const { x, y } = this.getRegionCellCenter(region, row, column);

              (draft.objects[_singleChange.id] as Region).childrenGridIds[
                index
              ] = bead.id;

              (draft.objects[bead.id] as Bead).regionId = region.id;

              draft.objects[bead.id].position.x = x;
              draft.objects[bead.id].position.y = y;

              singleHistoryChange.relatedChanges?.push({
                id: bead.id,
                properties: {
                  position: {
                    x: oldPosition.x,
                    y: oldPosition.y,
                    z: oldPosition.z,
                  },
                  regionId: undefined,
                },
              });

              updated[bead.id] = cloneDeep(draft.objects[bead.id]);
              foundObject = true;
            });
          }
        });
      } else {
        const region = draft.objects[_singleChange.id] as Region;
        const size = region.rows * region.columns;
        const newChildrenGridIds = Array(size).fill(undefined);

        let emptyCellsCount = size;
        const keys = Object.keys(draft.objects);
        for (let i = 0; i < keys.length; i++) {
          if (emptyCellsCount <= 0) {
            break;
          }

          const id = keys[i];

          if (draft.objects[id].objectType !== ObjectTypes.Bead) {
            continue;
          }

          const bead = draft.objects[id] as Bead;

          if (bead.groupId !== undefined) {
            continue;
          }

          if (this.isRegionContainBead(region, bead) === false) {
            continue;
          }

          const { index } = this.getRegionCellIndex(
            bead.position.x,
            bead.position.y,
            region
          );

          if (index >= 0 && newChildrenGridIds[index] === undefined) {
            newChildrenGridIds[index] = bead.id;
            emptyCellsCount = emptyCellsCount - 1;
          }
        }

        // obecne beadsy na regionie... ktory bead wypadł z regionu?
        (draft.objects[_singleChange.id] as Region).childrenGridIds.forEach(
          (beadId) => {
            if (beadId !== undefined && draft.objects[beadId] !== undefined) {
              if (newChildrenGridIds.includes(beadId) === false) {
                singleHistoryChange.relatedChanges?.push({
                  id: beadId,
                  properties: {
                    regionId: (draft.objects[beadId] as Bead).regionId,
                  },
                });
              }

              (draft.objects[beadId] as Bead).regionId = undefined;

              updated[beadId] = cloneDeep(draft.objects[beadId]);
            }
          }
        );

        // tworzymy relacje bead->region dla nowo wybranych beads
        newChildrenGridIds.forEach((beadId) => {
          if (beadId !== undefined) {
            if ((draft.objects[beadId] as Bead).regionId === undefined) {
              singleHistoryChange.relatedChanges?.push({
                id: beadId,
                properties: {
                  regionId: undefined,
                },
              });
            } else {
              singleHistoryChange.relatedChanges?.push({
                id: beadId,
                properties: {
                  regionId: (draft.objects[beadId] as Bead).regionId,
                },
              });
            }
            (draft.objects[beadId] as Bead).regionId = region.id;

            updated[beadId] = cloneDeep(draft.objects[beadId]);
          }
        });

        (draft.objects[_singleChange.id] as Region).childrenGridIds = cloneDeep(
          newChildrenGridIds
        );
      }
    }

    if (
      keyNames.includes(autoFillBeadsKey) ||
      keyNames.includes(rowsKey) ||
      keyNames.includes(columnsKey) ||
      keyNames.includes(positionKey) ||
      keyNames.includes(gridTypeKey)
    ) {
      const region = draft.objects[_singleChange.id] as Region;
      const autoFillBeadsEnabled = region[autoFillBeadsKey] === true;
      const snapBeadsToCellEnabled = region[snapBeadsToCellKey] === true;
      const size = region.rows * region.columns;
      if (region.childrenGridIds.length !== size) {
        logger.warn(
          "region childrenIds size have not correct value",
          size,
          region.childrenGridIds.length,
          region.childrenGridIds
        );
        return;
      }

      if (autoFillBeadsEnabled) {
        let nextIdIndexFromCreated = { value: 0 };
        let nextIdIndexFromDeleted = 0;
        region.childrenGridIds.forEach((beadId, index) => {
          // @TODO: obecnie nie jest uwzgledniony rotate dla regionu.

          const isActiveCell = isActiveRegionCell(region, index);

          if (isActiveCell === false) {
            return;
          }

          if (beadId === undefined) {
            const { x, y } = this.getRegionCellCenterFromCellIndex(
              region,
              index
            );
            const id = this.getIdFromUndoOrRedo(
              _singleChange,
              nextIdIndexFromCreated,
              nextIdIndexFromDeleted
            );
            const bead = buildBead({
              id: id,
              color: "red",
              regionId: region.id,
            });
            bead.position.x = x;
            bead.position.y = y;
            draft.objects[bead.id] = bead;
            (draft.objects[region.id] as Region).childrenGridIds[index] =
              bead.id;
            added.push(cloneDeep(bead));
            singleHistoryChange.shouldBeDeleted!.push(cloneDeep(bead));
          } else {
            const bead = draft.objects[beadId] as Bead;
            if (bead !== undefined) {
              const oldPosition = cloneDeep(bead.position);
              const { x, y } = this.getRegionCellCenterFromCellIndex(
                region,
                index
              );
              draft.objects[beadId].position.x = x;
              draft.objects[beadId].position.y = y;
              updated[beadId] = cloneDeep(draft.objects[beadId]);
              singleHistoryChange.relatedChanges?.push({
                id: beadId,
                properties: {
                  position: {
                    x: oldPosition.x,
                    y: oldPosition.y,
                    z: oldPosition.z,
                  },
                },
              });
            }
          }
        });
      } else {
        if (snapBeadsToCellEnabled) {
          region.childrenGridIds.forEach((beadId, index) => {
            const isActiveCell = isActiveRegionCell(region, index);

            if (isActiveCell === false) {
              return;
            }

            if (beadId !== undefined) {
              const bead = draft.objects[beadId] as Bead;
              const oldPosition = cloneDeep(bead.position);
              const { x, y } = this.getRegionCellCenterFromCellIndex(
                region,
                index
              );

              draft.objects[beadId].position.x = x;
              draft.objects[beadId].position.y = y;

              updated[beadId] = cloneDeep(draft.objects[beadId]);

              singleHistoryChange.relatedChanges?.push({
                id: beadId,
                properties: {
                  position: {
                    x: oldPosition.x,
                    y: oldPosition.y,
                    z: oldPosition.z,
                  },
                },
              });
            } else {
              const region = draft.objects[_singleChange.id] as Region;

              let foundObject = false;
              Object.keys(draft.objects).forEach((id) => {
                if (foundObject === true) {
                  return;
                }
                if (draft.objects[id].objectType !== ObjectTypes.Bead) {
                  return;
                }

                const bead = draft.objects[id] as Bead;

                if (bead.groupId !== undefined) {
                  return;
                }

                if (bead.regionId !== undefined) {
                  return;
                }

                if (
                  this.isRegionCellContainBead(region, bead, index) === false
                ) {
                  return;
                }
                const { x, y } = this.getRegionCellCenterFromCellIndex(
                  region,
                  index
                );

                (draft.objects[_singleChange.id] as Region).childrenGridIds[
                  index
                ] = bead.id;

                (draft.objects[bead.id] as Bead).regionId = region.id;

                const oldPosition = cloneDeep(bead.position);

                draft.objects[bead.id].position.x = x;
                draft.objects[bead.id].position.y = y;

                singleHistoryChange.relatedChanges?.push({
                  id: bead.id,
                  properties: {
                    position: {
                      x: oldPosition.x,
                      y: oldPosition.y,
                      z: oldPosition.z,
                    },
                    regionId: undefined,
                  },
                });

                updated[bead.id] = cloneDeep(draft.objects[bead.id]);
                foundObject = true;
              });
            }
          });
        }
      }
    }
  }

  getIdFromUndoOrRedo(
    _singleChange: ChangeObject,
    nextIdIndexFromCreated: { value: number },
    nextIdIndexFromDeleted: number
  ) {
    let id: string | undefined = undefined;

    const { shouldBeCreated } = _singleChange;

    if (
      shouldBeCreated !== undefined &&
      shouldBeCreated[nextIdIndexFromCreated.value]?.id !== undefined
    ) {
      id = shouldBeCreated[nextIdIndexFromCreated.value]?.id ?? undefined;
      nextIdIndexFromCreated.value = nextIdIndexFromCreated.value + 1;
    }
    return id;
  }

  getRegionChildrenGridIdsAfterResize(
    region: Region,
    autoFillBeadsEnabled: boolean,
    oldRows: number,
    oldColumns: number
  ): { toDeleteBeadIds: any; newChildrenGridIds: any } {
    let newChildrenGridIds: (string | undefined)[] = [];
    let toDeleteBeadIds: string[] = [];

    if (region.shape === RegionShape.Rectangle) {
      const newRows = region.rows;
      const newColumns = region.columns;
      const newSize = region.rows * region.columns;

      newChildrenGridIds = Array(newSize).fill(undefined);
      toDeleteBeadIds = [];

      for (let i = 0; i < region.childrenGridIds.length; i++) {
        const oldIndex = i;
        const oldCurrentRow = Math.floor(i / oldColumns);
        const oldCurrentCol = Math.floor(i % oldColumns);

        let newIndex: number = oldCurrentCol + oldCurrentRow * newColumns;
        const newIndexIsActive = isActiveRegionCell(region, newIndex);

        if (
          newIndexIsActive === false ||
          oldCurrentCol >= newColumns ||
          oldCurrentRow >= newRows
        ) {
          newIndex = newSize;

          if (autoFillBeadsEnabled) {
            const beadId = region.childrenGridIds[oldIndex];
            if (beadId !== undefined) {
              if (toDeleteBeadIds.includes(beadId) === false) {
                toDeleteBeadIds.push(beadId);
              }
            }
          }
        }

        if (newIndex < newSize && newIndexIsActive === true) {
          newChildrenGridIds[newIndex] = region.childrenGridIds[oldIndex];
        }
      }
    } else if (region.shape === RegionShape.Triangle) {
      const newColumns = region.columns;
      const newSize = region.rows * region.columns;

      newChildrenGridIds = Array(newSize).fill(undefined);
      toDeleteBeadIds = [];

      for (let i = 0; i < region.childrenGridIds.length; i++) {
        const oldCurrentRow = Math.floor(i / oldColumns);
        const oldCurrentCol = Math.floor(i % oldColumns);
        const oldMiddle = oldColumns / 2;
        const newMiddle = newColumns / 2;
        const diffMiddle =
          newMiddle - oldMiddle < 0
            ? Math.ceil(newMiddle - oldMiddle)
            : Math.floor(newMiddle - oldMiddle);

        const newColumn = oldCurrentCol + diffMiddle;
        const newRow = oldCurrentRow;
        const newIndex = newRow * newColumns + newColumn;
        const oldIndex = i;

        const newIndexInTriangle =
          isActiveRegionCell(region, newIndex) &&
          newColumn >= 0 &&
          newColumn < newColumns &&
          newRow < region.rows;

        if (newIndexInTriangle === false) {
          if (autoFillBeadsEnabled) {
            const beadId = region.childrenGridIds[oldIndex];
            if (beadId !== undefined) {
              if (toDeleteBeadIds.includes(beadId) === false) {
                toDeleteBeadIds.push(beadId);
              }
            }
          }
        } else {
          if (newChildrenGridIds[newIndex] === undefined) {
            newChildrenGridIds[newIndex] = region.childrenGridIds[oldIndex];
          } else {
            if (autoFillBeadsEnabled) {
              if (
                toDeleteBeadIds.includes(region.childrenGridIds[oldIndex]!) ===
                false
              ) {
                toDeleteBeadIds.push(region.childrenGridIds[oldIndex]!);
              }
            }
          }
        }
      }
    } else {
      logger.error("region shape not handled", region.shape);
    }

    return {
      toDeleteBeadIds,
      newChildrenGridIds,
    };
  }

  isRegionContainBead(region: Region, bead: Bead) {
    const regionTopLeftX = region.position.x - region.width / 2;
    const regionTopLeftY = region.position.y - region.height / 2;

    const containBead = contain(
      {
        x: regionTopLeftX,
        y: regionTopLeftY,
        z: 0,
      },
      {
        x: region.position.x + region.width / 2,
        y: region.position.y + region.height / 2,
        z: 0,
      },
      {
        x: bead.position.x,
        y: bead.position.y,
        z: 0,
      }
    );

    return containBead;
  }

  isRegionCellContainBead(region: Region, bead: Bead, cellIndex: number) {
    const currentRow = Math.floor(cellIndex / region.columns);
    const currentCol = Math.floor(cellIndex % region.columns);
    const cellW = region.width / region.columns;
    const cellH = region.height / region.rows;
    const regionTopLeftX = region.position.x - region.width / 2;
    const regionTopLeftY = region.position.y - region.height / 2;

    const offsetEnabled = region.gridType === RegionGridType.Bricks;
    const _offsetX = offsetEnabled ? (currentRow % 2 === 0 ? 0 : cellW / 2) : 0;

    const containBead = contain(
      {
        x: regionTopLeftX + currentCol * cellW + _offsetX,
        y: regionTopLeftY + currentRow * cellH,
        z: 0,
      },
      {
        x: regionTopLeftX + currentCol * cellW + cellW + _offsetX,
        y: regionTopLeftY + currentRow * cellH + cellH,
        z: 0,
      },
      {
        x: bead.position.x,
        y: bead.position.y,
        z: 0,
      }
    );

    return containBead;
  }

  getRegionCellCenterFromCellIndex(region: Region, cellIndex: number) {
    const currentRow = Math.floor(cellIndex / region.columns);
    const currentCol = Math.floor(cellIndex % region.columns);
    return this.getRegionCellCenter(region, currentRow, currentCol);
  }

  getRegionCellCenter(region: Region, rowIndex: number, columnIndex: number) {
    const cellW = region.width / region.columns;
    const cellH = region.height / region.rows;
    const offsetEnabled = region.gridType === RegionGridType.Bricks;
    const _offsetX = offsetEnabled ? (rowIndex % 2 === 0 ? 0 : cellW / 2) : 0;
    const offsetX = columnIndex * cellW + _offsetX;
    const offsetY = rowIndex * cellH;
    const startX = region.position.x - region.width / 2;
    const startY = region.position.y - region.height / 2;
    const posX = startX + offsetX + cellW / 2;
    const posY = startY + offsetY + cellH / 2;
    const position: Point = {
      x: posX,
      y: posY,
      z: 0,
    };
    return position;
  }

  getRegionCellIndex(x: number, y: number, region: Region) {
    const regionTopLeftX = region.position.x - region.width / 2;
    const regionTopLeftY = region.position.y - region.height / 2;
    const cellW = region.width / region.columns;
    const cellH = region.height / region.rows;
    const row = Math.floor((y - regionTopLeftY) / cellH);
    const offsetEnabled = region.gridType === RegionGridType.Bricks;
    const _offsetX = offsetEnabled ? (row % 2 === 0 ? 0 : cellW / 2) : 0;
    const distX = x - regionTopLeftX - _offsetX;
    const column = Math.floor(distX / cellW);
    const index = column < 0 ? -1 : row * region.columns + column;
    return { index, row, column };
  }
}
