import produce from "immer";
import { AddObjects, AppEventType } from "../AppEventType";
import { setStore, shouldRefreshReact, shouldRefreshThree } from "../store";
import { AddObjectsHistory } from "../AppHistory";
import { addObjectByObjectType } from "../../threejs/eventEmitterHandlers";
import {
  AppObject,
  Bead,
  ObjectTypes,
  Region,
} from "../../editorObjects/Editor";
import { refreshReactState } from "../refreshReactState";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import { notifyBackend, SaveEventType } from "../../ApiService";
import { formatISO } from "date-fns";

export class AddObjectHandler extends StoreObjectHandler<AddObjects> {
  run() {
    const objects: AppObject[] = [];
    let newStore = produce(this.store, (draft) => {
      this.event.payload.objects.forEach((object) => {
        let newObject = this.getAppObjectFromCloneAppObject(object);
        if (newObject === undefined) {
          return;
        }
        if (newObject.objectType === ObjectTypes.Region) {
          (newObject as Region).childrenGridIds.forEach((_beadId) => {
            if (_beadId !== undefined) {
              (draft.objects[_beadId] as Bead).regionId = newObject!.id;
            }
          });
        }
        draft.objects[newObject.id] = newObject;
        objects.push(newObject);
      });
    });

    if (this.saveToHistory) {
      const historyEvent: AddObjectsHistory = {
        do: this.event,
        redo: {
          name: AppEventType.AddObjects,
          payload: { objects: objects },
        },
        undo: {
          name: AppEventType.RemoveObjects,
          payload: { ids: objects.map((b) => b.id) },
        },
        createdAt: formatISO(new Date()),
      };
      this.history.push(historyEvent);
    }
    newStore = this.getStoreWithHistory(newStore);

    setStore(newStore);

    if (shouldRefreshThree()) {
      objects.forEach((obj) => {
        addObjectByObjectType(obj);
      });
    }

    if (shouldRefreshReact()) {
      refreshReactState(newStore);
    }

    notifyBackend(
      objects.map((object) => {
        return {
          action: SaveEventType.CREATE,
          createdAt: new Date().toISOString(),
          object,
        };
      })
    );
  }
}
