export class MouseDragHelper {
  isDragged: boolean = false;
  isDragStarted: boolean = false;
  end: { x: number; y: number } = { x: 0, y: 0 };
  start: { x: number; y: number } = { x: 0, y: 0 };
  shouldBehaveLikeMove: boolean = false;

  onDown(x: number, y: number) {
    this.isDragStarted = true;
    this.isDragged = false;
    this.start.x = x;
    this.start.y = y;
    this.end.x = x;
    this.end.y = y;
  }
  
  onUp() {
    this.isDragStarted = false;
    this.isDragged = false;
    this.start.x = 0;
    this.start.y = 0;
    this.end.x = 0;
    this.end.y = 0;
    this.shouldBehaveLikeMove = false;
  }

  onMove(x: number, y: number) {
    if (this.isDragStarted === true) {
      this.end.x = x;
      this.end.y = y;
      if (this.isDragged === false) {
        const { start, end } = this;
        const deltaX = end.x - start.x;
        const deltaY = end.y - start.y;

        if (Math.abs(deltaX) > 5 || Math.abs(deltaY) > 5) {
          this.isDragged = true;
        }
      }
    }
  }
}
