import { Store } from "../controllers/store";

export interface Point {
  x: number;
  y: number;
  z: number;
  // [immerable]: boolean;
}

export enum BeadTypes {
  // TOHO_MIYUKI = "TOHO_MIYUKI",
  // TOHO_15 = "TOHO_15",
  // TOHO_SPHERE = "TOHO_SPHERE",
  MODEL_SPHERE = "MODEL_SPHERE",
  MODEL_CYLINDER = "MODEL_CYLINDER",
  MODEL_FIRE_POLISH = "MODEL_FIRE_POLISH",
  MODEL_TOHO_ROUND = "MODEL_TOHO_ROUND",
}

export interface AppObjectProps {
  id: string;
  position: Point;
  width: number;
  height: number;
  depth: number;
  rotateX: number;
  rotateY: number;
  rotateZ: number;
  objectType: ObjectTypes;
  isLocked: boolean;
  canLockOrUnlock: boolean;
}

export interface PreviewProject3D extends AppObjectProps {
  color: string;
}

export enum BeadMaterialTypes {
  "Shiny" = "Shiny",
  "Mat" = "Mat",
}

export interface Bead extends AppObjectProps {
  type: BeadTypes;
  color: string;
  material: BeadMaterialTypes;
  lineRotate: number;
  keepAspectRatio: boolean;
  regionId: string | undefined;
  groupId: string | undefined;
}

export const groupIdKey: keyof Bead = "groupId";

export interface EditBBox {
  id: string;
  position: Point;
  // [immerable]: boolean;
  width: number;
  height: number;
  depth: number;
  rotateX: number;
  rotateY: number;
  rotateZ: number;
  editable: boolean;
  selectedIds: {
    id: string;
    objectType: ObjectTypes;
    groupId: string | undefined;
  }[];
  canLockOrUnlock: true;
  isLocked: false;
  objectType: ObjectTypes;
  bbox?: THREE.Box3;
}

export interface Line {
  id: string;
  start: Point;
  end: Point;
  beadIds: string[];
  // [immerable]: boolean;
  settings: {
    beadsCount: number;
    beadsAutoCount: boolean;
    beadsSpacing: number;
    beadsAutoSpacing: boolean;
    defaultBead: Partial<Bead>;
  };
}

export enum RegionGridType {
  Rectangles = "Rectangles",
  Bricks = "Bricks",
}

export enum RegionShape {
  Rectangle = "Rectangle",
  Triangle = "Triangle",
  Circle = "Circle",
}

export interface Region extends AppObjectProps {
  beadIds: string[];
  gridType: RegionGridType;
  shape: RegionShape;
  opacity: number;
  columns: number;
  rows: number;
  autoFillBeads: boolean;
  snapBeadsToCell: boolean;
  childrenGridIds: (string | undefined)[];
  // settings: {
  // beadsCount: number;
  // beadsAutoCount: boolean;
  // beadsSpacing: number;
  // beadsAutoSpacing: boolean;
  // defaultBead: Partial<Bead>;
  // };
}

export interface Group extends AppObjectProps {
  objectType: ObjectTypes;
  childrenIds: string[];
}

export interface AppData {
  objects: { [s: string]: AppObject };
  /**@deprecated */
  beads: { [s: string]: Bead };
  /**@deprecated */
  lines: { [s: string]: Line };
  /**@deprecated */
  regions: { [s: string]: Region };
}

export type AppObject = Bead | Region | Group | PreviewProject3D;
export type AppObjectKeys =
  | keyof Bead
  | keyof Region
  | keyof Group
  | keyof PreviewProject3D;

