import * as THREE from "three";
import { loadAppModels } from "./loadModels";
import { initMouseListeners } from "./mouseListeners/mouseListeners";
import { getRect10x10FromScreenToWorld } from "./mouseListeners/getRect10x10FromScreenToWorld";
import {
  initKeybinding,
  startListeningKeys,
  subscribeKeys,
} from "./keyboard/keyMap";
import {
  initHistory,
  redoHistory,
  undoHistory,
} from "../controllers/AppHistory";
import { getStore, initStore } from "../controllers/store";
import { clipboardCopy, clipboardCut, clipboardPaste } from "./clipboard";
import { initThumbnailsGenerator } from "./thumbnailsGenerator";
import { sendEvent } from "../controllers/storeController";
import { AppEventType, RemoveObjects } from "../controllers/AppEventType";
import { logger } from "../logger";

export let appThree: AppThree;

export const renderThreejs = (
  el: HTMLElement | null,
  canvas: HTMLCanvasElement | OffscreenCanvas | undefined,
  context: WebGLRenderingContext | undefined
) => {
  return initCanvasApp(el, canvas, context);
};

export const unmountThreejs = (el: HTMLElement | null) => {
  if (el !== undefined && el !== null) {
    el.innerHTML = "";

    if (el.classList && el.classList.contains("active") === true) {
      el.classList.remove("active");
    }
  }
  return new Promise((resolve) => {
    resolve(undefined);
  });
};

const initCanvasApp = async (
  el: HTMLElement | null,
  canvas: HTMLCanvasElement | OffscreenCanvas | undefined,
  context: WebGLRenderingContext | undefined
) => {
  if (el === null) {
    logger.warn("no html el element found");
    return;
  }

  if (el.classList.contains("active") === false) {
    el.classList.add("active");
  }

  try {
    initStore();
    initHistory();
    initKeybinding();
    appThree = new AppThree(el, canvas, context);
    el.appendChild(appThree.renderer.domElement);
    await loadAppModels();
    initMouseListeners();
    // initEventEmitterHandlers();
    requestAnimationFrame(threeRender);
    startListeningKeys();

    const rect10x10FromScreenToWorld = getRect10x10FromScreenToWorld();
    appThree.camera.userData = { sceneScale: 1, rect10x10FromScreenToWorld };
    subscribeKeys("keyz", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        logger.trace("listener:: undohistory");
        undoHistory();
      }
      return false;
    });
    subscribeKeys("keyy", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        logger.trace("listener:: redohistory");
        redoHistory();
      }
      return false;
    });
    subscribeKeys("keyc", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        logger.trace("listener:: copy");
        clipboardCopy();
      }
      return false;
    });
    subscribeKeys("keyx", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        logger.trace("listener:: cut");
        clipboardCut();
      }
      return false;
    });
    subscribeKeys("keyv", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        logger.trace("listener:: paste");
        clipboardPaste();
      }
      return false;
    });
    subscribeKeys("delete", (event, pressed) => {
      logger.trace("listener:: delete");
      const store = getStore();
      if (store.selectedIds.length > 0) {
        const action: RemoveObjects = {
          name: AppEventType.RemoveObjects,
          payload: {
            ids: store.selectedIds,
          },
        };
        sendEvent(action);
      }
      return false;
    });

    initThumbnailsGenerator();
  } catch (err) {
    console.error("err:", err.toString());
  }
  return;
};

export interface AppThreeScene extends THREE.Scene {}

class AppThree {
  el: HTMLElement;
  camera: THREE.OrthographicCamera;
  scene: AppThreeScene;
  renderer: THREE.WebGLRenderer;
  objects: Map<String, THREE.Object3D>;
  constructor(
    el: HTMLElement,
    canvas: HTMLCanvasElement | OffscreenCanvas | undefined,
    context: WebGLRenderingContext | undefined
  ) {
    this.el = el;
    const { width, height } = el.getBoundingClientRect();
    this.objects = new Map<String, THREE.Object3D>();
    this.camera = new THREE.OrthographicCamera(
      width / -2,
      width / 2,
      height / 2,
      height / -2,
      0,
      1000
    );

    this.camera.position.set(0, 0, 500);
    this.camera.lookAt(0, 0, 0);
    this.camera.layers.enable(2);
    this.camera.layers.enable(3);

    this.scene = new THREE.Scene() as any;
    this.scene.background = new THREE.Color("#f2f2f2");

    this.renderer = new THREE.WebGLRenderer({
      canvas: canvas ?? undefined,
      context: context ?? undefined,
      antialias: true,
      preserveDrawingBuffer: true,
    });
    // this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(width, height);
  }
}

function threeRender() {
  if (needToResizeRendererToDisplaySize(appThree.renderer)) {
    appThree.camera.updateProjectionMatrix();
  }

  appThree.renderer.render(appThree.scene, appThree.camera);
  requestAnimationFrame(threeRender);
}

function needToResizeRendererToDisplaySize(renderer) {
  const canvas = renderer.domElement;
  const width = canvas.clientWidth;
  const height = canvas.clientHeight;
  const needResize = canvas.width !== width || canvas.height !== height;
  if (needResize) {
    renderer.setSize(width, height, false);
  }
  return needResize;
}

