import produce from "immer";
import { ChangeUiSettingsSidebar } from "../AppEventType";
import { refreshReactState } from "../refreshReactState";
import { setStore } from "../store";
import { BaseEventHandler } from "../BaseHandlers/BaseEventHandler";

export class ChangeUiSettingsSidebarHandler extends BaseEventHandler<ChangeUiSettingsSidebar> {
  run() {
    const { panel, open, toggle } = this.event.payload;
    let newStore = produce(this.store, (draft) => {
      let newOpen = open;

      if (panel === "objects") {
        if (
          toggle !== undefined &&
          toggle === true &&
          draft.ui.rightSidebar.panel === "objects"
        ) {
          newOpen = !draft.ui.rightSidebar.isOpen;
        }
        draft.ui.rightSidebar.isOpen = newOpen;
        draft.ui.rightSidebar.panel = newOpen === false ? undefined : "objects";
      }
      if (panel === "history") {
        if (
          toggle !== undefined &&
          toggle === true &&
          draft.ui.rightSidebar.panel === "history"
        ) {
          newOpen = !draft.ui.rightSidebar.isOpen;
        }
        draft.ui.rightSidebar.isOpen = newOpen;
        draft.ui.rightSidebar.panel = newOpen === false ? undefined : "history";
      }

      if (panel === "comments") {
        if (
          toggle !== undefined &&
          toggle === true &&
          draft.ui.leftSidebar.panel === "comments"
        ) {
          newOpen = !draft.ui.leftSidebar.isOpen;
        }
        draft.ui.leftSidebar.isOpen = newOpen;
        draft.ui.leftSidebar.panel = newOpen === false ? undefined : "comments";
      }
    });
    setStore(newStore);
    refreshReactState(newStore);
  }
}
