import {
  AppEventType,
  ClipboardCopy,
  ClipboardCut,
  ClipboardPaste,
} from "../controllers/AppEventType";
import { sendEvent } from "../controllers/storeController";
import { AppObject } from "../editorObjects/Editor";

export interface ClipboardContent {
  objects: AppObject[];
}
let clipboardContent: ClipboardContent | undefined = undefined;

export const setClipboardBoardContent = (
  content: ClipboardContent | undefined
) => {
  clipboardContent = content;
};

export const getClipboardBoardContent = () => {
  return clipboardContent;
};

export const clipboardCopy = () => {
  const event: ClipboardCopy = {
    name: AppEventType.ClipboardCopy,
    payload: {},
  };
  sendEvent(event);
};

export const clipboardCut = () => {
  const event: ClipboardCut = {
    name: AppEventType.ClipboardCut,
    payload: {},
  };
  sendEvent(event);
};

export const clipboardPaste = () => {
  if (clipboardContent === undefined) {
    return;
  }
  const event: ClipboardPaste = {
    name: AppEventType.ClipboardPaste,
    payload: {
      objects: clipboardContent.objects,
    },
  };
  sendEvent(event);
};
