import { appThree } from "../../renderThreejs";
import * as THREE from "three";
import { getStore } from "../../../controllers/store";

export class MouseEventsIdle {
  isCameraGrabed: boolean;
  startDragMousePosition: THREE.Vector2;
  startDragCamera: THREE.Vector2;
  boundingClientRect: DOMRect | undefined;

  constructor() {
    this.isCameraGrabed = false;
    this.startDragMousePosition = new THREE.Vector2();
    this.startDragCamera = new THREE.Vector2();
  }

  onWheel(event: PointerEvent) {}

  onResize(event: PointerEvent) {}

  onDown(event: PointerEvent) {
    if (appThree.el !== null) {
      this.boundingClientRect = appThree.el.getBoundingClientRect();
    }

    if (this.boundingClientRect === undefined) {
      return [];
    }

    this.isCameraGrabed = true;

    this.boundingClientRect = appThree.el.getBoundingClientRect();
    const { top, left } = this.boundingClientRect;

    const screenX = event.clientX - left;
    const screenY = event.clientY - top;

    this.startDragMousePosition.x = screenX;
    this.startDragMousePosition.y = screenY;

    this.startDragCamera.x = appThree.camera.position.x;
    this.startDragCamera.y = appThree.camera.position.y;
  }

  onMove(event: PointerEvent) {
    if (this.boundingClientRect === undefined) {
      return;
    }

    if (this.isCameraGrabed === true) {
      const store = getStore();

      const { top, left } = this.boundingClientRect;
      const screenX = event.clientX - left;
      const screenY = event.clientY - top;
      const sceneScale = store.ui.zoomScale;

      const screenMouseDiffX = this.startDragMousePosition.x - screenX;
      const screenMouseDiffY = this.startDragMousePosition.y - screenY;

      const mouseDiffXInWorldSpace = screenMouseDiffX * sceneScale;
      const mouseDiffYInWorldSpace = screenMouseDiffY * sceneScale;

      const cameraX = this.startDragCamera.x + mouseDiffXInWorldSpace;
      const cameraY = this.startDragCamera.y - mouseDiffYInWorldSpace;

      appThree.camera.position.x = cameraX;
      appThree.camera.position.y = cameraY;
      appThree.camera.lookAt(cameraX, cameraY, 0);
    }
  }

  onUp(event: PointerEvent) {
    this.boundingClientRect = undefined;
    if (this.isCameraGrabed === true) {
      this.isCameraGrabed = false;
    }
  }
}
