export const ItemActionWrapper: React.FC<{
  onClick: any;
  type: ActionsTypes;
  selectedAction?: ActionsTypes;
}> = (props) => {
  const elRef = React.useRef<HTMLDivElement | null>(null);
  const onClick = () => {
    if (elRef.current) {
      const { offsetLeft, offsetParent, offsetWidth } = elRef.current;
      let groupOffsetLeft = 0;
      if (offsetParent) {
        groupOffsetLeft = (offsetParent as HTMLDivElement).offsetLeft;
      } else {
        console.warn("ref parent element not found");
      }
      const halfWidth = offsetWidth / 2;
      const finalOffsetLeft = groupOffsetLeft + offsetLeft + halfWidth;
      props.onClick(props.type, finalOffsetLeft);
    } else {
      console.warn("ref element not found");
    }
  };
  return (
    <div
      className={`action-item-wrapper ${
        props.selectedAction !== undefined &&
        props.selectedAction === props.type
          ? "active"
          : ""
      }`}
      ref={(e) => (elRef.current = e)}
      onClick={onClick}
    >
      {props.children}
    </div>
  );
};

