export const IconActionsTypeEditBeadColor: React.FC<{
  onClick: any;
  type: ActionsTypes;
  selectedAction?: ActionsTypes;
}> = (props) => {
  const [t] = useTranslation(["board"]);
  const backgroundColors = useSelector((state: Editor) => {
    const colors: string[] = [];
    state.selectedIds.forEach((id) => {
      if (!state.objects[id]) {
        return;
      }
      const color = (state.objects[id] as Bead).color;
      if (colors.includes(color) === false) {
        colors.push(color);
      }
    });
    return colors;
  });

  if (backgroundColors.length === 0) {
    return <React.Fragment />;
  }

  return (
    <ItemActionWrapper
      onClick={props.onClick}
      type={props.type}
      selectedAction={props.selectedAction}
    >
      <div className={"icon-action-type-color"}>
        <div
          data-tip={t(`board:action_type.${props.selectedAction}`)}
          data-place={"bottom"}
          data-type={"dark"}
          data-effect={"solid"}
        >
          <div
            className={"value"}
            style={{
              background: `linear-gradient(to right, ${
                backgroundColors.length === 1
                  ? [backgroundColors[0], backgroundColors[0]].join()
                  : backgroundColors.slice(0, 10).join()
              })`,
            }}
          >
            &nbsp;
          </div>
        </div>
      </div>
    </ItemActionWrapper>
  );
};

const IconActionsUnlock: React.FC<{
  onClick: any;
  type: ActionsTypes;
  selectedAction?: ActionsTypes;
}> = (props) => {
  const [t] = useTranslation(["board"]);
  const [startLongPress, setStartLongPress] = React.useState(false);
  const ms = 600;
  const onSuccess = () => {
    const store = getStore();
    const event: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: store.selectedIds.map((id) => {
          const change: ChangeObject = {
            id,
            properties: {
              isLocked: false,
            },
          };
          return change;
        }),
      },
    };
    sendEvent(event);
    props.onClick(undefined, undefined);
  };

  React.useEffect(() => {
    let timerId;
    if (startLongPress) {
      timerId = setTimeout(onSuccess, ms);
    } else {
      clearTimeout(timerId);
    }

    return () => {
      clearTimeout(timerId);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [startLongPress]);

  const start = React.useCallback(() => {
    setStartLongPress(true);
  }, []);

  const stop = React.useCallback(() => {
    setStartLongPress(false);
  }, []);

  return (
    <div
      onMouseDown={start}
      onMouseUp={stop}
      onMouseLeave={stop}
      onTouchStart={start}
      onTouchEnd={stop}
      className={`long-press-wrapper ${startLongPress ? "active" : ""}`}
    >
      <div
        className={`long-press-ghost`}
        style={{ transitionDuration: `${ms}ms` }}
      >
        &nbsp;
      </div>
      <div className={"long-press-icon"}>
        <div className={"icon-wrapper"}>
          <FontAwesomeIcon icon={faLock} />
        </div>

        <div className={"label-wrapper"}>
          <span>{t("board:edit_bead_popup.long_press_to_unlock")}</span>
        </div>
      </div>
    </div>
  );
};

const IconActionsLock: React.FC<{
  onClick: any;
  type: ActionsTypes;
  selectedAction?: ActionsTypes;
}> = (props) => {
  const [t] = useTranslation(["board"]);
  const onClick = () => {
    const store = getStore();
    const _event: ChangeSelectObjects = {
      name: AppEventType.ChangeSelectObjects,
      payload: {
        change: store.selectedIds.map((id) => {
          const _change: ChangeSelectObject = {
            id,
            isSelected: false,
          };
          return _change;
        }),
      },
    };
    sendEvent(_event);
    const event: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: store.selectedIds.map((id) => {
          const change: ChangeObject = {
            id,
            properties: {
              isLocked: true,
            },
          };
          return change;
        }),
      },
    };
    sendEvent(event);
  };
  return (
    <ItemActionWrapper onClick={onClick} type={ActionsTypes.Lock}>
      <div className={"icon-action-type"}>
        <div
          data-tip={t(`board:action_type.${ActionsTypes.Lock}`)}
          data-place={"bottom"}
          data-type={"dark"}
          data-effect={"solid"}
        >
          <div className={"align-icons-wrapper"}>
            <FontAwesomeIcon icon={faUnlock} />
          </div>
        </div>
      </div>
    </ItemActionWrapper>
  );
};

