import React from "react";
import { Editor } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { getStore, setStore } from "../controllers/store";
import produce from "immer";
import { refreshReactState } from "../controllers/refreshReactState";
import styled from "styled-components";
import { useTranslation } from "react-i18next";

export const ModalEditBoard: React.FC<{}> = () => {
  const [t] = useTranslation(["board", "translation"]);
  const project = useSelector((state: Editor) => {
    return state.board;
  });
  const [name, setName] = React.useState(project?.name ?? "");
  if (!project) {
    return <React.Fragment />;
  }
  if (project.editModalVisible === false) {
    return <React.Fragment />;
  }
  const onClickCancel = () => {
    const store = getStore();
    const newStore = produce(store, (draft) => {
      if (draft.board) {
        draft.board.editModalVisible = false;
      }
    });
    setStore(newStore);
    refreshReactState(newStore);
  };
  const setNewProjectName = (newName: string) => {
    const store = getStore();
    const newStore = produce(store, (draft) => {
      if (draft.board) {
        draft.board.name = newName;
      }
    });
    setStore(newStore);
    refreshReactState(newStore);
  };


  return (
    <ModalEditProjectStyles>
      <div className={"edit-modal-popup"}>
        <div className={"edit-modal-popup-overlay"} onClick={onClickCancel}>
          &nbsp;
        </div>
        <div className={"edit-modal-popup-content-wrapper"}>
          <div className={"edit-modal-popup-content"}>
            <div className={"btn-cancel"} onClick={onClickCancel}>
              <FontAwesomeIcon icon={faTimes} color={"#000"} />
            </div>

            <h2>{t("board:modal_edit_board.title")}</h2>
            <div>
              <input
                name={"name"}
                className={"input"}
                placeholder={t(
                  "board:modal_edit_board.name_input_placeholder"
                )}
                value={name}
                onChange={(event) => {
                  setName(event.target.value);
                }}
                onBlur={(event) => {
                  const newName = event.target.value;
                  if (newName.length > 0) {
                    setNewProjectName(newName);
                  }
                }}
              />
            </div>

            <div className={"actions"}></div>
          </div>
        </div>
      </div>
    </ModalEditProjectStyles>
  );
};

