import i18next from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";
// import LocalStorageBackend from "i18next-localstorage-backend";
import HttpApi from "i18next-http-backend";
import sprintf from "i18next-sprintf-postprocessor";
import { enUS, pl } from "date-fns/locale";
import {
  isDate,
  format as formatDate,
  formatRelative,
  formatDistance,
} from "date-fns";
const locales = { enUS, pl }; // used to look up the required locale

const isDev = !process.env.NODE_ENV || process.env.NODE_ENV === "development";
const translationHost = isDev
  ? "./locales/"
  : "https://rpcal.github.io/coralinio-preview/build/locales/";

i18next
  .use(HttpApi)
  .use(LanguageDetector)
  .use(initReactI18next)
  .use(sprintf)
  .init({
    // debug: isDev ? true : false,
    debug: false,
    preload: ["pl"],
    whitelist: ["en", "pl"],
    ns: ["board", "translation", "boards", "dates"],
    fallbackLng: "pl",
    fallbackNS: "translation",
    defaultNS: "translation",
    saveMissing: false,
    initImmediate: false,
    interpolation: {
      escapeValue: false,
      format: (value, format, lng) => {
        if (isDate(value)) {
          let localesKey: string = lng ?? "pl";
          if (lng === "en") {
            localesKey = "enUS";
          }

          const locale = locales[localesKey];
          
          if (format === undefined) {
            format = "short";
          }

          if (format === "short") return formatDate(value, "P", { locale });
          if (format === "long") return formatDate(value, "PPPP", { locale });
          if (format === "relative")
            return formatRelative(value, new Date(), { locale });
          if (format === "ago")
            return formatDistance(value, new Date(), {
              locale,
              addSuffix: true,
            });

          return formatDate(value, format, { locale });
        }
        return value;
      },
    },
    returnEmptyString: false,
    overloadTranslationOptionHandler: sprintf.overloadTranslationOptionHandler,
    backend: {
      loadPath: translationHost + "./{{lng}}__{{ns}}.json",
    },
    detection: {
      order: [
        "querystring",
        "cookie",
        "localStorage",
        "sessionStorage",
        "navigator",
      ],
      lookupQuerystring: "lang",
      lookupCookie: "i18next",
      lookupLocalStorage: "i18nextLang",
      lookupSessionStorage: "i18nextLang",
      caches: ["localStorage", "cookie"],
    },
    react: {
      //   bindI18n: "languageChanged",
      //   bindI18nStore: "",
      //   transEmptyNodeValue: "",
      //   transSupportBasicHtmlNodes: true,
      //   transKeepBasicHtmlNodesFor: ["br", "strong", "i"],
      useSuspense: false,
    },
  });

export const getI18n = () => i18next;
