import React, { Suspense } from "react";
// import "bulma/css/bulma.css";
// import "./App.css";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import { AppData } from "./AppData";
import { PageBoards } from "./PageBoards";
import { PageBoard } from "./PageBoard";
import { PageHome } from "./PageHome";
import { PageNoMatch } from "./PageNoMatch";
import { PageNotAutorized } from "./PageNotAutorized";
import { useContextSelector } from "use-context-selector";
import { AuthContext, useValue } from "./AuthContext";
import { logger } from "./logger";
import { AppFonts, GlobalAppStyles } from "./GlobalAppStyles";
import { I18nextProvider } from "react-i18next";
import { getI18n } from "./i18n";


const App: React.FC<{
  data: AppData;
}> = (props) => {
  logger.log("render App.tsx");
  const isAuthorized = (props.data?.token ?? "").length > 0;
  const [state, dispatch] = useValue(
    isAuthorized === true
      ? {
          isAuthorized: true,
          data: props.data,
        }
      : {
          isAuthorized: false,
          data: undefined,
        }
  );
  return (
    <Suspense fallback={<div>Loading... </div>}>
      <I18nextProvider i18n={getI18n()}>
        <AuthContext.Provider value={{ state, dispatch }}>
          <GlobalAppStyles></GlobalAppStyles>
          <style>{AppFonts}</style>
          <Routes data={props.data} />
        </AuthContext.Provider>
      </I18nextProvider>
    </Suspense>
  );
};

const Routes: React.FC<{
  data: AppData;
}> = (props) => {
  const isAuthorized = useContextSelector(
    AuthContext,
    (ctx) => ctx.state.isAuthorized
  );
  return (
    <Router hashType={"slash"}>
      {isAuthorized ? (
        <Switch>
          <Route exact path="/">
            <PageHome data={props.data} />
          </Route>
          <Route exact path="/boards">
            <PageBoards data={props.data} />
          </Route>
          <Route exact path="/board/:uuid">
            <PageBoard
              data={props.data}
              canvas={undefined}
              context={undefined}
            />
          </Route>
          <Route path="*">
            <PageNoMatch data={props.data} />
          </Route>
        </Switch>
      ) : (
        <Switch>
          <Route exact path="/">
            <PageNotAutorized data={props.data} />
          </Route>
          <Route path="*">
            <PageNoMatch data={props.data} />
          </Route>
        </Switch>
      )}
    </Router>
  );
};

export default App;

