import * as THREE from "three";
import { getStore } from "../../controllers/store";
import { appStore } from "../../immerReducers/appStore";
import { appThree } from "../renderThreejs";

export const screenToWorld = (
  clientX: number,
  clientY: number
): THREE.Vector2 => {
  if (appThree.el === null) {
    throw new Error("no el in dom");
  }
  if (!appStore) {
    throw new Error("no el in dom");
  }
  // const { getState } = appStore;
  // const state = getState();
  const store = getStore();
  const sceneScale = store.ui.zoomScale;
  const { width, height, top, left } = appThree.el.getBoundingClientRect();
  const screenX = clientX - left; // canvasX
  const screenY = clientY - top; // canvasY
  const x = (screenX - width / 2) * sceneScale + appThree.camera.position.x;
  const y = (-screenY + height / 2) * sceneScale + appThree.camera.position.y;
  return new THREE.Vector2(x, y);
};

export const worldToScreen = (pos: THREE.Vector3) => {
  if (!appStore) {
    throw new Error("no el in dom");
  }
  // const { getState } = appStore;
  // const state = getState();
  // const store = getStore();
  // const sceneScale = store.ui.zoomScale;
  const { width, height, top, left } = appThree.el.getBoundingClientRect();
  var vector = pos.clone();
  // vector.setFromMatrixPosition(obj.matrixWorld);
  var widthHalf = width / 2;
  var heightHalf = height / 2;
  
  vector.project(appThree.camera);
  // console.log("??? ", vector)
  // console.log("------", vector.x, vector.y, sceneScale);
  vector.x = vector.x * widthHalf + widthHalf + left;
  vector.y = -(vector.y * heightHalf) + heightHalf + top;
  return vector;
};

export const getTopBarPositionInScreenFromObject = (bbox: THREE.Box3) => {
  // const pos = worldToScreen(obj);

  // const bbox = new THREE.Box3();
  // bbox.expandByObject(obj);
  if (isFinite(bbox.min.x) === false || isFinite(bbox.max.y) === false) {
    return undefined;
  }

  // const offset = new THREE.Vector3();
  // offset.x = bbox.min.x;
  // offset.y = bbox.min.y;

  // console.log(
  //   "getTopBarPositionInScreenFromObject bbox.min",
  //   bbox.min,
  //   bbox.max
  // );

  // bbox.getCenter().x
  var vector = new THREE.Vector3();
  vector.x = bbox.min.x + (bbox.max.x - bbox.min.x) / 2;
  vector.y = bbox.max.y;

  // vector = obj.localToWorld(vector);
  // vector.sub(offset);
  const pos2 = worldToScreen(vector);

  if(pos2.x < 0){
    pos2.x = 0;
  } 
  if(pos2.y < 70){
    pos2.y = 70;
  } 

  // var vector2 = new THREE.Vector3();
  // vector2.add()
  // vector.add(pos2)

  return pos2;
};

export const getTopBarPositionInScreenFromBBox = (vector: THREE.Vector3) => {
  return worldToScreen(vector);
};
