import * as THREE from "three";

let mousePosition: undefined | THREE.Vector2 = undefined;
export const getMousePositionAtWindow = () => {
  return mousePosition;
};
export const setMousePositionAtWindow = (clientX?: number, clientY?: number) => {
  if (!clientX || !clientY) {
    mousePosition = undefined;
  } else {
    const newPos = new THREE.Vector2();
    newPos.x = clientX;
    newPos.y = clientY;
    mousePosition = newPos;
  }
//   console.log("new mouse position", mousePosition);
};
