import { appThree } from "../../renderThreejs";
import * as THREE from "three";
import { Group, groupIdKey, ObjectTypes } from "../../../editorObjects/Editor";
import { getStore } from "../../../controllers/store";

export class MouseEventsBase {
  boundingClientRect: DOMRect | undefined;
  mouse: THREE.Vector2;
  raycaster: THREE.Raycaster;
  intersects: THREE.Intersection[];

  constructor() {
    this.boundingClientRect = undefined;
    this.mouse = new THREE.Vector2();
    this.raycaster = new THREE.Raycaster();
    this.raycaster.layers.enable(3);
    this.intersects = [];
  }

  onWheel(event: PointerEvent) {}

  onResize(event: PointerEvent) {}

  onDown(event: PointerEvent) {}

  onMove(event: PointerEvent) {}

  onUp(event: PointerEvent) {}

  getIntersect(clientX: number, clientY: number) {
    if (appThree.el !== null) {
      this.boundingClientRect = appThree.el.getBoundingClientRect();
    }

    if (this.boundingClientRect === undefined) {
      return [];
    }

    const { width, height, top, left } = this.boundingClientRect;
    this.mouse.x = ((clientX - left) / width) * 2 - 1;
    this.mouse.y = -((clientY - top) / height) * 2 + 1;
    this.raycaster.setFromCamera(this.mouse, appThree.camera);
    // @TODO: use raycaster with layers
    // this.raycaster.layers.
    return this.raycaster.intersectObjects(appThree.scene.children, true);
  }

  getIntersectObjects(clientX: number, clientY: number) {
    return this.getObjectsFromIntersect(this.getIntersect(clientX, clientY));
  }

  getActiveObjectsFromIntersect(clientX: number, clientY: number) {
    const intersects = this.getIntersect(clientX, clientY);
    return intersects.filter((obj) => {
      const data = obj.object.userData;
      const objectType = data?.objectType ?? undefined;
      return (
        objectType === ObjectTypes.Bead ||
        objectType === ObjectTypes.Region ||
        objectType === ObjectTypes.Group ||
        objectType === ObjectTypes.PreviewProject3D
      );
    });
  }

  getObjectsFromIntersect(intersects: THREE.Intersection[]) {
    const { Bead, Region, SceneEditBBox } = ObjectTypes;
    return intersects.filter((obj) => {
      const objectType = obj?.object?.userData?.objectType ?? undefined;
      return (
        objectType === Bead ||
        objectType === Region ||
        objectType === SceneEditBBox
      );
    });
  }

  getIdsFromGroupOrInGroup(objectId: string) {
    const store = getStore();
    if (store.objects[objectId] === undefined) {
      return [];
    }
    // store.objects[objectId].objectType;
    const isGroup = store.objects[objectId].objectType === ObjectTypes.Group;
    const isInGroup =
      store.objects[objectId][groupIdKey] !== undefined &&
      store.objects[objectId][groupIdKey].length > 0;
    const groupId = store.objects[objectId][groupIdKey];
    if (isGroup) {
      return (store.objects[objectId] as Group).childrenIds;
    } else if (isInGroup) {
      return [groupId, ...(store.objects[groupId] as Group).childrenIds];
    } else {
      return [];
    }
  }

  groupObjectsByTypeFromIntersect(
    intersects: THREE.Intersection[],
    maxItems?: number
  ) {
    // const { Bead, Region, SceneEditBBox } = ObjectTypes;
    const result: {
      Bead: THREE.Intersection[];
      Region: THREE.Intersection[];
      SceneEditBBox: THREE.Intersection[];
    } = {
      Bead: [],
      Region: [],
      SceneEditBBox: [],
    };
    const selected: number[] = [];
    intersects.forEach((obj, index) => {
      if (maxItems !== undefined) {
        if (selected.length >= maxItems) {
          return;
        }
      }
      const id = obj?.object?.userData?.id ?? undefined;
      const objectType = obj?.object?.userData?.objectType ?? undefined;
      // const isLocked = obj?.object?.userData?.isLocked ?? false;

      if (id !== undefined) {
        if (objectType !== undefined) {
          result[objectType].push(obj);
          selected.push(index);
        }
      }
    });
    return result;
  }
}
