export const PopupWrapper: React.FC<{
  offsetLeft: number;
  type: ActionsTypes;
}> = (props) => {
  let leftOffset = props.offsetLeft;
  if (
    props.type === ActionsTypes.EditRotate ||
    props.type === ActionsTypes.EditPosition
  ) {
    leftOffset = props.offsetLeft - 150;
  }
  if (
    props.type === ActionsTypes.EditWidthHeight ||
    props.type === ActionsTypes.EditWidthHeightDepth
  ) {
    leftOffset = props.offsetLeft - 250;
  }
  return (
    <PopupWrapperStyles>
      <div
        className={"popup-wrapper"}
        style={{
          left: leftOffset,
        }}
      >
        {props.type === ActionsTypes.EditBeadColor ? (
          <div className={"popup-wrapper-content"}>
            <EditBeadColorPopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.More ? (
          <div className={"popup-wrapper-content"}>
            <MorePopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditRegionShape ? (
          <div className={"popup-wrapper-content"}>
            <EditRegionShapePopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditRegionGridType ? (
          <div className={"popup-wrapper-content"}>
            <EditRegionGridTypePopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditRegionGridSettings ? (
          <div className={"popup-wrapper-content"}>
            <EditRegionGridSettingsPopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditPosition ? (
          <div className={"popup-wrapper-content"}>
            <EditPositionPopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditRotate ? (
          <div className={"popup-wrapper-content"}>
            <EditRotatePopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditWidthHeight ? (
          <div className={"popup-wrapper-content"}>
            <EditWidthHeightPopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditWidthHeightDepth ? (
          <div className={"popup-wrapper-content"}>
            <EditWidthHeightDepthPopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.Alignment ? (
          <div className={"popup-wrapper-content"}>
            <AlignmentPopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditBeadType ? (
          <div className={"popup-wrapper-content"}>
            <EditBeadTypePopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditBeadProduct ? (
          <div className={"popup-wrapper-content"}>
            <EditBeadProductPopup />
          </div>
        ) : null}

        {props.type === ActionsTypes.EditBeadMaterial ? (
          <div className={"popup-wrapper-content"}>
            <EditBeadMaterialPopup />
          </div>
        ) : null}

        {/* @TODO: REFACTOR THAT */}
        {!(
          props.type === ActionsTypes.More ||
          props.type === ActionsTypes.EditRegionGridType ||
          props.type === ActionsTypes.EditPosition ||
          props.type === ActionsTypes.EditRotate ||
          props.type === ActionsTypes.EditWidthHeight ||
          props.type === ActionsTypes.EditWidthHeightDepth ||
          props.type === ActionsTypes.Alignment ||
          props.type === ActionsTypes.EditRegionGridSettings ||
          props.type === ActionsTypes.EditRegionShape ||
          props.type === ActionsTypes.EditBeadType ||
          props.type === ActionsTypes.EditBeadProduct ||
          props.type === ActionsTypes.EditBeadMaterial ||
          props.type === ActionsTypes.EditBeadColor
        ) ? (
          <div className={"popup-wrapper-content"}>
            <div>popup of type: {props.type}</div>
          </div>
        ) : null}
      </div>
    </PopupWrapperStyles>
  );
};

