const EditBeadColorPopup: React.FC<{}> = (props) => {
  const selectedIds = useSelector((state: Editor) => {
    return state.selectedIds;
  });
  const backgroundColors = useSelector((state: Editor) => {
    const colors: string[] = [];
    state.selectedIds.forEach((id) => {
      if (!state.objects[id]) {
        return;
      }
      const color = (state.objects[id] as Bead).color;
      if (colors.includes(color) === false) {
        colors.push(color);
      }
    });
    return colors;
  });

  const onSelectColor = (color: ColorResult) => {
    const event: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: selectedIds.map((id) => {
          const change: ChangeObject = {
            id,
            properties: {
              color: color.hex,
            },
          };
          return change;
        }),
      },
    };
    sendEvent(event);
  };
  return (
    <div>
      <SwatchesPickerStyles>
        <SwatchesPicker
          width={300}
          height={300}
          onChangeComplete={onSelectColor}
          color={backgroundColors.length > 1 ? undefined : backgroundColors[0]}
          className={"SwatchesPicker-custom"}
        />
      </SwatchesPickerStyles>
    </div>
  );
};

const SwatchesPickerStyles = styled.div`
  .SwatchesPicker-custom > div > div:first-child {
    display: none !important;
  }
`;

