const EditRegionGridTypePopup: React.FC<{}> = (props) => {
  const selectedIds = useSelector((state: Editor) => {
    return state.selectedIds;
  });

  const onSelect = (gridType: RegionGridType) => {
    const store = getStore();
    const event: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: selectedIds.map((id) => {
          const region = store.objects[id] as Region;
          const properties: ChangeObject["properties"] = {
            gridType,
            shape: region.shape,
            columns: region.columns,
            rows: region.rows,
          };
          const columnsKey: keyof Region = "columns";
          const rowsKey: keyof Region = "rows";
          if (
            gridType === RegionGridType.Bricks &&
            region.shape === RegionShape.Triangle
          ) {
            const cols = (store.objects[id] as Region).columns;
            const { columns, rows } = getBricksColumnsAndRowsCount(cols);
            properties[columnsKey] = columns;
            properties[rowsKey] = rows;
          }
          const change: ChangeObject = {
            id,
            properties,
          };
          return change;
        }),
      },
    };
    sendEvent(event);
  };
  const onSelectRect = () => {
    onSelect(RegionGridType.Rectangles);
  };
  const onSelectBricks = () => {
    onSelect(RegionGridType.Bricks);
  };
  return (
    <div className={"region-type-selection"}>
      <div onClick={onSelectRect} className={"region-type-item"}>
        <FontAwesomeIcon icon={faGripHorizontal} />
      </div>
      <div onClick={onSelectBricks} className={"region-type-item"}>
        <FontAwesomeIcon icon={faAlignCenter} />
      </div>
    </div>
  );
};

