const EditRegionGridSettingsPopup: React.FC<{}> = () => {
  const [t] = useTranslation(["board"]);
  const { columns, rows, showRows } = useSelector((state: Editor) => {
    const columns: number[] = [];
    const rows: number[] = [];
    let onlyBricks: boolean = true;
    let onlyShapeTriange: boolean = true;
    state.selectedIds.forEach((id) => {
      if (state.objects[id] === undefined) {
        return;
      }
      if (state.objects[id].objectType !== ObjectTypes.Region) {
        onlyBricks = false;
        onlyShapeTriange = false;
        return;
      }
      if ((state.objects[id] as Region).gridType !== RegionGridType.Bricks) {
        onlyBricks = false;
      }

      if ((state.objects[id] as Region).shape !== RegionShape.Triangle) {
        onlyShapeTriange = false;
      }

      const _columns = (state.objects[id] as Region).columns;
      const _rows = (state.objects[id] as Region).rows;
      if (columns.includes(_columns) === false) {
        columns.push(_columns);
      }
      if (rows.includes(_rows) === false) {
        rows.push(_rows);
      }
    });
    return {
      columns,
      rows,
      showRows: !(
        (onlyBricks as boolean) === true &&
        (onlyShapeTriange as boolean) === true
      ),
    };
  });

  const onChangeRows = (values: number) => {
    if (values === undefined) {
      return;
    }
    const store = getStore();
    const event: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: store.selectedIds.map((id) => {
          if (!store.objects[id]) {
            return {
              id,
              properties: {},
            };
          }
          const region = store.objects[id] as Region;
          const change: ChangeObject = {
            id,
            properties: {
              shape: region.shape,
              gridType: region.gridType,
              columns: region.columns,
              rows: values,
            },
          };
          return change;
        }),
      },
    };
    sendEvent(event);
  };
  const onChangeColumns = (values: number) => {
    if (values === undefined) {
      return;
    }
    const store = getStore();
    const event: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: store.selectedIds.map((id) => {
          if (!store.objects[id]) {
            return {
              id,
              properties: {},
            };
          }
          const region = store.objects[id] as Region;
          const properties: ChangeObject["properties"] = {
            columns: values,
            rows: region.rows,
            shape: region.shape,
            gridType: region.gridType,
          };
          if (showRows === false) {
            const columnsKey: keyof Region = "columns";
            const rowsKey: keyof Region = "rows";
            const { columns, rows } = getBricksColumnsAndRowsCount(values);
            properties[columnsKey] = columns;
            properties[rowsKey] = rows;
          }
          const change: ChangeObject = {
            id,
            properties,
          };
          return change;
        }),
      },
    };
    sendEvent(event);
  };

  return (
    <div className={"region-grid-settings"}>
      <div className={"region-grid-settings-columns"}>
        <InputTypeNumber
          defaultValue={columns.length > 1 ? undefined : columns[0]}
          max={20}
          min={1}
          step={1}
          showDecimal={false}
          label={t("board:edit_bead_popup.region_columns")}
          onChange={onChangeColumns}
        />
      </div>
      {showRows ? (
        <div className={"region-grid-settings-rows"}>
          <InputTypeNumber
            max={20}
            min={1}
            step={1}
            defaultValue={rows.length > 1 ? undefined : rows[0]}
            showDecimal={false}
            label={t("board:edit_bead_popup.region_rows")}
            onChange={onChangeRows}
          />
        </div>
      ) : null}
    </div>
  );
};

const InputTypeNumber: React.FC<{
  defaultValue: number | undefined;
  label: string;
  onChange: (values: number) => void;
  min: number;
  max: number;
  step: number;
  showDecimal: boolean;
}> = (props) => {
  function roundTo(n) {
    var negative = false;
    var digits = 2;
    if (n < 0) {
      negative = true;
      n = n * -1;
    }
    var multiplicator = Math.pow(10, digits);
    n = parseFloat((n * multiplicator).toFixed(11));
    n = (Math.round(n) / multiplicator).toFixed(digits);
    if (negative) {
      n = (n * -1).toFixed(digits);
    }
    return n;
  }

  return (
    <div>
      <div className={"fieldset-wrapper"}>
        <fieldset>
          <label>{props.label}</label>
          <NumericInput
            min={props.min}
            max={props.max}
            step={props.step}
            className={"columns-input"}
            value={props.defaultValue}
            strict={true}
            onChange={props.onChange}
            mobile={true}
            format={props.showDecimal ? roundTo : undefined}
            // parse={roundTo}
          />
        </fieldset>
      </div>
    </div>
  );
};

