const SliderWrapper: React.FC<{
  value: number;
  onChange: any;
  min: number;
  max: number;
}> = (props) => {
  const [value, setValue] = React.useState<number>(props.value);
  return (
    <div style={{ marginBottom: 8, marginTop: 8 }}>
      <SliderPlus
        step={1}
        min={props.min}
        max={props.max}
        value={value}
        onChange={(value) => {
          setValue(value);
        }}
        onAfterChange={(value) => {
          props.onChange(value);
        }}
      />
    </div>
  );
};

