const AlignmentPopup: React.FC<{}> = (props) => {
  const sendSnapTo = (snapType: SnapToLineType) => {
    const store = getStore();

    const event: ChangeSelectedObjectsAlignment = {
      name: AppEventType.ChangeObjectsAlignment,
      payload: {
        selectedIds: store.selectedIds,
        snapType,
      },
    };
    sendEvent(event);
  };

  const onClickAlighXLeft = () => {
    sendSnapTo(SnapToLineType.X_MIN);
  };
  const onClickAlighXMiddle = () => {
    sendSnapTo(SnapToLineType.X_MIDDLE);
  };
  const onClickAlighXRight = () => {
    sendSnapTo(SnapToLineType.X_MAX);
  };
  const onClickAlighYLeft = () => {
    sendSnapTo(SnapToLineType.Y_MIN);
  };
  const onClickAlighYMiddle = () => {
    sendSnapTo(SnapToLineType.Y_MIDDLE);
  };
  const onClickAlighYRight = () => {
    sendSnapTo(SnapToLineType.Y_MAX);
  };
  const uiButton = getComputedStyle(document.body).getPropertyValue(
    "--uiButton"
  );
  return (
    <div>
      X: <br />
      <button onClick={onClickAlighXLeft}>
        <FontAwesomeIcon icon={faAlignLeft} color={uiButton} size={"lg"} />
      </button>
      <button onClick={onClickAlighXMiddle}>
        <FontAwesomeIcon icon={faAlignCenter} color={uiButton} size={"lg"} />
      </button>
      <button onClick={onClickAlighXRight}>
        <FontAwesomeIcon icon={faAlignRight} color={uiButton} size={"lg"} />
      </button>
      <br />
      <br />
      Y: <br />
      <button onClick={onClickAlighYLeft}>
        <FontAwesomeIcon icon={faAlignLeft} color={uiButton} size={"lg"} />
      </button>
      <button onClick={onClickAlighYMiddle}>
        <FontAwesomeIcon icon={faAlignCenter} color={uiButton} size={"lg"} />
      </button>
      <button onClick={onClickAlighYRight}>
        <FontAwesomeIcon icon={faAlignRight} color={uiButton} size={"lg"} />
      </button>
    </div>
  );
};

const MorePopup: React.FC<{}> = (props) => {
  const [t] = useTranslation(["board"]);
  const onlyRegionsInfo = useSelector((state: Editor) => {
    let snapToCellChecked: "allFalse" | "mixed" | "allTrue" | "unknow" =
      "unknow";
    let autoFillChecked: "allFalse" | "mixed" | "allTrue" | "unknow" = "unknow";
    let regionIds: string[] = [];
    let onlyRegions: boolean = state.selectedIds.length > 0 ? true : false;
    for (let i = 0; i < state.selectedIds.length; i++) {
      const id = state.selectedIds[i];
      if (state.objects[id] === undefined) {
        continue;
      }
      const objectType = state.objects[id].objectType;
      if (objectType !== ObjectTypes.Region) {
        onlyRegions = false;
        break;
      }

      const _value1 = (state.objects[id] as Region).snapBeadsToCell;
      if (snapToCellChecked === "unknow") {
        snapToCellChecked = _value1 === true ? "allTrue" : "allFalse";
      } else {
        if (snapToCellChecked === "allTrue" && _value1 === false) {
          snapToCellChecked = "mixed";
        } else if (snapToCellChecked === "allFalse" && _value1 === true) {
          snapToCellChecked = "mixed";
        }
      }

      const _value2 = (state.objects[id] as Region).autoFillBeads;
      if (autoFillChecked === "unknow") {
        autoFillChecked = _value2 === true ? "allTrue" : "allFalse";
      } else {
        if (autoFillChecked === "allTrue" && _value2 === false) {
          autoFillChecked = "mixed";
        } else if (autoFillChecked === "allFalse" && _value2 === true) {
          autoFillChecked = "mixed";
        }
      }

      regionIds.push(id);
    }
    return { onlyRegions, snapToCellChecked, autoFillChecked, regionIds };
  });

  const onClickDuplicate = () => {
    const store = getStore();
    const event: DuplicateObjects = {
      name: AppEventType.DuplicateObjects,
      payload: {
        objects: store.selectedIds.map((id) => store.objects[id]),
      },
    };
    sendEvent(event);
  };
  const onClickDelete = () => {
    const store = getStore();
    const event: RemoveObjects = {
      name: AppEventType.RemoveObjects,
      payload: {
        ids: store.selectedIds,
      },
    };
    sendEvent(event);
  };
  // const onClickCopy = () => {
  //   const event: ClipboardCopy = {
  //     name: AppEventType.ClipboardCopy,
  //     payload: {},
  //   };
  //   sendEvent(event);
  // };
  const onClickAutofill = () => {
    const event: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: onlyRegionsInfo.regionIds.map((id) => {
          let newValue: boolean = true;
          if (onlyRegionsInfo.autoFillChecked === "allTrue") {
            newValue = false;
          } else if (onlyRegionsInfo.autoFillChecked === "allFalse") {
            newValue = true;
          } else {
            newValue = true;
          }
          return {
            id,
            properties: {
              autoFillBeads: newValue,
            },
          };
        }),
      },
    };
    sendEvent(event);
  };
  const onClickSnaptocell = () => {
    const event: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: onlyRegionsInfo.regionIds.map((id) => {
          let newValue: boolean = true;
          if (onlyRegionsInfo.snapToCellChecked === "allTrue") {
            newValue = false;
          } else if (onlyRegionsInfo.snapToCellChecked === "allFalse") {
            newValue = true;
          } else {
            newValue = true;
          }
          return {
            id,
            properties: {
              snapBeadsToCell: newValue,
            },
          };
        }),
      },
    };
    sendEvent(event);
  };
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        minWidth: 220,
      }}
    >
      {onlyRegionsInfo.onlyRegions === true ? (
        <React.Fragment>
          <div onClick={onClickAutofill}>
            <span>{t("board:edit_bead_popup.region_autofill")}</span>
            {onlyRegionsInfo.autoFillChecked === "allTrue" ? (
              <FontAwesomeIcon icon={faCheck} color={"#000"} />
            ) : null}
            {onlyRegionsInfo.autoFillChecked === "mixed" ? (
              <FontAwesomeIcon icon={faExclamationCircle} color={"#444"} />
            ) : null}
          </div>

          <div onClick={onClickSnaptocell}>
            <span>{t("board:edit_bead_popup.region_snap_to_cell")}</span>
            {onlyRegionsInfo.snapToCellChecked === "allTrue" ? (
              <FontAwesomeIcon icon={faCheck} color={"#000"} />
            ) : null}

            {onlyRegionsInfo.snapToCellChecked === "mixed" ? (
              <FontAwesomeIcon icon={faExclamationCircle} color={"#444"} />
            ) : null}
          </div>
        </React.Fragment>
      ) : null}
      {/* <div onClick={onClickCopy}>Copy</div> */}
      <div onClick={onClickDuplicate}>
        {t("board:edit_bead_popup.duplicate")}
      </div>
      <div onClick={onClickDelete}>{t("board:edit_bead_popup.delete")}</div>
    </div>
  );
};
