import { EditorMode } from "../../editorObjects/Editor";
import { appThree } from "../renderThreejs";
import { MouseEventsEditorModeAddBead } from "./mouseEventEditorMode/MouseEventsEditorModeAddBead";
import { MouseEventsEditorModeAddRegion } from "./mouseEventEditorMode/MouseEventsEditorModeAddRegion";
import { MouseEventsEditorModeDelete } from "./mouseEventEditorMode/MouseEventsEditorModeDelete";
import { MouseEventsEditorModeSelectOrMove } from "./mouseEventEditorMode/MouseEventsEditorModeSelectOrMove";
import { MouseEventsEditorModeIdle } from "./mouseEventEditorMode/MouseEventsEditorModeIdle";
import { MouseEventsEditorModePaint } from "./mouseEventEditorMode/MouseEventsEditorModePaint";
import { MouseEventsEditorModeSelectMultiple } from "./mouseEventEditorMode/MouseEventsEditorModeSelectMultiple";
import { getStore } from "../../controllers/store";
import {
  AppEventType,
  ChangeUiSettingsZoom,
} from "../../controllers/AppEventType";
import { sendEvent } from "../../controllers/storeController";
import { setMousePositionAtWindow } from "./mouseScreenPosition";
import { logger } from "../../logger";

let moudeEditorModeIdle,
  moudeEditorModeSelectOrMove,
  moudeEditorModePaint,
  moudeEditorModeDelete,
  moudeEditorModeAddRegion,
  moudeEditorModeAddBead,
  moudeEditorModeSelectMultiple;

export const initMouseListeners = () => {
  logger.log("initMouseListeners... bind")
  window.addEventListener("resize", onWindowResize, false);

  appThree.el.addEventListener("pointerdown", onPointerDown, false);
  appThree.el.addEventListener("pointermove", onPointerMove, false);
  appThree.el.addEventListener("pointerup", onPointerUp, false);
  appThree.el.addEventListener("mouseout", onPointerOut, false);
  appThree.el.addEventListener("wheel", onWheel, false);

  moudeEditorModeIdle = new MouseEventsEditorModeIdle();
  moudeEditorModeSelectOrMove = new MouseEventsEditorModeSelectOrMove();
  moudeEditorModePaint = new MouseEventsEditorModePaint();
  moudeEditorModeDelete = new MouseEventsEditorModeDelete();
  moudeEditorModeAddRegion = new MouseEventsEditorModeAddRegion();
  moudeEditorModeAddBead = new MouseEventsEditorModeAddBead();
  moudeEditorModeSelectMultiple = new MouseEventsEditorModeSelectMultiple();
};

function onWheel(event) {
  event.preventDefault();
  if (appThree.el === null) {
    return;
  }

  const store = getStore();
  // const { width, height, top, left } = appThree.el.getBoundingClientRect();

  // const screenX = event.clientX - left;
  // const screenY = event.clientY - top;

  // const cameraX = appThree.camera.position.x;
  // const cameraY = appThree.camera.position.y;

  // const mouseXInWorldBefore = (screenX - width / 2) * store.ui.zoomScale;
  // const mouseYInWorldBefore = -1 * (screenY - height / 2) * store.ui.zoomScale;

  // const zoomChange = store.ui.zoomScale >= 0.35 ? 0.02 * 5 : 0.01;
  // const zoomMax = 100;
  // const zoomMin = 0.0125;
  // let sceneScale = 1;
  // sceneScale = store.ui.zoomScale + Math.sign(event.deltaY) * zoomChange;
  // sceneScale = Math.min(Math.max(zoomMin, sceneScale), zoomMax);

  // const mouseXInWorldAfter = (screenX - width / 2) * sceneScale;
  // const mouseYInWorldAfter = -1 * (screenY - height / 2) * sceneScale;

  // appThree.camera.left = (width * sceneScale) / -2;
  // appThree.camera.right = (width * sceneScale) / 2;
  // appThree.camera.top = (height * sceneScale) / 2;
  // appThree.camera.bottom = (height * sceneScale) / -2;
  // appThree.camera.updateProjectionMatrix();

  // const diffX = mouseXInWorldBefore - mouseXInWorldAfter;
  // const diffY = mouseYInWorldBefore - mouseYInWorldAfter;

  // appThree.camera.position.x = cameraX + diffX;
  // appThree.camera.position.y = cameraY + diffY;
  // appThree.camera.lookAt(
  //   appThree.camera.position.x,
  //   appThree.camera.position.y,
  //   0
  // );

  // const rect10x10FromScreenToWorld = getRect10x10FromScreenToWorld();
  // appThree.camera.userData = { sceneScale, rect10x10FromScreenToWorld };

  const updateUiEvent: ChangeUiSettingsZoom = {
    name: AppEventType.ChangeUiSettingsZoom,
    payload: {
      zoomType: "zoomByScroll",
      useAnimation: false,
      clientX: event.clientX,
      clientY: event.clientY,
      scrollDelta: event.deltaY,
    },
  };
  sendEvent(updateUiEvent);

  if (store.ui.mode === EditorMode.Idle) {
    moudeEditorModeIdle.onWheel(event);
  } else if (store.ui.mode === EditorMode.SelectOrMove) {
    moudeEditorModeSelectOrMove.onWheel(event);
  } else if (store.ui.mode === EditorMode.DeleteBeads) {
    moudeEditorModeDelete.onWheel(event);
  } else if (store.ui.mode === EditorMode.PaintBeads) {
    moudeEditorModePaint.onWheel(event);
  } else if (store.ui.mode === EditorMode.AddRegion) {
    moudeEditorModeAddRegion.onWheel(event);
  } else if (store.ui.mode === EditorMode.AddBeads) {
    moudeEditorModeAddBead.onWheel(event);
  } else if (store.ui.mode === EditorMode.SelectMultiple) {
    moudeEditorModeSelectMultiple.onWheel(event);
  }
}

function onWindowResize(event) {
  if (appThree.el !== null) {
    const { width, height } = appThree.el.getBoundingClientRect();
    appThree.camera.left = width / -2;
    appThree.camera.right = width / 2;
    appThree.camera.top = height / 2;
    appThree.camera.bottom = height / -2;
    appThree.camera.updateProjectionMatrix();
    appThree.renderer.setSize(width, height);
  }

  const store = getStore();
  if (store.ui.mode === EditorMode.Idle) {
    moudeEditorModeIdle.onResize(event);
  } else if (store.ui.mode === EditorMode.SelectOrMove) {
    moudeEditorModeSelectOrMove.onResize(event);
  } else if (store.ui.mode === EditorMode.DeleteBeads) {
    moudeEditorModeDelete.onResize(event);
  } else if (store.ui.mode === EditorMode.PaintBeads) {
    moudeEditorModePaint.onResize(event);
  } else if (store.ui.mode === EditorMode.AddRegion) {
    moudeEditorModeAddRegion.onResize(event);
  } else if (store.ui.mode === EditorMode.AddBeads) {
    moudeEditorModeAddBead.onResize(event);
  } else if (store.ui.mode === EditorMode.SelectMultiple) {
    moudeEditorModeSelectMultiple.onResize(event);
  }
}

function onPointerDown(event: PointerEvent) {
  const store = getStore();

  if (store.ui.mode === EditorMode.Idle) {
    moudeEditorModeIdle.onDown(event);
  } else if (store.ui.mode === EditorMode.SelectOrMove) {
    moudeEditorModeSelectOrMove.onDown(event);
  } else if (store.ui.mode === EditorMode.DeleteBeads) {
    moudeEditorModeDelete.onDown(event);
  } else if (store.ui.mode === EditorMode.PaintBeads) {
    moudeEditorModePaint.onDown(event);
  } else if (store.ui.mode === EditorMode.AddRegion) {
    moudeEditorModeAddRegion.onDown(event);
  } else if (store.ui.mode === EditorMode.AddBeads) {
    moudeEditorModeAddBead.onDown(event);
  } else if (store.ui.mode === EditorMode.SelectMultiple) {
    moudeEditorModeSelectMultiple.onDown(event);
  }
}

function onPointerUp(event: MouseEvent) {
  const store = getStore();
  if (store.ui.mode === EditorMode.AddBeads) {
    moudeEditorModeAddBead.onUp(event);
  } else if (store.ui.mode === EditorMode.Idle) {
    moudeEditorModeIdle.onUp(event);
  } else if (store.ui.mode === EditorMode.SelectOrMove) {
    moudeEditorModeSelectOrMove.onUp(event);
  } else if (store.ui.mode === EditorMode.DeleteBeads) {
    moudeEditorModeDelete.onUp(event);
  } else if (store.ui.mode === EditorMode.PaintBeads) {
    moudeEditorModePaint.onUp(event);
  } else if (store.ui.mode === EditorMode.AddRegion) {
    moudeEditorModeAddRegion.onUp(event);
  } else if (store.ui.mode === EditorMode.SelectMultiple) {
    moudeEditorModeSelectMultiple.onUp(event);
  }
}

function onPointerOut(event: MouseEvent) {
  setMousePositionAtWindow(undefined, undefined);
  onPointerUp(event);
}

function onPointerMove(event: MouseEvent) {
  setMousePositionAtWindow(event.clientX, event.clientY);

  const store = getStore();

  if (store.ui.mode === EditorMode.Idle) {
    moudeEditorModeIdle.onMove(event);
  } else if (store.ui.mode === EditorMode.SelectOrMove) {
    moudeEditorModeSelectOrMove.onMove(event);
  } else if (store.ui.mode === EditorMode.PaintBeads) {
    moudeEditorModePaint.onMove(event);
  } else if (store.ui.mode === EditorMode.AddRegion) {
    moudeEditorModeAddRegion.onMove(event);
  } else if (store.ui.mode === EditorMode.DeleteBeads) {
    moudeEditorModeDelete.onMove(event);
  } else if (store.ui.mode === EditorMode.AddBeads) {
    moudeEditorModeAddBead.onMove(event);
  } else if (store.ui.mode === EditorMode.SelectMultiple) {
    moudeEditorModeSelectMultiple.onMove(event);
  }
}
