import * as THREE from "three";
import {
  ChangeSelectObjects,
  AppEventType,
} from "../../../controllers/AppEventType";
import { getStore } from "../../../controllers/store";
import { sendEvent } from "../../../controllers/storeController";
import { AppObject, ObjectTypes, Region } from "../../../editorObjects/Editor";
import { logger } from "../../../logger";
import { isPressedKey } from "../../keyboard/keyMap";
import { appThree } from "../../renderThreejs";
import { MouseDragHelper } from "../MouseDragHelper";
import { MouseSelectionHelper } from "../MouseSelectionHelper";
import { screenToWorld } from "../screenToWorld";
import { SelectionFilter } from "../SelectionFilter";
import { MouseEventsBase } from "./MouseEventsBase";
import { MouseEventsIdle } from "./MouseEventsIdle";

enum ActionType {
  moveCamera = "moveCamera",
  selectMultipleObjects = "selectMultipleObjects",
  deselectMultipleObjects = "deselectMultipleObjects",
}

export class MouseEventsEditorModeSelectMultiple extends MouseEventsBase {
  actionType: ActionType | undefined;
  isCameraGrabed: boolean;
  startDragMousePosition: THREE.Vector2;
  startDragCamera: THREE.Vector2;
  boundingClientRect: DOMRect | undefined;
  mouseDragHelper: MouseDragHelper;
  mouse: THREE.Vector2;
  raycaster: THREE.Raycaster;
  selectionBox: SelectionFilter;
  mouseSelectionHelper: MouseSelectionHelper;
  prevEditBBox: any | undefined;
  mouseEventsIdle: MouseEventsIdle;

  constructor() {
    super();
    this.boundingClientRect = undefined;
    this.isCameraGrabed = false;
    this.startDragMousePosition = new THREE.Vector2();
    this.startDragCamera = new THREE.Vector2();
    this.mouseDragHelper = new MouseDragHelper();
    this.mouse = new THREE.Vector2();
    this.raycaster = new THREE.Raycaster();

    this.mouseSelectionHelper = new MouseSelectionHelper();
    this.mouseSelectionHelper.setRenderer(appThree.renderer);

    this.selectionBox = new SelectionFilter();
    this.selectionBox.setCamera(appThree.camera);
    this.selectionBox.setScene(appThree.scene);

    this.mouseEventsIdle = new MouseEventsIdle();
    this.actionType = undefined;
  }

  onWheel(event: PointerEvent) {}

  onResize(event: PointerEvent) {}

  onDown(event: PointerEvent) {
    if (isPressedKey("space")) {
      this.actionType = ActionType.moveCamera;
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onDown(x, y);
      this.mouseEventsIdle.onDown(event);
      logger.trace(
        "MouseEventsEditorModeSelectOrMove: onDown action type",
        this.actionType
      );
      return;
    }

    if (event.altKey) {
      this.actionType = ActionType.deselectMultipleObjects;
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onDown(x, y);
      if (appThree.el !== null) {
        this.boundingClientRect = appThree.el.getBoundingClientRect();
      }
      if (this.boundingClientRect === undefined) {
        return;
      }
      const { top, left, width, height } = this.boundingClientRect;
      const screenX = event.clientX - left;
      const screenY = event.clientY - top;

      this.mouseSelectionHelper.onDown(screenX, screenY);

      this.selectionBox.startPoint.set(
        ((event.clientX - left) / width) * 2 - 1,
        -((event.clientY - top) / height) * 2 + 1,
        0
      );
      logger.trace(
        "MouseEventsEditorModeSelectOrMove: onDown action type",
        this.actionType
      );
      return;
    }

    this.actionType = ActionType.selectMultipleObjects;
    const { x, y } = screenToWorld(event.clientX, event.clientY);
    this.mouseDragHelper.onDown(x, y);
    if (appThree.el !== null) {
      this.boundingClientRect = appThree.el.getBoundingClientRect();
    }
    if (this.boundingClientRect === undefined) {
      return;
    }
    const { top, left, width, height } = this.boundingClientRect;
    const screenX = event.clientX - left;
    const screenY = event.clientY - top;

    this.mouseSelectionHelper.onDown(screenX, screenY);

    this.selectionBox.startPoint.set(
      ((event.clientX - left) / width) * 2 - 1,
      -((event.clientY - top) / height) * 2 + 1,
      0
    );
    logger.trace(
      "MouseEventsEditorModeSelectOrMove: onDown action type",
      this.actionType
    );
  }

  onMove(event: PointerEvent) {
    if (this.actionType === undefined) {
      return;
    }

    if (this.actionType === ActionType.moveCamera) {
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onMove(x, y);
      this.mouseEventsIdle.onMove(event);
      this.updateUIAtMove(false);
    }

    if (this.actionType === ActionType.selectMultipleObjects) {
      this.selectMultipleObjects(event.clientX, event.clientY, true);
    }

    if (this.actionType === ActionType.deselectMultipleObjects) {
      this.selectMultipleObjects(event.clientX, event.clientY, false);
    }
  }

  onUp(event: PointerEvent) {
    if (this.actionType === ActionType.selectMultipleObjects) {
      if (this.mouseDragHelper.isDragged === false) {
        this.intersects = this.getActiveObjectsFromIntersect(
          event.clientX,
          event.clientY
        );
        const firstClickedActiveObject = this.intersects.length > 0;
        if (firstClickedActiveObject) {
          const clicked = this.intersects[0].object.userData as AppObject;
          const clickedId = clicked.id;
          if (clickedId !== undefined) {
            const store = getStore();
            const clickedObjectIsLocked =
              store.objects[clickedId] !== undefined
                ? store.objects[clickedId].isLocked === true
                : false;
            let selectedIsLocked: any = false;
            store.selectedIds.forEach((id) => {
              if (store.objects[id].isLocked === true) {
                selectedIsLocked = true;
              }
            });
            if (selectedIsLocked === true) {
            } else if (clickedObjectIsLocked === false) {
              sendEvent(
                this.getSelectionAppendAction([clickedId], undefined, false)
              );
            }
          }
        }
        this.intersects = [];
      } else {
        if (this.boundingClientRect !== undefined) {
          const { top, left, width, height } = this.boundingClientRect;
          this.selectionBox.endPoint.set(
            ((event.clientX - left) / width) * 2 - 1,
            -((event.clientY - top) / height) * 2 + 1,
            1
          );
          const store = getStore();
          let selectedIsLocked: any = false;
          store.selectedIds.forEach((id) => {
            if (store.objects[id].isLocked === true) {
              selectedIsLocked = true;
            }
          });
          if (selectedIsLocked === true) {
          } else {
            const allSelectedIds = this.selectionBox.selectIds();

            sendEvent(
              this.getSelectionAppendAction(allSelectedIds, true, false)
            );
          }
        }
      }
      this.mouseDragHelper.onUp();
      this.mouseSelectionHelper.onUp();
    }

    if (this.actionType === ActionType.deselectMultipleObjects) {
      if (isPressedKey("altleft") || isPressedKey("altright")) {
        if (this.mouseDragHelper.isDragged === false) {
          this.intersects = this.getActiveObjectsFromIntersect(
            event.clientX,
            event.clientY
          );
          const firstClickedActiveObject = this.intersects.length > 0;
          if (firstClickedActiveObject) {
            const clicked = this.intersects[0].object.userData as AppObject;
            const clickedId = clicked.id;
            if (clickedId !== undefined) {
              sendEvent(
                this.getSelectionAppendAction([clickedId], false, true)
              );
            }
          }

          this.intersects = [];
        } else {
          if (this.boundingClientRect !== undefined) {
            const { top, left, width, height } = this.boundingClientRect;
            this.selectionBox.endPoint.set(
              ((event.clientX - left) / width) * 2 - 1,
              -((event.clientY - top) / height) * 2 + 1,
              1
            );
            const allSelectedIds = this.selectionBox.selectIds();

            sendEvent(
              this.getSelectionAppendAction(allSelectedIds, false, false)
            );
          }
        }
      }

      this.mouseDragHelper.onUp();
      this.mouseSelectionHelper.onUp();
    }
  }

  getSelectionAppendAction(
    objectIds: string[],
    _newIsSelected?: boolean,
    _addWithIsLock?: boolean
  ) {
    const store = getStore();
    const change: ChangeSelectObjects["payload"]["change"] = [];
    const addWithIsLock = _addWithIsLock ?? false;

    objectIds.forEach((objectId) => {
      const isLocked =
        store.objects[objectId] !== undefined &&
        store.objects[objectId].isLocked === true;
      if (
        addWithIsLock === true ||
        (addWithIsLock === false && isLocked === false)
      ) {
        const isSelected = store.selectedIds.includes(objectId) === true;
        const newSelected =
          _newIsSelected === undefined ? !isSelected : _newIsSelected;
        const childrenIds = this.getIdsFromGroupOrInGroup(objectId);
        change.push({
          id: objectId,
          isSelected: newSelected,
        });
        childrenIds.forEach((id) => {
          change.push({
            id: id,
            isSelected: newSelected,
          });
        });
      }
    });

    const action: ChangeSelectObjects = {
      name: AppEventType.ChangeSelectObjects,
      payload: {
        change,
      },
    };

    return action;
  }

  selectMultipleObjects(
    clientX: number,
    clientY: number,
    shouldBeSelected: boolean
  ) {
    const { x, y } = screenToWorld(clientX, clientY);
    this.mouseDragHelper.onMove(x, y);
    if (this.boundingClientRect === undefined) {
      return;
    }
    const { top, left } = this.boundingClientRect;
    const screenX = clientX - left;
    const screenY = clientY - top;
    this.mouseSelectionHelper.onMove(screenX, screenY);
  }

  moveSingleObject(clientX: number, clientY: number) {
    const store = getStore();

    const { x, y } = screenToWorld(clientX, clientY);
    this.mouseDragHelper.onMove(x, y);

    const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
    const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

    const clicked = this.intersects[0].object.userData;
    const objectId = clicked.id;
    if (objectId === undefined) {
      logger.warn("objectId not found in clicked item", clicked);
      return;
    }

    const object = appThree.objects.get(objectId);
    if (object === undefined) {
      logger.warn("object not found in app state for id", objectId, clicked);
      return;
    }

    if (store.objects[objectId].objectType === ObjectTypes.Region) {
      const region = store.objects[objectId] as Region;
      if (region.snapBeadsToCell === true) {
        if (region.childrenGridIds.length > 0) {
          region.childrenGridIds.forEach((id) => {
            if (id !== undefined && store.objects[id] !== undefined) {
              const bead = store.objects[id];
              if (bead === undefined) {
                logger.warn("object not exists in app", objectId);
                return;
              }
              const child_object = appThree.objects.get(bead.id);
              if (child_object === undefined) {
                logger.warn("object not found in app scene for id", objectId);
                return;
              }
              child_object.position.x = store.objects[id].position.x + diffX;
              child_object.position.y = store.objects[id].position.y + diffY;
            }
          });
        }
      }
    }

    object.position.x = store.objects[objectId].position.x + diffX;
    object.position.y = store.objects[objectId].position.y + diffY;

    this.updateUIAtMove(false);
  }

  updateUIAtMove(visible: boolean) {
    if (visible === false) {
      this.hideTopBar();
    } else {
      this.showTopBar();
    }
  }

  hideTopBar() {
    const selector = ".selected-objects-top-bar";
    const topBar = document.querySelector(selector) as HTMLDivElement | null;
    if (topBar === null) {
      return;
    }
    topBar.style.display = "none";
  }

  showTopBar() {
    const selector = ".selected-objects-top-bar";
    const topBar = document.querySelector(selector) as HTMLDivElement | null;
    if (topBar === null) {
      return;
    }
    topBar.style.display = "block";
  }
}
