import { AppObject } from "../../editorObjects/Editor";
import { AppEventType, RemoveObjects } from "../AppEventType";
import { setStore, shouldRefreshReact, shouldRefreshThree } from "../store";
import { RemoveObjectsHistory } from "../AppHistory";
import { triggerDeleteObjectIds } from "../../threejs/eventEmitterHandlers";
import { refreshReactState } from "../refreshReactState";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import { notifyBackend, SaveEventType } from "../../ApiService";
import { formatISO } from "date-fns";

export class RemoveObjectHandler extends StoreObjectHandler<RemoveObjects> {
  run() {
    const { ids } = this.event.payload;
    const objects: AppObject[] = [];
    let newStore = this.deleteSelectedObjectsFromStore(ids, objects);

    if (this.saveToHistory) {
      const historyEvent: RemoveObjectsHistory = {
        do: this.event,
        redo: this.event,
        undo: {
          name: AppEventType.AddObjects,
          payload: { objects },
        },
        createdAt: formatISO(new Date()),
      };
      this.history.push(historyEvent);
    }
    newStore = this.getStoreWithHistory(newStore);
    setStore(newStore);

    if (shouldRefreshThree()) {
      triggerDeleteObjectIds(
        objects.map((obj) => {
          return { id: obj.id, objectType: obj.objectType };
        })
      );
    }

    if (shouldRefreshReact()) {
      refreshReactState(newStore);
    }

    notifyBackend(
      objects.map((object) => {
        return {
          action: SaveEventType.DELETE,
          createdAt: new Date().toISOString(),
          object,
        };
      })
    );
  }
}
