import { produce } from "immer";
import * as THREE from "three";
import { buildBead } from "../../editorObjects/Bead";
import {
  AppObject,
  Bead,
  Group,
  groupIdKey,
  ObjectTypes,
  Region,
} from "../../editorObjects/Editor";
import { buildGroup } from "../../editorObjects/Group";
import { buildPreviewProject3D } from "../../editorObjects/PreviewProject3D";
import { buildRegion } from "../../editorObjects/Region";
import { getTopBarPosition } from "../../threejs/eventEmitterHandlers";
import { screenToWorld } from "../../threejs/mouseListeners/screenToWorld";
import { getSelectBBox } from "../../threejs/SelectBBox";
import { AppEvents } from "../AppEventType";
import { BaseEventHandler } from "./BaseEventHandler";

export interface IBaseEventHandler {
  run(refreshThreejs: boolean, refreshReact: boolean): void;
}
export class StoreObjectHandler<T extends AppEvents>
  extends BaseEventHandler<T>
  implements IBaseEventHandler {
  run(refreshThreejs: boolean = true, refreshReact: boolean = true) {}

  getAppObjectFromCloneAppObject(object: Partial<AppObject>) {
    if (object.objectType === undefined) {
      console.warn("object type not found in payload: ", object);
      return undefined;
    }
    if (object.objectType === ObjectTypes.Bead) {
      return buildBead(object);
    }
    if (object.objectType === ObjectTypes.Region) {
      return buildRegion(object);
    }
    if (object.objectType === ObjectTypes.Group) {
      return buildGroup(object);
    }
    if (object.objectType === ObjectTypes.PreviewProject3D) {
      return buildPreviewProject3D(object);
    }
    console.warn("unknow object type from payload: ", object);
    return undefined;
  }

  selectObjectOnThreeScene(object: AppObject, isSelected: boolean) {
    if (object === undefined) {
      return;
    }

    getSelectBBox().updateRect(
      {
        depth: object.depth,
        height: object.height,
        width: object.width,
        id: object.id,
        position: new THREE.Vector3(
          object.position.x,
          object.position.y,
          object.position.z
        ),
        rotation: new THREE.Vector3(
          object.rotateX,
          object.rotateY,
          object.rotateZ
        ),
      },
      isSelected
    );

    // if (object.objectType === ObjectTypes.Bead) {
    //   triggerSelectBead(object.id, isSelected);
    // }
    // if (object.objectType === ObjectTypes.Region) {
    //   triggerSelectRegion(object.id, isSelected);
    // }
  }

  deleteSelectedObjectsFromStore(ids: string[], objects: AppObject[]) {
    return produce(this.store, (draft) => {
      ids.forEach((id) => {
        const object = this.store.objects[id];
        if (draft.selectedIds.includes(id)) {
          const index = draft.selectedIds.indexOf(id);
          draft.selectedIds.splice(index, 1);
          this.selectObjectOnThreeScene(object, false);
        }

        if (draft.objects[object.id] !== undefined) {
          objects.push(object);
          if (object.objectType === ObjectTypes.Region) {
            (object as Region).childrenGridIds.forEach((_beadId) => {
              if (_beadId !== undefined) {
                (draft.objects[_beadId] as Bead).regionId = undefined;
              }
            });
          }
          delete draft.objects[object.id];
        }
      });

      draft.ui.selectedTopBarPosition = getTopBarPosition(
        draft.selectedIds.map((id) => {
          return { id, objectType: draft.objects[id].objectType };
        })
      );
    });
  }

  getPositionDiffFromMousePosition(objects: AppObject[]) {
    const { posX, posY } = this.getMousePosition();
    const worldPos = screenToWorld(posX, posY);
    const bbox = new THREE.Box3();
    objects.forEach((object) => {
      const w = object.width / 2;
      const h = object.height / 2;
      const x = object.position.x;
      const y = object.position.y;
      bbox.expandByPoint(new THREE.Vector3().set(x - w, y - h, 0.1));
      bbox.expandByPoint(new THREE.Vector3().set(x + w, y - h, 0.2));
      bbox.expandByPoint(new THREE.Vector3().set(x - w, y + h, 0.1));
      bbox.expandByPoint(new THREE.Vector3().set(x + w, y + h, 0.1));
    });
    const bboxCenter = new THREE.Vector3();
    bbox.getCenter(bboxCenter);
    const diffX = worldPos.x - bboxCenter.x;
    const diffY = worldPos.y - bboxCenter.y;
    return {
      diffX,
      diffY,
    };
  }

  moveObjectsByDiff(objects: AppObject[], diffX: number, diffY: number) {
    objects.forEach((object) => {
      if (object.objectType === ObjectTypes.Group) {
        object.position.x = object.position.x + diffX;
        object.position.y = object.position.y + diffY;
        objects.forEach((obj) => {
          if ((object as Group).childrenIds.includes(obj.id)) {
            obj.position.x = obj.position.x + diffX;
            obj.position.y = obj.position.y + diffY;
          }
        });
      } else {
        if (object[groupIdKey] === undefined) {
          object.position.x = object.position.x + diffX;
          object.position.y = object.position.y + diffY;
        }
      }
    });
  }
}
