import React from "react";
import { Bead, Editor, ObjectTypes } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronDown,
  faChevronUp,
  faSearch,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";
import { getStore, setStore, StoreUiSettings } from "../controllers/store";
import produce from "immer";
import { refreshReactState } from "../controllers/refreshReactState";
import styled from "styled-components";
import { BeadResponse, fetchBeadProducts } from "../ApiService";
import { thumbnailsGenerator } from "../threejs/thumbnailsGenerator";
import { sendEvent } from "../controllers/storeController";
import { AppEventType } from "../controllers/AppEventType";
import { useTranslation } from "react-i18next";

export const ModalSearchProducts: React.FC<{}> = () => {
  const modalSearchProducts = useSelector((state: Editor) => {
    return state.ui.modalSearchProducts;
  });

  if (!modalSearchProducts) {
    return <React.Fragment />;
  }
  if (modalSearchProducts.isOpen === false) {
    return <React.Fragment />;
  }
  return <Content modalSearchProducts={modalSearchProducts} />;
};

const Content: React.FC<{
  modalSearchProducts: StoreUiSettings["modalSearchProducts"];
}> = (props) => {
  const productsRef = React.useRef<null | HTMLDivElement>(null);
  const [q, setQ] = React.useState(props.modalSearchProducts?.q ?? "");
  const [producer, setProducer] = React.useState(
    props.modalSearchProducts?.producer ?? ""
  );
  const [color, setColor] = React.useState(
    props.modalSearchProducts?.color ?? ""
  );

  type DataType = { bead: Bead; item: BeadResponse };
  const [advBlockVisible, setAdvBlockVisible] = React.useState<boolean>(
    props.modalSearchProducts?.advBlockVisible ?? false
  );
  const [loading, setLoading] = React.useState<undefined | boolean>(undefined);
  const [error, setError] = React.useState<undefined | string>(undefined);
  const [data, setData] = React.useState<undefined | DataType[]>(
    props.modalSearchProducts.cachedResponse ?? []
  );

  const onClickCancel = () => {
    const store = getStore();
    const newStore = produce(store, (draft) => {
      if (draft.ui.modalSearchProducts) {
        draft.ui.modalSearchProducts.isOpen = false;
        draft.ui.modalSearchProducts.q = q;
        draft.ui.modalSearchProducts.producer = producer;
        draft.ui.modalSearchProducts.color = color;
        draft.ui.modalSearchProducts.advBlockVisible = advBlockVisible;
      }
      if (productsRef && productsRef.current) {
        draft.ui.modalSearchProducts.scrollPosition =
          productsRef.current.scrollTop;
      }
    });
    setStore(newStore);
    refreshReactState(newStore);
  };
  const onStartSearch = (_q?: string, _producer?: string, _color?: string) => {
    if (loading === true) {
      return;
    }
    setLoading(true);
    fetchBeadProducts(_q ?? q, _producer ?? producer, _color ?? color)
      .then(({ response }) => {
        setData(response);
        setLoading(undefined);
        setError(undefined);
      })
      .catch((err) => {
        setLoading(undefined);
        setError(err);
      });
  };
  const toggleAdvBlock = () => {
    setAdvBlockVisible(!advBlockVisible);
  };
  const onSelectProduct = (item: DataType) => {
    const store = getStore();
    const newStore = produce(store, (draft) => {
      if (draft.ui.modalSearchProducts) {
        draft.ui.modalSearchProducts.isOpen = false;
        draft.ui.modalSearchProducts.q = q;
        draft.ui.modalSearchProducts.producer = producer;
        draft.ui.modalSearchProducts.color = color;
        draft.ui.modalSearchProducts.advBlockVisible = advBlockVisible;
        if (data) {
          draft.ui.modalSearchProducts.cachedResponse = data;
        }
        if (productsRef && productsRef.current) {
          draft.ui.modalSearchProducts.scrollPosition =
            productsRef.current.scrollTop;
        }
      }
    });
    setStore(newStore);
    refreshReactState(newStore);
    sendEvent({
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: newStore.selectedIds
          .filter((id) => {
            if (!newStore.objects[id]) {
              return false;
            }
            if (newStore.objects[id].objectType !== ObjectTypes.Bead) {
              return false;
            }
            return true;
          })
          .map((id) => {
            const { height, width, depth, type, color, material } = item.bead;
            return {
              id,
              properties: {
                height,
                width,
                depth,
                type,
                color,
                material,
              },
            };
          }),
      },
    });
  };
  const onClickClearQ = () => {
    setQ("");
    triggerSearch("");
  };

  const onClickClearProducer = () => {
    setProducer("");
    triggerSearch(undefined, "");
  };

  const onClickClearColor = () => {
    setColor("");
    triggerSearch(undefined, undefined, "");
  };

  const onClickSearch = () => {
    triggerSearch();
  };

  const triggerSearch = (_q?: string, _producer?: string, _color?: string) => {
    onStartSearch(_q, _producer, _color);
  };

  //   React.useEffect(() => {
  //     verify();
  //   }, [q]);

  //   const verify = React.useCallback(
  //     debounce(() => {
  //       onStartSearch();
  //     }, 500),
  //     []
  //   );

  const onClickBeadProducer = (search: string) => {
    if (producer === search) {
      setProducer("");
      triggerSearch(q, search, color);
    } else {
      setProducer(search);
      triggerSearch(q, search, color);
    }
  };

  const onClickBeadColorCategory = (search: string) => {
    if (color === search) {
      setColor("");
      triggerSearch(q, producer, search);
    } else {
      setColor(search);
      triggerSearch(q, producer, search);
    }
  };
  const [t] = useTranslation(["board", "translation"]);

  const bead_colors_categories = [
    { search: "czarny", hex: "black" },
    { search: "niebieski", hex: "blue" },
    { search: "zielony", hex: "green" },
    { search: "czerwony", hex: "red" },
    { search: "pomarańczowy", hex: "orange" },
    { search: "brazowy", hex: "brown" },
    { search: "fioletowy", hex: "purple" },
    { search: "złoty", hex: "gold" },
    { search: "różowy", hex: "pink" },
    { search: "żółty", hex: "yellow" },
    { search: "srebrny", hex: "#eee" },
  ];

  return (
    <ModalSearchProductsStyles>
      <div className={"modal-search-products-popup"}>
        <div
          className={"modal-search-products-popup-overlay"}
          onClick={onClickCancel}
        >
          &nbsp;
        </div>
        <div className={"modal-search-products-popup-content-wrapper"}>
          <div className={"modal-search-products-popup-content"}>
            <div className={"btn-cancel"} onClick={onClickCancel}>
              <FontAwesomeIcon icon={faTimes} color={"#000"} />
            </div>

            <div className="inputs-wrapper">
              <div className="simple-inputs-wrapper">
                <div className={"one-input-wrapper"}>
                  <input
                    name={"q"}
                    className={"input"}
                    placeholder={t(
                      "board:modal_search_bead.search_input_placeholder"
                    )}
                    autoComplete={"off"}
                    onKeyPress={(event: any) => {
                      if (event.key === "Enter") {
                        triggerSearch();
                      }
                    }}
                    value={q}
                    onChange={(event) => {
                      setQ(event.target.value);
                    }}
                  />

                  {q.length > 0 ? (
                    <div className={"clear-input"} onClick={onClickClearQ}>
                      <FontAwesomeIcon icon={faTimes} />
                    </div>
                  ) : null}
                </div>
                <button onClick={onClickSearch}>
                  <FontAwesomeIcon icon={faSearch} />
                </button>
                <button onClick={toggleAdvBlock}>
                  {advBlockVisible === true
                    ? t("board:modal_search_bead.close_advanced_filters")
                    : t("board:modal_search_bead.open_advanced_filters")}{" "}
                  <FontAwesomeIcon
                    icon={
                      advBlockVisible === true ? faChevronUp : faChevronDown
                    }
                  />
                </button>
              </div>

              {advBlockVisible === true ? (
                <div className="adv-wrapper">
                  <div>
                    <div className={"bead-colors-category-wrapper"}>
                      {bead_colors_categories.map((category) => {
                        return (
                          <BeadColorCategory
                            key={category.search}
                            search={category.search}
                            hex={category.hex}
                            onClick={onClickBeadColorCategory}
                            isSelected={color === category.search}
                          />
                        );
                      })}
                      {color.length > 0 ? (
                        <div
                          className={"clear-input"}
                          onClick={onClickClearColor}
                        >
                          <FontAwesomeIcon icon={faTimes} />
                        </div>
                      ) : null}
                    </div>
                  </div>

                  <div className={"bead-producers-wrapper"}>
                    <ProducerItem
                      label={"TOHO"}
                      value={"toho"}
                      isSelected={producer === "toho"}
                      onClick={onClickBeadProducer}
                    />
                    <ProducerItem
                      label={"MIYUKI"}
                      value={"miyuki"}
                      isSelected={producer === "miyuki"}
                      onClick={onClickBeadProducer}
                    />
                    <ProducerItem
                      label={"FirePolish"}
                      value={"firepolish"}
                      onClick={onClickBeadProducer}
                      isSelected={producer === "firepolish"}
                    />

                    {producer.length > 0 ? (
                      <div
                        className={"clear-input"}
                        onClick={onClickClearProducer}
                      >
                        <FontAwesomeIcon icon={faTimes} />
                      </div>
                    ) : null}
                  </div>
                </div>
              ) : null}
            </div>
            <div className={"content-wrapper"}>
              {loading ? (
                <div>{t("translation:query_loading")}loading...</div>
              ) : null}
              {error ? (
                <div>
                  {t("translation:query_error")}error: {error}
                </div>
              ) : null}
              {data ? (
                <div className={"products"} ref={productsRef}>
                  {data.map((item) => {
                    const onClick = () => {
                      onSelectProduct(item);
                    };
                    return (
                      <div
                        key={item.item.bead_product_uuid}
                        className={"item"}
                        onClick={onClick}
                      >
                        <div className={"img"}>
                          <img
                            src={thumbnailsGenerator.getObjectThumbnail(
                              item.bead,
                              100,
                              100
                            )}
                            alt={""}
                          />
                        </div>
                        <h2>{item.item.display_name}</h2>
                      </div>
                    );
                  })}
                </div>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </ModalSearchProductsStyles>
  );
};

const ProducerItem: React.FC<{
  value: string;
  label: string;
  onClick: any;
  isSelected: boolean;
}> = (props) => {
  const onClick = () => {
    props.onClick(props.value);
  };
  return (
    <div
      className={`bead-producer-item ${props.isSelected ? "selected" : ""}`}
      onClick={onClick}
    >
      {props.label}
    </div>
  );
};

const BeadColorCategory: React.FC<{
  search: string;
  hex: string;
  onClick: any;
  isSelected: boolean;
}> = (props) => {
  const onClick = () => {
    props.onClick(props.search);
  };
  return (
    <div
      className={`bead-color-category-item ${
        props.isSelected ? "selected" : ""
      }`}
      onClick={onClick}
      style={{ backgroundColor: props.hex }}
    >
      &nbsp;
    </div>
  );
};

