import {
  Bead,
  BeadMaterialTypes,
  BeadTypes,
  NAME_SEPARATOR,
  ObjectTypes,
} from "./Editor";
import { DeepPartial } from "ts-essentials";
import { buildPoint } from "./Point";
import { merge } from "lodash";
import { defaulBeadColor } from "../components/colors";
import * as THREE from "three";

export enum BeadChildSuffix {
  MAIN = "MAIN",
  ANY = "beadChildOf",
  SELECTION = "SELECTION",
  SELECTION_CHILD = "SELECTION_CHILD",
}

export const getThreeNameFromBeadId = (
  id: string,
  childSuffix: BeadChildSuffix = BeadChildSuffix.MAIN
) => {
  return `${ObjectTypes.Bead}${NAME_SEPARATOR}${id}${NAME_SEPARATOR}${childSuffix}`;
};

export const getBeadIdFromThreeName = (name?: string) => {
  if (!name) {
    return undefined;
  }
  if (name.includes(ObjectTypes.Bead) === false) {
    return undefined;
  }
  return name.split(NAME_SEPARATOR)[1];
};

export const getThreeUserDataFromBead = (
  bead: Bead,
  childSuffix?: BeadChildSuffix
) => {
  return merge({}, bead, {
    name: getThreeNameFromBeadId(bead.id, childSuffix),
  });
};

// let index = 0;
// const names = "ABCDEFGHIJKLMNOPRSTQWYZ";

export const buildBead = (props: DeepPartial<Bead>): Bead => {
  // if (props.id === undefined && names[index] !== undefined) {
  //   props.id = names[index];
  //   index = index + 1;
  // }
  const defaults: Bead = {
    id: THREE.MathUtils.generateUUID(),
    position: buildPoint(0, 0),
    type: BeadTypes.MODEL_TOHO_ROUND,
    material: BeadMaterialTypes.Mat,
    rotateX: 0,
    rotateY: 0,
    rotateZ: 0,
    lineRotate: 0,
    color: defaulBeadColor,
    height: 10,
    width: 10,
    depth: 10,
    keepAspectRatio: false,
    canLockOrUnlock: true,
    isLocked: false,
    objectType: ObjectTypes.Bead,
    // [immerable]: true,
    groupId: undefined,
    regionId: undefined,
  };

  return merge(defaults, props);
};
