import { produce } from "immer";
import {
  AppEventType,
  ChangeObjectsProperty,
  ChangeSelectObjects,
} from "../../../controllers/AppEventType";
import { refreshReactState } from "../../../controllers/refreshReactState";
import { getStore, setStore } from "../../../controllers/store";
import { sendEvent } from "../../../controllers/storeController";
import {
  AppObject,
  Group,
  groupIdKey,
  ObjectTypes,
  Region,
} from "../../../editorObjects/Editor";
import { logger } from "../../../logger";
import { getTopBarPosition } from "../../eventEmitterHandlers";
import { getKeysPressed, isPressedKey } from "../../keyboard/keyMap";
import { appThree } from "../../renderThreejs";
import { getSelectBBox } from "../../SelectBBox";
import { MouseDragHelper } from "../MouseDragHelper";
import { MouseSelectionHelper } from "../MouseSelectionHelper";
import { screenToWorld } from "../screenToWorld";
import { SelectionFilter } from "../SelectionFilter";
import { MouseEventsBase } from "./MouseEventsBase";
import { MouseEventsIdle } from "./MouseEventsIdle";

enum ActionType {
  moveCamera = "moveCamera",
  moveAllSelectedObjects = "moveAllSelectedObjects",
  moveSelectBBox = "moveSelectBBox",
  editSelectBBox = "editSelectBBox",
  moveObjectGroup = "moveObjectGroup",
  moveSingleObject = "moveSingleObject",
  selectMultipleObjects = "selectMultipleObjects",
  deselectMultipleObjects = "deselectMultipleObjects",
}

export class MouseEventsEditorModeSelectOrMove extends MouseEventsBase {
  actionType: ActionType | undefined;
  mouseDragHelper: MouseDragHelper;
  selectionBox: SelectionFilter;
  mouseSelectionHelper: MouseSelectionHelper;
  prevEditBBox: any | undefined;
  mouseEventsIdle: MouseEventsIdle;

  constructor() {
    super();
    this.mouseDragHelper = new MouseDragHelper();

    this.mouseSelectionHelper = new MouseSelectionHelper();
    this.mouseSelectionHelper.setRenderer(appThree.renderer);

    this.selectionBox = new SelectionFilter();
    this.selectionBox.setCamera(appThree.camera);
    this.selectionBox.setScene(appThree.scene);

    this.mouseEventsIdle = new MouseEventsIdle();
    this.actionType = undefined;
  }

  onWheel(event: PointerEvent) {}

  onResize(event: PointerEvent) {}

  onDown(event: PointerEvent) {
    logger.trace(
      "MouseEventsEditorModeSelectOrMove: jak z kliknieciem???",
      getKeysPressed(),
      isPressedKey("space")
    );

    if (isPressedKey("space")) {
      this.actionType = ActionType.moveCamera;
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onDown(x, y);
      this.mouseEventsIdle.onDown(event);
      logger.trace(
        "MouseEventsEditorModeSelectOrMove: onDown action type",
        this.actionType
      );
      return;
    }

    if (event.altKey) {
      this.actionType = ActionType.deselectMultipleObjects;
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onDown(x, y);
      if (appThree.el !== null) {
        this.boundingClientRect = appThree.el.getBoundingClientRect();
      }
      if (this.boundingClientRect === undefined) {
        return;
      }
      const { top, left, width, height } = this.boundingClientRect;
      const screenX = event.clientX - left;
      const screenY = event.clientY - top;

      this.mouseSelectionHelper.onDown(screenX, screenY);

      this.selectionBox.startPoint.set(
        ((event.clientX - left) / width) * 2 - 1,
        -((event.clientY - top) / height) * 2 + 1,
        0
      );
      logger.trace(
        "MouseEventsEditorModeSelectOrMove: onDown action type",
        this.actionType
      );
      return;
    }

    if (event.shiftKey) {
      this.actionType = ActionType.selectMultipleObjects;
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onDown(x, y);
      if (appThree.el !== null) {
        this.boundingClientRect = appThree.el.getBoundingClientRect();
      }
      if (this.boundingClientRect === undefined) {
        return;
      }
      const { top, left, width, height } = this.boundingClientRect;
      const screenX = event.clientX - left;
      const screenY = event.clientY - top;

      this.mouseSelectionHelper.onDown(screenX, screenY);

      this.selectionBox.startPoint.set(
        ((event.clientX - left) / width) * 2 - 1,
        -((event.clientY - top) / height) * 2 + 1,
        0
      );
      logger.trace(
        "MouseEventsEditorModeSelectOrMove: onDown action type",
        this.actionType
      );
      return;
    }

    this.actionType = undefined;

    const intersects = this.getIntersect(event.clientX, event.clientY);
    const selectBBox = getSelectBBox();

    if (selectBBox.isHandle(intersects)) {
      this.actionType = ActionType.editSelectBBox;
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onDown(x, y);
      selectBBox.onDownEdit(intersects);
      return;
    }

    this.intersects = intersects.filter((obj) => {
      const data = obj.object.userData;
      const objectType = data?.objectType ?? undefined;
      return (
        objectType === ObjectTypes.Bead ||
        objectType === ObjectTypes.Region ||
        objectType === ObjectTypes.Group ||
        objectType === ObjectTypes.PreviewProject3D
      );
    });

    const firstClickedActiveObject = this.intersects.length > 0;

    if (firstClickedActiveObject) {
      const store = getStore();
      const clicked = this.intersects[0].object.userData as AppObject;
      const clickedId = clicked.id;
      const clickedItemInSelectedObjects = store.selectedIds.includes(
        clickedId
      );

      let selectedIsLocked: any = false;
      store.selectedIds.forEach((id) => {
        if (store.objects[id].isLocked === true) {
          selectedIsLocked = true;
        }
      });

      if (clicked.isLocked === true) {
        sendEvent(this.getSelectionForSingleObjectIdAction(clickedId));
      } else if (selectedIsLocked === true) {
        sendEvent(this.getSelectionForSingleObjectIdAction(clickedId));
      } else if (clickedItemInSelectedObjects) {
        this.actionType = ActionType.moveAllSelectedObjects;
        const { x, y } = screenToWorld(event.clientX, event.clientY);
        this.mouseDragHelper.onDown(x, y);
        selectBBox.onDownMove();
      } else {
        const isGroup =
          clicked.objectType !== undefined &&
          clicked.objectType === ObjectTypes.Group;

        const isGroupChild =
          clicked[groupIdKey] !== undefined && clicked[groupIdKey].length > 0;

        const isClickedOnGroup = isGroup || isGroupChild;

        if (isClickedOnGroup) {
          this.actionType = ActionType.moveObjectGroup;
        } else {
          this.actionType = ActionType.moveSingleObject;
        }
        const { x, y } = screenToWorld(event.clientX, event.clientY);
        this.mouseDragHelper.onDown(x, y);
      }
    } else {
      if (selectBBox.isChild(intersects)) {
        this.actionType = ActionType.moveSelectBBox;
        const { x, y } = screenToWorld(event.clientX, event.clientY);
        this.mouseDragHelper.onDown(x, y);
        selectBBox.onDownMove();
      } else {
        this.actionType = ActionType.moveCamera;
        const { x, y } = screenToWorld(event.clientX, event.clientY);
        this.mouseDragHelper.onDown(x, y);
        this.mouseEventsIdle.onDown(event);
      }
    }

    logger.trace(
      "MouseEventsEditorModeSelectOrMove: onDown action type",
      this.actionType
    );
  }

  onMove(event: PointerEvent) {
    if (this.actionType === undefined) {
      return;
    }

    if (this.actionType === ActionType.moveCamera) {
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onMove(x, y);
      this.mouseEventsIdle.onMove(event);
      this.updateUIAtMove(false);
    }

    if (this.actionType === ActionType.moveAllSelectedObjects) {
      this.moveAllSelectedObjects(event.clientX, event.clientY);
    }

    if (this.actionType === ActionType.moveSingleObject) {
      this.moveSingleObject(event.clientX, event.clientY);
    }

    if (this.actionType === ActionType.moveObjectGroup) {
      this.moveObjectGroup(event.clientX, event.clientY);
    }

    if (this.actionType === ActionType.selectMultipleObjects) {
      this.selectMultipleObjects(event.clientX, event.clientY, true);
    }

    if (this.actionType === ActionType.deselectMultipleObjects) {
      this.selectMultipleObjects(event.clientX, event.clientY, false);
    }

    const selectBBox = getSelectBBox();

    if (this.actionType === ActionType.editSelectBBox) {
      this.updateUIAtMove(false);
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onMove(x, y);
      const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
      const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;
      selectBBox.onMoveEdit(diffX, diffY);
    }

    if (this.actionType === ActionType.moveSelectBBox) {
      this.moveAllSelectedObjects(event.clientX, event.clientY);
      this.updateUIAtMove(false);
      const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
      const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;
      selectBBox.onMoveMove(diffX, diffY);
    }
  }

  onUp(event: PointerEvent) {
    if (this.actionType === undefined) {
      return;
    }

    if (this.actionType === ActionType.moveCamera) {
      this.mouseEventsIdle.onUp(event);
      if (this.mouseDragHelper.isDragged === false) {
        const store = getStore();
        const action: ChangeSelectObjects = {
          name: AppEventType.ChangeSelectObjects,
          payload: {
            change: store.selectedIds.map((id) => {
              return {
                id,
                isSelected: false,
              };
            }),
          },
        };
        sendEvent(action);
      } else {
        this.updateUIAtMove(true);
        const store = getStore();
        const newStore = produce(store, (draft) => {
          const position = getTopBarPosition(
            store.selectedIds.map((id) => {
              return { id, objectType: store.objects[id].objectType };
            })
          );
          draft.ui.selectedTopBarPosition = position;
        });
        setStore(newStore);
        refreshReactState(newStore);
      }
      this.mouseDragHelper.onUp();
    }

    if (this.actionType === ActionType.moveObjectGroup) {
      const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
      const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

      if (!(Math.abs(diffY) === 0 && Math.abs(diffY) === 0)) {
        const objectId =
          this.intersects?.[0]?.object?.userData?.id ?? undefined;
        if (objectId !== undefined) {
          const ids = this.getIdsFromGroupOrInGroup(objectId);
          sendEvent(
            this.getMoveObjectsAction([objectId, ...ids], diffX, diffY)
          );
        }
      }

      if (this.mouseDragHelper.isDragged === false) {
        const objectId =
          this.intersects?.[0]?.object?.userData?.id ?? undefined;
        if (objectId !== undefined) {
          if (
            isPressedKey("controlleft") ||
            isPressedKey("controlright") ||
            isPressedKey("shiftleft") ||
            isPressedKey("shiftright")
          ) {
            const store = getStore();
            const clickedObjectIsLocked =
              store.objects[objectId] !== undefined
                ? store.objects[objectId].isLocked === true
                : false;
            let selectedIsLocked: any = false;
            store.selectedIds.forEach((id) => {
              if (store.objects[id].isLocked === true) {
                selectedIsLocked = true;
              }
            });
            if (selectedIsLocked === true) {
            } else if (clickedObjectIsLocked === false) {
              sendEvent(
                this.getSelectionAppendAction([objectId], undefined, false)
              );
            }
          } else {
            const store = getStore();
            if (store.selectedIds.length === 0) {
              sendEvent(this.getSelectionForSingleObjectIdAction(objectId));
            } else {
              const clickedObjectIsLocked =
                store.objects[objectId] !== undefined
                  ? store.objects[objectId].isLocked === true
                  : false;
              let selectedIsLocked: any = false;
              store.selectedIds.forEach((id) => {
                if (store.objects[id].isLocked === true) {
                  selectedIsLocked = true;
                }
              });
              if (selectedIsLocked === true) {
              } else if (clickedObjectIsLocked === false) {
                sendEvent(this.getSelectionForSingleObjectIdAction(objectId));
              }
            }
          }
        }
        logger.trace("MouseEventsEditorModeSelectOrMove: zaznaczam group");
      }
      this.updateUIAtMove(true);
      this.mouseDragHelper.onUp();
    }

    if (this.actionType === ActionType.moveSingleObject) {
      const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
      const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

      if (!(Math.abs(diffY) === 0 && Math.abs(diffY) === 0)) {
        const objectId =
          this.intersects?.[0]?.object?.userData?.id ?? undefined;
        if (objectId !== undefined) {
          sendEvent(this.getMoveObjectsAction([objectId], diffX, diffY));
        }
      }

      if (this.mouseDragHelper.isDragged === false) {
        const objectId =
          this.intersects?.[0]?.object?.userData?.id ?? undefined;
        if (objectId !== undefined) {
          if (
            isPressedKey("controlleft") ||
            isPressedKey("controlright") ||
            isPressedKey("shiftleft") ||
            isPressedKey("shiftright")
          ) {
            const store = getStore();
            const clickedObjectIsLocked =
              store.objects[objectId] !== undefined
                ? store.objects[objectId].isLocked === true
                : false;

            let selectedIsLocked: any = false;
            store.selectedIds.forEach((id) => {
              if (store.objects[id].isLocked === true) {
                selectedIsLocked = true;
              }
            });
            if (selectedIsLocked === true) {
              // nothing
            } else if (clickedObjectIsLocked === false) {
              sendEvent(
                this.getSelectionAppendAction([objectId], undefined, true)
              );
            }
          } else {
            const store = getStore();
            if (store.selectedIds.length === 0) {
              sendEvent(this.getSelectionForSingleObjectIdAction(objectId));
            } else {
              const clickedObjectIsLocked =
                store.objects[objectId] !== undefined
                  ? store.objects[objectId].isLocked === true
                  : false;
              let selectedIsLocked: any = false;
              store.selectedIds.forEach((id) => {
                if (store.objects[id].isLocked === true) {
                  selectedIsLocked = true;
                }
              });
              if (selectedIsLocked === true) {
              } else if (clickedObjectIsLocked === false) {
                sendEvent(this.getSelectionForSingleObjectIdAction(objectId));
              }
            }
          }
        }
        logger.trace(
          "MouseEventsEditorModeSelectOrMove: zaznaczam single object"
        );
      }
      this.updateUIAtMove(true);
      this.mouseDragHelper.onUp();
    }

    if (this.actionType === ActionType.moveAllSelectedObjects) {
      const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
      const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

      if (!(Math.abs(diffY) === 0 && Math.abs(diffY) === 0)) {
        const store = getStore();
        sendEvent(this.getMoveObjectsAction(store.selectedIds, diffX, diffY));
      }

      if (
        isPressedKey("controlleft") ||
        isPressedKey("controlright") ||
        isPressedKey("shiftleft") ||
        isPressedKey("shiftright")
      ) {
        const objectId =
          this.intersects?.[0]?.object?.userData?.id ?? undefined;
        sendEvent(this.getSelectionAppendAction([objectId], undefined, false));
      }

      this.mouseDragHelper.onUp();
      getSelectBBox().onUpMove();
      this.updateUIAtMove(true);
    }

    if (this.actionType === ActionType.selectMultipleObjects) {
      if (isPressedKey("shiftleft") || isPressedKey("shiftright")) {
        if (this.mouseDragHelper.isDragged === false) {
          this.intersects = this.getActiveObjectsFromIntersect(
            event.clientX,
            event.clientY
          );
          const firstClickedActiveObject = this.intersects.length > 0;
          if (firstClickedActiveObject) {
            const clicked = this.intersects[0].object.userData as AppObject;
            const clickedId = clicked.id;
            if (clickedId !== undefined) {
              const store = getStore();
              const clickedObjectIsLocked =
                store.objects[clickedId] !== undefined
                  ? store.objects[clickedId].isLocked === true
                  : false;
              let selectedIsLocked: any = false;
              store.selectedIds.forEach((id) => {
                if (store.objects[id].isLocked === true) {
                  selectedIsLocked = true;
                }
              });
              if (selectedIsLocked === true) {
              } else if (clickedObjectIsLocked === false) {
                sendEvent(
                  this.getSelectionAppendAction([clickedId], undefined, false)
                );
              }
            }
          }
          this.intersects = [];
        } else {
          if (this.boundingClientRect !== undefined) {
            const { top, left, width, height } = this.boundingClientRect;
            this.selectionBox.endPoint.set(
              ((event.clientX - left) / width) * 2 - 1,
              -((event.clientY - top) / height) * 2 + 1,
              1
            );
            const store = getStore();
            let selectedIsLocked: any = false;
            store.selectedIds.forEach((id) => {
              if (store.objects[id].isLocked === true) {
                selectedIsLocked = true;
              }
            });
            if (selectedIsLocked === true) {
            } else {
              const allSelectedIds = this.selectionBox.selectIds();

              sendEvent(
                this.getSelectionAppendAction(allSelectedIds, true, false)
              );
            }
          }
        }
      }
      this.mouseDragHelper.onUp();
      this.mouseSelectionHelper.onUp();
    }

    if (this.actionType === ActionType.deselectMultipleObjects) {
      if (isPressedKey("altleft") || isPressedKey("altright")) {
        if (this.mouseDragHelper.isDragged === false) {
          this.intersects = this.getActiveObjectsFromIntersect(
            event.clientX,
            event.clientY
          );
          const firstClickedActiveObject = this.intersects.length > 0;
          if (firstClickedActiveObject) {
            const clicked = this.intersects[0].object.userData as AppObject;
            const clickedId = clicked.id;
            if (clickedId !== undefined) {
              sendEvent(
                this.getSelectionAppendAction([clickedId], false, true)
              );
            }
          }

          this.intersects = [];
        } else {
          if (this.boundingClientRect !== undefined) {
            const { top, left, width, height } = this.boundingClientRect;
            this.selectionBox.endPoint.set(
              ((event.clientX - left) / width) * 2 - 1,
              -((event.clientY - top) / height) * 2 + 1,
              1
            );
            const allSelectedIds = this.selectionBox.selectIds();

            sendEvent(
              this.getSelectionAppendAction(allSelectedIds, false, false)
            );
          }
        }
      }

      this.mouseDragHelper.onUp();
      this.mouseSelectionHelper.onUp();
    }

    const selectBBox = getSelectBBox();

    if (this.actionType === ActionType.editSelectBBox) {
      this.updateUIAtMove(true);
      // @TODO
      selectBBox.onUpEdit();
    }

    if (this.actionType === ActionType.moveSelectBBox) {
      this.updateUIAtMove(true);
      const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
      const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;
      const store = getStore();
      sendEvent(this.getMoveObjectsAction(store.selectedIds, diffX, diffY));
      selectBBox.onUpMove();
      this.mouseDragHelper.onUp();
      if (Math.abs(diffX) < 0.5 && Math.abs(diffY) < 0.5) {
        const store = getStore();
        const action: ChangeSelectObjects = {
          name: AppEventType.ChangeSelectObjects,
          payload: {
            change: store.selectedIds.map((id) => {
              return {
                id,
                isSelected: false,
              };
            }),
          },
        };
        sendEvent(action);
      }
    }

    this.actionType = undefined;
  }

  getMoveObjectsAction(objectIds: string[], diffX: number, diffY: number) {
    const store = getStore();

    const ids: string[] = [];
    const change: ChangeObjectsProperty["payload"]["changes"] = [];
    objectIds.forEach((objectId) => {
      const childrenIds = this.getIdsFromGroupOrInGroup(objectId);
      if (store.objects[objectId] !== undefined) {
        if (ids.includes(objectId) === false) {
          ids.push(objectId);
          change.push({
            id: objectId,
            properties: {
              position: {
                x: store.objects[objectId].position.x + diffX,
                y: store.objects[objectId].position.y + diffY,
                z: store.objects[objectId].position.z,
              },
            },
          });
        }
      }
      childrenIds.forEach((id) => {
        if (store.objects[id] !== undefined) {
          if (ids.includes(objectId) === false) {
            ids.push(objectId);
            change.push({
              id: id,
              properties: {
                position: {
                  x: store.objects[id].position.x + diffX,
                  y: store.objects[id].position.y + diffY,
                  z: store.objects[objectId].position.z,
                },
              },
            });
          }
        }
      });
    });

    const action: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: change,
      },
    };

    return action;
  }

  getSelectionForSingleObjectIdAction(id: string) {
    const store = getStore();
    const change: ChangeSelectObjects["payload"]["change"] = [];

    store.selectedIds.forEach((_id) => {
      const childrenIds = this.getIdsFromGroupOrInGroup(_id);
      change.push({
        id: _id,
        isSelected: false,
      });
      childrenIds.forEach((__id) => {
        change.push({
          id: __id,
          isSelected: false,
        });
      });
    });

    change.push({
      id,
      isSelected: true,
    });
    const childrenIds = this.getIdsFromGroupOrInGroup(id);
    childrenIds.forEach((_id) => {
      change.push({
        id: _id,
        isSelected: true,
      });
    });

    const action: ChangeSelectObjects = {
      name: AppEventType.ChangeSelectObjects,
      payload: {
        change,
      },
    };

    return action;
  }

  getSelectionAppendAction(
    objectIds: string[],
    _newIsSelected?: boolean,
    _addWithIsLock?: boolean
  ) {
    const store = getStore();
    const change: ChangeSelectObjects["payload"]["change"] = [];
    const addWithIsLock = _addWithIsLock ?? false;

    objectIds.forEach((objectId) => {
      const isLocked =
        store.objects[objectId] !== undefined &&
        store.objects[objectId].isLocked === true;
      if (
        addWithIsLock === true ||
        (addWithIsLock === false && isLocked === false)
      ) {
        const isSelected = store.selectedIds.includes(objectId) === true;
        const newSelected =
          _newIsSelected === undefined ? !isSelected : _newIsSelected;
        const childrenIds = this.getIdsFromGroupOrInGroup(objectId);
        change.push({
          id: objectId,
          isSelected: newSelected,
        });
        childrenIds.forEach((id) => {
          change.push({
            id: id,
            isSelected: newSelected,
          });
        });
      }
    });

    const action: ChangeSelectObjects = {
      name: AppEventType.ChangeSelectObjects,
      payload: {
        change,
      },
    };

    return action;
  }

  selectMultipleObjects(
    clientX: number,
    clientY: number,
    shouldBeSelected: boolean
  ) {
    const { x, y } = screenToWorld(clientX, clientY);
    this.mouseDragHelper.onMove(x, y);
    if (this.boundingClientRect === undefined) {
      return;
    }
    const { top, left } = this.boundingClientRect;
    const screenX = clientX - left;
    const screenY = clientY - top;
    this.mouseSelectionHelper.onMove(screenX, screenY);
  }

  moveSingleObject(clientX: number, clientY: number) {
    const store = getStore();

    const { x, y } = screenToWorld(clientX, clientY);
    this.mouseDragHelper.onMove(x, y);

    const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
    const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

    const clicked = this.intersects[0].object.userData;
    const objectId = clicked.id;
    if (objectId === undefined) {
      logger.warn("objectId not found in clicked item", clicked);
      return;
    }
    // const obj = store.objects[objectId];
    // if (obj === undefined) {
    //   logger.warn("objectId not found in app store", objectId, clicked);
    //   return;
    // }

    // const name = getStateObjectFromObjectId(objectId, obj.objectType);
    // if (name === undefined) {
    //   return;
    // }
    // const object = appThree.scene.getObjectByUserDataProperty("name", name);

    const object = appThree.objects.get(objectId);
    if (object === undefined) {
      logger.warn("object not found in app state for id", objectId, clicked);
      return;
    }

    if (store.objects[objectId].objectType === ObjectTypes.Region) {
      const region = store.objects[objectId] as Region;
      if (region.snapBeadsToCell === true) {
        if (region.childrenGridIds.length > 0) {
          region.childrenGridIds.forEach((id) => {
            if (id !== undefined && store.objects[id] !== undefined) {
              const bead = store.objects[id];
              if (bead === undefined) {
                logger.warn("object not exists in app", objectId);
                return;
              }
              // const name = getStateObjectFromObjectId(bead.id, bead.objectType);
              // if (name === undefined) {
              //   return;
              // }
              // const child_object = appThree.scene.getObjectByUserDataProperty(
              //   "name",
              //   name
              // );
              const child_object = appThree.objects.get(bead.id);
              if (child_object === undefined) {
                logger.warn(
                  "object not found in app scene for id",
                  objectId
                  // name
                );
                return;
              }
              child_object.position.x = store.objects[id].position.x + diffX;
              child_object.position.y = store.objects[id].position.y + diffY;
            }
          });
        }
      }
    }

    object.position.x = store.objects[objectId].position.x + diffX;
    object.position.y = store.objects[objectId].position.y + diffY;

    this.updateUIAtMove(false);
  }

  moveObjectGroup(clientX: number, clientY: number) {
    const store = getStore();

    const { x, y } = screenToWorld(clientX, clientY);

    this.mouseDragHelper.onMove(x, y);

    const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
    const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

    const clicked = this.intersects[0].object.userData;
    let objectId = clicked.id;
    if (objectId === undefined) {
      logger.warn("objectId not found in clicked item", clicked);
      return;
    }
    let group = store.objects[objectId];
    if (group === undefined) {
      logger.warn("objectId not found in app store", objectId);
      return;
    }

    const isGroupChild =
      group[groupIdKey] !== undefined && group[groupIdKey].length > 0;

    if (isGroupChild) {
      objectId = group[groupIdKey];
      group = store.objects[objectId];
    }

    // const name = getStateObjectFromObjectId(objectId, group.objectType);
    // if (name === undefined) {
    //   return;
    // }
    // const object = appThree.scene.getObjectByUserDataProperty("name", name);
    const object = appThree.objects.get(objectId);
    if (object === undefined) {
      logger.warn("object not found in app state for id", objectId);
      return;
    }
    object.position.x = store.objects[objectId].position.x + diffX;
    object.position.y = store.objects[objectId].position.y + diffY;

    (group as Group).childrenIds.forEach((id) => {
      const children: AppObject | undefined = store.objects[id];
      if (children === undefined) {
        logger.warn("object not exists in app", id);
        return;
      }
      // const name = getStateObjectFromObjectId(children.id, children.objectType);
      // if (name === undefined) {
      //   return;
      // }
      // const child = appThree.scene.getObjectByUserDataProperty("name", name);
      const child = appThree.objects.get(children.id);
      if (child === undefined) {
        logger.warn("object not found in app scene for id", id);
        return;
      }
      child.position.x = store.objects[id].position.x + diffX;
      child.position.y = store.objects[id].position.y + diffY;
    });

    this.updateUIAtMove(false);
  }

  moveAllSelectedObjects(clientX: number, clientY: number) {
    const { x, y } = screenToWorld(clientX, clientY);
    this.mouseDragHelper.onMove(x, y);

    const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
    const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

    const store = getStore();

    store.selectedIds.forEach((objectId) => {
      const obj: AppObject | undefined = store.objects[objectId];
      if (obj === undefined) {
        logger.warn("object not exists in app", objectId);
        return;
      }
      const object = appThree.objects.get(objectId);
      if (object === undefined) {
        logger.warn("object not found in app scene for id", objectId);
        return;
      }

      if (store.objects[objectId].objectType === ObjectTypes.Region) {
        const region = store.objects[objectId] as Region;
        if (region.snapBeadsToCell === true) {
          if (region.childrenGridIds.length > 0) {
            region.childrenGridIds.forEach((id) => {
              if (id !== undefined && store.objects[id] !== undefined) {
                const bead = store.objects[id];
                if (bead === undefined) {
                  logger.warn("object not exists in app", objectId);
                  return;
                }
                const child_object = appThree.objects.get(objectId);
                if (child_object === undefined) {
                  logger.warn(
                    "object not found in app scene for id",
                    objectId
                  );
                  return;
                }
                child_object.position.x = store.objects[id].position.x + diffX;
                child_object.position.y = store.objects[id].position.y + diffY;
              }
            });
          }
        }
      }

      object.position.x = store.objects[objectId].position.x + diffX;
      object.position.y = store.objects[objectId].position.y + diffY;
    });

    const selectBBox = getSelectBBox();
    selectBBox.onMoveMove(diffX, diffY);

    this.updateUIAtMove(false);
  }

  updateUIAtMove(visible: boolean) {
    if (visible === false) {
      this.hideTopBar();
    } else {
      this.showTopBar();
    }
  }

  hideTopBar() {
    const selector = ".selected-objects-top-bar";
    const topBar = document.querySelector(selector) as HTMLDivElement | null;
    if (topBar === null) {
      return;
    }
    topBar.style.display = "none";
  }

  showTopBar() {
    const selector = ".selected-objects-top-bar";
    const topBar = document.querySelector(selector) as HTMLDivElement | null;
    if (topBar === null) {
      return;
    }
    topBar.style.display = "block";
  }
}
