import React from "react";
import { Editor } from "../editorObjects/Editor";
import { useSelector } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faReply,
  faStar as faStarSolid,
  faUpload,
} from "@fortawesome/free-solid-svg-icons";
import { faStar as faStarRegular } from "@fortawesome/free-regular-svg-icons";
import { redoHistory, undoHistory } from "../controllers/AppHistory";
import { getStore, setStore } from "../controllers/store";
import produce from "immer";
import { refreshReactState } from "../controllers/refreshReactState";
import { appThree } from "../threejs/renderThreejs";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

export const ProjectInfoWrapper: React.FC<{}> = (props) => {
  const { canRedo, canUndo } = useSelector((state: Editor) => {
    const { canRedo, canUndo } = state.history;
    return {
      canRedo,
      canUndo,
    };
  });
  const board = useSelector((state: Editor) => {
    return state.board;
  });
  const onClickRedo = () => {
    if (canRedo) {
      redoHistory();
    }
  };
  const onClickUndo = () => {
    if (canUndo) {
      undoHistory();
    }
  };
  const onClickName = () => {
    if (!board) {
      return;
    }
    if (!board.canToggleToFavourite) {
      return;
    }
    const store = getStore();
    const newStore = produce(store, (draft) => {
      if (draft.board) {
        draft.board.editModalVisible = true;
      }
    });
    setStore(newStore);
    refreshReactState(newStore);
  };
  const onClickLogo = () => {
    // window.alert("back to home");
  };
  const onClickFavourite = () => {
    if (!board) {
      return;
    }
    if (!board.canToggleToFavourite) {
      return;
    }
    const store = getStore();
    const newStore = produce(store, (draft) => {
      if (draft.board) {
        draft.board.isFavourite = !draft.board.isFavourite;
      }
    });
    setStore(newStore);
    refreshReactState(newStore);
  };
  const onClickExportAsImage = () => {
    try {
      const content = appThree.renderer.domElement
        .toDataURL("image/png")
        .replace("image/png", "image/octet-stream");
      const link = document.createElement("a");
      document.body.appendChild(link);
      link.download = "coralinio-preview.png";
      link.href = content;
      link.click();
      document.body.removeChild(link);
    } catch (e) {
      console.log(e);
      return;
    }
  };
  const [t] = useTranslation(["board"]);

  return (
    <ProjectInfoWrapperStyles>
      <div className={"project-info-wrapper"}>
        <div className={"block-with-info"}>
          <div
            className="coralinio-logo"
            onClick={onClickLogo}
            data-tip={t("board:ui.back_to_home")}
            data-place={"bottom"}
            data-type={"dark"}
            data-effect={"solid"}
          >
            <div className={"value"}>
              <Link to="/">coralinio</Link>
            </div>
          </div>

          {board?.name ? (
            <div
              className={"project-name"}
              onClick={onClickName}
              data-tip={board?.name ?? ""}
              data-place={"bottom"}
              data-type={"dark"}
              data-effect={"solid"}
            >
              <div className={"value"}>{board?.name ?? ""}</div>
            </div>
          ) : null}

          {board?.canToggleToFavourite ? (
            <div
              className={"add-to-fav"}
              onClick={onClickFavourite}
              data-tip={
                board.isFavourite === true
                  ? t("board:ui.remove_from_fav")
                  : t("board:ui.add_to_fav")
              }
              data-place={"bottom"}
              data-type={"dark"}
              data-effect={"solid"}
            >
              <FontAwesomeIcon
                icon={board.isFavourite ? faStarSolid : faStarRegular}
              />
            </div>
          ) : null}

          {board?.canExportAsImage ? (
            <div
              className={"export-as-image"}
              onClick={onClickExportAsImage}
              data-tip={t("board:ui.export_as_image")}
              data-place={"bottom"}
              data-type={"dark"}
              data-effect={"solid"}
            >
              <FontAwesomeIcon icon={faUpload} />
            </div>
          ) : null}
        </div>

        {canRedo || canUndo ? (
          <div className={"block-with-history"}>
            <div
              className={`undo-button ${canUndo ? "active" : "inactive"}`}
              onClick={onClickUndo}
              data-tip={t("board:ui.undo")}
              data-place={"bottom"}
              data-type={"dark"}
              data-effect={"solid"}
            >
              <span className="icon">
                <FontAwesomeIcon icon={faReply} />
              </span>
            </div>

            <div
              className={`redo-button ${canRedo ? "active" : "inactive"} `}
              onClick={onClickRedo}
              data-tip={t("board:ui.redo")}
              data-place={"bottom"}
              data-type={"dark"}
              data-effect={"solid"}
            >
              <span className="icon">
                <FontAwesomeIcon
                  icon={faReply}
                  style={{ transform: "scaleX(-1)" }}
                />
              </span>
            </div>
          </div>
        ) : null}
      </div>
    </ProjectInfoWrapperStyles>
  );
};

