import { cloneDeep } from "lodash";
import * as THREE from "three";
import {
  AppObject,
  EditorGridType,
  EditorMode,
  ThreeGridHelperConfig,
  ThreeLightConfig,
} from "../editorObjects/Editor";
import { logger } from "../logger";

export interface StoreUiSettings {
  zoomScale: number;
  mode: EditorMode;
  lights: ThreeLightConfig[];
  grid: ThreeGridHelperConfig;
  selectedTopBarPosition: THREE.Vector3 | undefined;
  modalSearchProducts: {
    isOpen: boolean;
    successAction: "updateBeadType" | undefined;
    advBlockVisible: boolean;
    q: string;
    producer: string;
    color: string;
    cachedResponse: any[];
    scrollPosition: number;
  };
  leftSidebar: {
    isOpen: boolean;
    panel: "comments" | undefined;
  };
  rightSidebar: {
    isOpen: boolean;
    panel: "history" | "objects" | undefined;
  };
  blockThreejs: {
    loading: boolean;
    error: string | undefined;
  };
}
export interface SimpleHistoryEvent {
  name: string;
  isActive: boolean;
  createdAt: string;
}
export interface StoreHistory {
  events: SimpleHistoryEvent[];
  canRedo: boolean;
  canUndo: boolean;
}

export interface StoreBoard {
  uuid: string;
  isFavourite: boolean;
  canToggleToFavourite: boolean;
  canEdit: boolean;
  canExportAsImage: boolean;
  name: string;
  editModalVisible: boolean;
}
export interface Store {
  objects: { [s: string]: AppObject };
  selectedIds: string[];
  ui: StoreUiSettings;
  history: StoreHistory;
  board: StoreBoard;
}

let store: Store;

